<script>
import axios from "axios";
import $ from "jquery";
import Vue from "vue";
import firebase from "firebase";
import Loader from "../../Loader";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";

export default {
  name: "cocategory",
  components: {
    Loader
  },
  data() {
    return {
      coid: null,
      item_id: null,
      name: null,
      active: true,

      deliveryGuys: [],
      errors: [],
      edit: false,
      message: null,
      disable: false,
      disableMessage: null,
      success: false
    };
  },

  created() {
    this.fetchDeliveryGuys();
  },

  methods: {
    add() {
      if (this.validate()) {
        this.disable = true;
        let url = domain + "cocat";

        if (this.edit == false) {
          axios
            .post(
              url,
              {
                coid: this.coid,
                item_id: this.item_id,
                name: this.name,
                priceBandValue: this.priceBandValue,
                active: this.active
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              this.coid = null;
              this.item_id = null;
              this.name = null;
              this.priceBandValue = 10;

              this.cocategories.unshift(res.data);

              //// to show
              this.message = "Data Upload Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              //// to show
              this.message = "Data Upload Failed";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
              console.log(err);
            });
        } else {
          url = domain + "cocat/" + this.item_id;
          axios
            .post(
              url,
              {
                _method: "PUT",
                coid: this.coid,
                name: this.name,
                priceBandValue: this.priceBandValue,
                active: this.active
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              this.cocategories.forEach((doc, index) => {
                if (doc.item_id == res.data.item_id) {
                  Vue.set(this.cocategories, index, res.data);
                }
              });

              this.coid = null;
              this.item_id = null;
              this.name = null;
              this.priceBandValue = 10;
              this.active = true;
              //// to show
              this.message = "Data Upload Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              });

              this.edit = false;
            })
            .catch(err => {
              /// to show
              this.message = "Data Upload In MYSql Failed";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        }
      }
    },

    deleteCo(index) {
      if (confirm("You are about to delete " + this.cocategories[index].name)) {
        let url = domain + "cocat/" + this.cocategories[index].item_id;
        axios
          .post(
            url,
            {
              _method: "delete"
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data) {
              db.collection("co category")
                .where("item_id", "==", this.cocategories[index].item_id)
                .get()
                .then(res => {
                  res.forEach(doc => {
                    doc.ref.delete();
                  });
                });
            }
            this.cocategories.splice(index, 1);
          });
      }
    },

    editCoCategory(index) {
      if (this.cocategories) {
        //it's editing not adding
        this.edit = true;
        this.disableId = true;
        //details
        this.item_id = this.cocategories[index].item_id;
        this.name = this.cocategories[index].name;
        this.coid = this.cocategories[index].coid;
        this.priceBandValue = this.cocategories[index].priceBandValue;
        this.active = this.cocategories[index].active;
      }
    },

    fetchCoCategories() {
      let url = domain + "cat";
      axios
        .get(url, {
          params: {
            cocategories: "budbak"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            doc.subcategories.forEach(subcat => {
              subcat.cocategories.forEach(cocat => {
                let active = 1;
                if (cocat.active == "0") {
                  active = 0;
                }
                let data = {
                  coid: cocat.coid,
                  item_id: cocat.item_id,
                  name: cocat.name,
                  image: cocat.image,
                  active: active
                };
                this.cocategories.push(data);
              });
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    fetchSubCategories() {
      let url = domain + "subcat";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            this.subcategories.push(doc);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    goBack() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push("/inventory");
    },

    validate() {
      this.errors = [];
      if (this.item_id && this.name && this.coid) {
        return true;
      } else {
        if (!this.item_id) {
          Vue.set(this.errors, 4, "Fill valid Item Id");
        }
        if (!this.name) {
          Vue.set(this.errors, 3, "Fill Name");
        }
        if (!this.coid) {
          Vue.set(this.errors, 2, "Choose Subcategory");
        }

        Vue.set(this.errors, 1, "Something is missing");
        return false;
      }
    }
  }
};
</script>

<style>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
