<script>
import axios from "axios";
import { domain } from "@/store/variable";
import store from "@/store/store";
export default {
  name: "MainDashboard",

  data() {
    return {
      uploader: [],
      totalPay: 0,
      AmountPaid: 0,

      totalOrders: 0
    };
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  mounted() {
    setTimeout(() => {
      this.countTotalOrders();
    }, 3000);
  },

  methods: {
    countTotalOrders() {
      if (this.user && this.user.designation == "admin") {
        if (this.user.designation == "admin") {
          let url = domain + "order";
          axios
            .get(url, {
              params: {
                count: "aajKuchTufaniKrteHai"
              }
            })
            .then(res => {
              if (res.data) {
                this.totalOrders = res.data;
              }
            });
        } else {
          //ghanta
        }
      }
    }
  }
};
</script>

