<template>
  <div class="row">
    <div class="col-12">
      <!-- <DeliveryView/> -->
      <div class="card content border-1 shadow-sm">
        <div class="card-body">
          <div class="row pt-2">
            <div class="col-6">
              <form class="form-inline">
                <div class="input-group content-search border">
                  <div class="input-group-prepend">
                    <span class="input-group-text" id="basic-addon1">
                      <i class="fa fa-search"></i>
                    </span>
                  </div>
                  <input
                    type="text"
                    v-model="search"
                    class="form-control hz-input"
                    placeholder="Search Here..."
                    aria-label="Search Here..."
                    aria-describedby="basic-addon1"
                    @keyup="searching"
                  />
                </div>
              </form>
            </div>
            <div class="col-6">
              <form
                class="form-inline float-right"
                v-if="status && (status==4 || status==3 || status==6)"
              >
                <span class="font-weight-semibold pr-3">Select Delivery Status</span>
                <select
                  v-model="action"
                  name="status"
                  class="custom-select"
                  @change="updateAction()"
                >
                  <option disabled="disabled" selected="selected">Choose Action</option>

                  <!-- <option value="1">Cancel</option> -->
                  <option value="4" v-if="status==3">Out For Delivery</option>
                  <option value="5" v-if="status==4">Delivered</option>
                  <option value="7" v-if="status==6">Return Picked</option>
                  <option value="8">Failed</option>
                  <!-- <option value="delete">Delete</option> -->
                </select>
              </form>
            </div>
          </div>
          <div class="table-responsive pt-4">
            <table class="table table-hover table-sm table-borderless table-content text-left">
              <thead>
                <tr>
                  <th v-if="status && (status==4 || status==3 || status==6)">
                    <form>
                      <div class="custom-control custom-checkbox">
                        <input
                          type="checkbox"
                          class="custom-control-input"
                          id="customCheck"
                          @click="selectAll()"
                          v-model="select"
                        />
                        <label class="custom-control-label" for="customCheck"></label>
                      </div>
                    </form>
                  </th>
                  <th>Delivery Id</th>
                  <th>Order Id</th>
                  <th>Order By</th>
                  <th>Products</th>
                  <th>Amount</th>
                  <th>Payment Status</th>
                  <th>Delivery Status</th>
                  <th>Delivery By</th>
                  <th>Inventory Address</th>
                  <th>Delivery Address</th>
                  <th>At</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody v-if="deliveries.length>0">
                <tr v-for="delivery in deliveries" :key="delivery.id">
                  <td v-if="status && (status==4 || status==3 || status==6)">
                    <form>
                      <div class="custom-control custom-checkbox">
                        <input
                          type="checkbox"
                          class="custom-control-input"
                          :id="delivery.deliveryId"
                          v-model="delivery.checked"
                          :value="delivery.deliveryId"
                        />
                        <label class="custom-control-label" :for="delivery.deliveryId"></label>
                      </div>
                    </form>
                  </td>
                  <td>{{delivery.deliveryId}}</td>
                  <td>#{{delivery.order.id}}</td>
                  <td>
                    <span>{{delivery.order.user_address.name}}</span>
                    <span>{{delivery.order.user_address.mobileNumber}}</span>
                  </td>
                  <td>
                    <div v-if="delivery.order.products.length>2">
                      <span
                        v-for="product in delivery.order.products.slice(0,2)"
                        :key="product.productId"
                      >{{product.name}} +</span>
                      <span>
                        <b>{{delivery.order.products.length-2}} more</b>
                      </span>
                    </div>
                    <div v-else>
                      <span
                        v-for="product in delivery.order.products"
                        :key="product.productId"
                      >{{product.name}}</span>
                    </div>
                  </td>
                  <td>{{delivery.order.totalPayableAmount}}</td>
                  <td>
                    <span
                      class="bagde badge-waiting pl-1 pr-1"
                      v-if="delivery.order.paidAmount==0"
                    >Not Paid</span>
                    <span
                      class="bagde badge-paid pl-1 pr-1"
                      v-else-if="delivery.order.paidAmount==delivery.order.totalPayableAmount"
                    >Paid</span>
                    <span
                      class="bagde badge-failed pl-1 pr-1"
                      v-else
                    >Pending : {{delivery.order.totalPayableAmount-delivery.order.paidAmount}}</span>
                  </td>
                  <td>
                    <span
                      class="badge badge-cancelled pl-2 pr-2"
                      v-if="delivery.status==1"
                    >Cancelled</span>
                    <span
                      class="badge badge-processing pl-2 pr-2"
                      v-if="delivery.status==2"
                    >Not yet Ready</span>
                    <span
                      class="badge badge-packed pl-2 pr-2"
                      v-if="delivery.status==3"
                    >Ready To Pickup</span>
                    <span class="badge badge-shipping pl-2 pr-2" v-if="delivery.status==4">Shipping</span>
                    <span
                      class="badge badge-delivered pl-2 pr-2"
                      v-if="delivery.status==5"
                    >Delivered</span>
                    <span
                      class="badge badge-request-return pl-2 pr-2"
                      v-if="delivery.status==6"
                    >Return PickUp</span>
                    <span class="badge badge-return pl-2 pr-2" v-if="delivery.status==7">Returned</span>
                    <span class="badge badge-failed pl-2 pr-2" v-if="delivery.status==8">Failed</span>
                  </td>
                  <td class="text-dark">{{delivery.deliveryBy}}</td>
                  <td class="text-dark">{{delivery.order.inventory_address.name}}</td>
                  <td class="text-dark">
                    {{delivery.order.user_address.addressName}},
                    {{delivery.order.user_address.houseOfficeNo}},
                    {{delivery.order.user_address.street}},
                    {{delivery.order.user_address.city}},
                    {{delivery.order.user_address.landmark}},
                    {{delivery.order.user_address.pincode}}
                  </td>
                  <td>
                    {{delivery.date}}
                    <small>{{delivery.date}}</small>
                  </td>
                  <td class="text-dark">
                    <div>
                      <router-link
                        :to="{name: 'ViewDelivery', params: {deliveryId: delivery.deliveryId}}"
                      >
                        <i class="fa fa-eye"></i>
                      </router-link>
                    </div>
                  </td>
                </tr>
              </tbody>
              <tbody v-else>
                <tr>
                  <td colspan="9" class="text-center">
                    <div>
                      <img
                        class="card-img-top img-fluid"
                        src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                        alt="Card image cap"
                        style="width:20em"
                      />
                      <div class="card-body">
                        <p class="card-text">Oops! There ain't any delivery Made</p>
                      </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <DeliveryFooter />
    <Loader :disable="loading" :success="success" :message="message" />
  </div>
</template>
<script>
import axios from "axios";
import firebase from "firebase";
import Vue from "vue";
import Loader from "../../Loader";
import DeliveryFooter from "./DeliveryFooter.vue";
import { bus } from "../../../main";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";

export default {
  name: "DashBoard",
  components: {
    DeliveryFooter,
    Loader
  },
  props: {
    status: Number,
    newDeliveries: Array,
    disable: Boolean
  },

  data() {
    return {
      deliveryStatus: [],
      search: null,
      searchArray: [],

      select: false,
      action: null,

      message: null,
      success: null,
      loading: this.disable
    };
  },

  computed: {
    deliveries() {
      this.searchArray = this.newDeliveries;
      return this.newDeliveries;
    },

    user() {
      return store.state.user;
    }
  },

  created() {
    //this.getOrderStatus();
    bus.$on("paginateDelivery", data => {
      this.paginateDelivery(data["page"]);
    });
  },

  methods: {
    updateAction() {
      if (
        this.user.designation == "admin" ||
        this.user.designation == "deliverer"
      ) {
        this.deliveries.forEach((delivery, index) => {
          if (delivery.checked) {
            if (this.action == "delete") {
              if (confirm("You are about to delete #" + delivery.deliveryId)) {
                this.loading = true;
                let url = domain + "delivery/" + delivery.deliveryId;
                axios
                  .post(
                    url,
                    {
                      _method: "DELETE"
                    },
                    {
                      withCredentials: true,
                      headers: {
                        "Content-Type": ContentType,
                        "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                        "X-Requested-With": XRequestedWith,
                        "X-api-token": firebase.auth().currentUser.uid
                      }
                    }
                  )
                  .then(res => {
                    if (res.data) {
                      this.deliveries.splice(index, 1);
                    }
                  })
                  .catch(err => {
                    console.log(err.message);
                  });
              }
            } else {
              this.loading = true;
              let url = domain + "delivery/" + delivery.deliveryId;
              axios
                .post(
                  url,
                  {
                    _method: "PUT",
                    status: this.action
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": ContentType,
                      "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                      "X-Requested-With": XRequestedWith,
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  //colsing inputs
                  this.message = "Delivery Updated";
                  this.success = true;
                  this.loading = false;

                  // Vue.set(this.deliveries[index], status, snap.status);

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                })
                .catch(err => {
                  this.message = "Some error occured";
                  this.success = true;
                  this.loading = false;
                  console.log(err.message);

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
            }
          }
        });
      } else {
        confirm("You are not allowed to make changes");
      }
    },

    paginateDelivery(page) {
      this.$emit("fetchDeliveries", page);
    },

    searching() {
      if (this.search) {
        this.deliveries = this.searchArray.filter(
          v => v.orderId.indexOf(this.search) != -1
        );
      } else {
        this.deliveries = this.searchArray;
      }
    },

    selectAll() {
      this.deliveries.forEach(delivery => {
        if (!this.select) {
          delivery.checked = true;
          //Vue.set(order, checked, true);
        } else {
          delivery.checked = false;
        }
      });
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped>
</style>



