<template>
  <div class="container">
    <div class="table-filter">
      <div class="row">
        <div class="col-sm-3"></div>
        <!-- <div class="col-sm-3">
              <div class="show-entries">
                <span>Show</span>
                <select class="form-control">
                  <option>5</option>
                  <option>10</option>
                  <option>15</option>
                  <option>20</option>
                </select>
                <span>entries</span>
              </div>
        </div>-->
        <div class="col-sm-9">
          <div class="float-right">
            <div class="btn-group">
              <a class="filter-icon d-inline-block pr-2 text-dark" data-toggle="dropdown" href="#">
                <i class="fa fa-filter"></i>
              </a>
              <ul class="dropdown-menu">
                <li class="dropdown-item" @click="trackBy='Email'">Email</li>
                <li class="dropdown-item" @click="trackBy='orderId'">Order Id</li>
                <li class="dropdown-item" @click="trackBy='mobileNumber'">Mobile Number</li>
              </ul>
            </div>
            <div class="filter-group d-inline-block">
              <label v-if="trackBy=='user'">Email</label>
              <label v-if="trackBy=='mobileNumber'">Mobile Number</label>
              <label v-if="trackBy=='orderId'">Order Id</label>
              <input type="text" v-model="trackData" class="form-control" />
            </div>
            <a class="btn btn-primary text-dark" @click="getData()" v-if="trackBy!='orderId'">Track</a>
            <router-link :to="{name: 'ViewOrder', params: {orderId: trackData}}" class="btn btn-primary text-dark" tag="a" target="_blank" v-else>
              Track
            </router-link>
          </div>
        </div>
      </div>
    </div>

    <!--  user orders -->
    <div class="row" v-if="trackedUser != null || userOrders.length>0">
      <div class="col-12">
        <div class="card content border-1 shadow-sm mt-4">
          <div class="card-body">
            <div class="table-responsive pt-2">
              <table class="table table-hover table-sm table-borderless table-content text-left">
                <thead>
                  <tr>
                    <th>S.No.</th>
                    <th>OrderId</th>
                    <th>Description</th>
                    <th>Amount</th>
                    <th>Payment Method</th>
                    <th>Status</th>
                    <th>Placed On</th>
                    <th>View</th>
                  </tr>
                </thead>
                <tbody v-if="userOrders.length>0">
                  <tr v-for="(order,index) in userOrders" :key="index+1">
                    <td>{{index+1}}</td>
                    <td>{{order.orderId}}</td>
                    <td>
                      <div v-if="order.products.length>2">
                        <span
                          v-for="product in order.products.slice(0,2)"
                          :key="product.productId"
                        >{{product.name}} +</span>
                        <span>
                          <b>{{order.products.length-2}} more</b>
                        </span>
                      </div>
                      <div v-else>
                        <span
                          v-for="product in order.products"
                          :key="product.productId"
                        >{{product.name}}</span>
                      </div>
                    </td>
                    <td>{{order.totalPayableAmount}}</td>
                    <td>{{order.paymentMode}}</td>
                    <td>{{order.status}}</td>
                    <td>{{order.orderDate}} at {{order.orderTime}}</td>
                    <td>
                      <router-link :to="{name: 'ViewOrder', params: {orderId: order.orderId}}" class="btn btn-sm btn-go-green font-weight-bold d-inline" tag="a" target="_blank">
                        View
                      </router-link>
                      <!-- <button
                        
                        type="submit"
                        @click="getOrder(index)"
                      >View</button> -->
                    </td>
                  </tr>
                </tbody>

                <tbody v-else>
                  <tr>
                    <td colspan="8" class="text-center">
                      <div>
                        <img
                          class="card-img-top img-fluid"
                          src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                          alt="Card image cap"
                          style="width:20em"
                        />
                        <div class="card-body">
                          <p class="card-text">Oops! There ain't any Orders Made</p>
                        </div>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>

    <Loader :disable="loading" :success="success" :message="message" />
  </div>
</template>

<script>
import axios from "axios";
import Vue from "vue";
import { domain } from "@/store/variable";
import store from "@/store/store";
import Loader from "../../Loader";
import OrderStatus from "./orderStatus.vue";

export default {
  name: "TrackOrder",
  components: {
    Loader,
    OrderStatus
  },
  data() {
    return {
      trackBy: "user",
      trackData: null,
      order: null,
      trackedUser: null,
      userOrders: [],

      loading: false,
      success: false,
      message: "Searching"
    };
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  methods: {
    getData() {
      if (this.user && this.user.designation == "admin") {
        let url = domain + "order";
        let userURL = domain + "user";
        this.order = null;


        if (this.trackBy == "email") {
          this.loading = true;
          axios
            .get(userURL, {
              params: {
                email: this.trackData.trim()
              }
            })
            .then(res => {
              res.data.forEach(doc => {
                this.trackedUser = doc;
              });

              if (this.trackedUser) {
                axios
                  .get(url, {
                    params: {
                      userId: this.trackedUser["uuid"]
                    },
                    headers: {
                      from: "android"
                    }
                  })
                  .then(res => {
                    this.userOrders = res.data;
                    this.loading = false;
                  });
              } else {
                this.success = true;
                this.loading = false;
                this.message = "User not found";

                setTimeout(() => {
                  this.success = false;
                  this.message = "Searching";
                }, 2000);
              }
            });
        } else if (this.trackBy == "mobileNumber") {
          this.loading = true;
          axios
            .get(userURL, {
              params: {
                mobileNumber: this.trackData.trim()
              }
            })
            .then(res => {
              res.data.forEach(doc => {
                this.trackedUser = doc;
              });

              if (this.trackedUser) {
                axios
                  .get(url, {
                    params: {
                      userId: this.trackedUser["uuid"]
                    },
                    headers: {
                      from: "android"
                    }
                  })
                  .then(res => {
                    this.userOrders = res.data;
                    this.loading = false;
                  });
              } else {
                this.success = true;
                this.loading = false;
                this.message = "User not found";

                setTimeout(() => {
                  this.success = false;
                  this.message = "Searching";
                }, 2000);
              }
            });
        }
      }
    },

    getOrder(index) {
      if (this.userOrders) {
        this.order = this.userOrders[index];
      }
    }
  }
};
</script>

<style scoped>
.order-by-header h6 {
  color: rgba(47, 48, 61, 1);
  font-weight: 700;
  margin-bottom: 0.2em;
}

.order-by-header .heading {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
}

.order-by-header .location {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
  font-size: smaller;
}

.order-content {
  font-size: small;
}

.order-content img {
  width: 4em;
}

.response {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 700;
}

.table-filter input,
.table-filter select {
  height: 34px;
  border-radius: 3px;
  border-color: #ddd;
  box-shadow: none;
}
.table-filter {
  padding: 5px 0 15px;
  border-bottom: 1px solid #e9e9e9;
}
.table-filter .btn {
  height: 34px;
}
.filter-group select.form-control {
  width: 110px;
}
.filter-icon i {
  font-size: 18px;
  opacity: 0.7;
}
.filter-icon {
  display: inline-block;
}
</style>



