<template>
  <div>
    <Dashboard :products="items" :disable="loading" v-if="items"></Dashboard>

    <Footer @fetchItems="fetchItems" />
  </div>
</template>

<script>
import axios from "axios";
import db from "../firebaseInit";
import Dashboard from "./DashBoard.vue";
import Footer from "./Footer.vue";
import { bus } from "../../main";
import { domain } from "@/store/variable";
import store from "@/store/store";
import firebase from "firebase";

export default {
  name: "current_inventory",
  data() {
    return {
      items: [],
      loading: false,
      orderItems: "uploadedAt",
      orderWay: "desc"
    };
  },

  components: {
    Footer,
    Dashboard
  },

  computed: {
    user() {
      return store.state.user;
    },

    size() {
      return store.state.inventoryPageSize;
    }
  },

  created() {
    bus.$on("filterItems", data => {
      this.order(data["On"], data["Way"], data["page"]);
    });

    bus.$on("fetchItems", data => {
      this.fetchItems(data["page"]);
    });
  },

  mounted() {
    setTimeout(() => {
      if (this.user) {
        this.fetchItems();
      }
    }, 4000);
  },

  methods: {
    fetchItems(page) {
      this.loading = true;
      this.items = [];

      // loading content
      let url = domain + "item";
      if (!page) {
        page = 1;
      }

      if (this.user.designation == "admin") {
        axios
          .get(url, {
            params: {
              user: "aajaBhai",
              category: "fetch",
              page: page,
              limit: this.size,
              order: this.orderItems,
              orderWay: this.orderWay
            }
          })
          .then(res => {
            res.data.data.forEach(doc => {
              {
                var item = new Array();
                let active = 0;
                // image name
                let imageName = null;
                if (doc.image) {
                  let start = doc.image.indexOf("items%2F") + 8;
                  let end = doc.image.indexOf("?");
                  imageName = doc.image.substring(start, end);
                  imageName = this.truncate(imageName);
                }
                if (doc.active == 1) {
                  active = 1;
                } else {
                  active = 0;
                }
                item = {
                  productId: doc.productId,
                  itemId: doc.itemId,
                  name: doc.name,
                  cat: doc.allcat,
                  stock: doc.stock,
                  unit: doc.unit,
                  mrp: doc.MRP,
                  currentPrice: doc.currentPrice,
                  discount: doc.discount,
                  image: doc.image,
                  imageName: imageName,
                  showImage: 0,
                  qty: doc.qty,
                  active: active,
                  uploadedBy: doc.uploadedBy,
                  uploadedAt: doc.uploadedAt,
                  uploadedByName: doc.user.name,
                  cocatName: doc.cocategory.name,
                  cocatId: doc.cocategory.coid,
                  subcatName: doc.cocategory.subcategory.name,
                  subcatId: doc.cocategory.subcategory.subcatid,
                  catName: doc.cocategory.subcategory.category.name
                };

                this.items.push(item);
              }
            });

            store.dispatch("patchinventoryPageCount", res.data.last_page);
            store.dispatch("patchinventoryPageNumber", res.data.current_page);
            store.dispatch("patchinventoryTotalItems", res.data.total);
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        axios
          .get(url, {
            params: {
              page: page,
              limit: this.size,
              order: this.orderItems,
              orderWay: this.orderWay,
              uploadedBy: firebase.auth().currentUser.uid
            }
          })
          .then(res => {
            res.data.data.forEach(doc => {
              if (this.user.uuid == doc.uploadedBy) {
                var item = new Array();

                // image name
                let imageName = null;
                if (doc.image) {
                  let start = doc.image.indexOf("items%2F") + 8;
                  let end = doc.image.indexOf("?");
                  imageName = doc.image.substring(start, end);
                  imageName = this.truncate(imageName);
                }
                item = {
                  productId: doc.productId,
                  itemId: doc.itemId,
                  name: doc.name,
                  cat: doc.allcat,
                  stock: doc.stock,
                  unit: doc.unit,
                  mrp: doc.MRP,
                  currentPrice: doc.currentPrice,
                  discount: doc.discount,
                  image: doc.image,
                  imageName: imageName,
                  showImage: 0,
                  qty: doc.qty,
                  uploadedBy: doc.uploadedBy,
                  uploadedAt: doc.uploadedAt,

                  cocatName: doc.cocategory.name,
                  cocatId: doc.cocategory.coid,
                  subcatName: doc.cocategory.subcategory.name,
                  subcatId: doc.cocategory.subcategory.subcatid,
                  catName: doc.cocategory.subcategory.category.name
                };

                this.items.push(item);
              }
            });

            store.dispatch("patchinventoryPageCount", res.data.last_page);
            store.dispatch("patchinventoryPageNumber", res.data.current_page);
            store.dispatch("patchinventoryTotalItems", res.data.total);
          })
          .catch(err => {
            console.log(err);
          });
      }

      this.loading = false;
    },

    order(On, Way, page) {
      this.loading = true;
      //
      this.orderItems = On;
      this.orderWay = Way;

      if (On == "imageName") {
        this.orderItems = "image";
      }
      this.fetchItems(page);

      this.loading = false;
    },

    truncate(value) {
      const length = 10;
      return value.length <= length
        ? value
        : value.substring(0, length) + "...";
    }
  },

  sockets: {
    message(data) {
      console.log(data);
    }
  }
};
</script>




