<template>
  <div class="container-fluid">
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <h4>COUPONS</h4>
      </div>
      <div class="col-6 content-header small">
        <span class="float-right pr-2">doorstep > coupons</span>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <!-- <DeliveryView/> -->
        <div class="card content border-1 shadow-sm">
          <div class="card-body">
            <div class="row pt-2">
              <div class="col-6">
                <form class="form-inline">
                  <div class="input-group content-search border">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">
                        <i class="fa fa-search"></i>
                      </span>
                    </div>
                    <input
                      type="text"
                      v-model="search"
                      class="form-control hz-input"
                      placeholder="Search Here..."
                      aria-label="Search Here..."
                      aria-describedby="basic-addon1"
                      @keyup="searching"
                    />
                  </div>
                </form>
              </div>
              <div class="col-6"></div>
            </div>
            <div class="p-4">
              <table
                class="table table-responsive table-hover table-sm table-borderless table-content text-left"
              >
                <thead>
                  <tr>
                    <th></th>
                    <th>Coupon Code</th>
                    <th>Name</th>
                    <th>Type</th>
                    <th>For</th>
                    <th>Description</th>
                    <th>Uses</th>
                    <th>Max Uses</th>
                    <th>Max User Uses</th>
                    <th>Discount</th>
                    <th>Return</th>
                    <th>Restriction</th>
                    <th>Starts At</th>
                    <th>Ends At</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(coupon,index) in coupons" :key="coupon.id">
                    <td>
                      <i
                        class="fas fa-circle text-success"
                        aria-hidden="true"
                        v-if="coupon.active==1"
                      ></i>
                      <i class="fas fa-circle text-danger" aria-hidden="true" v-else></i>
                    </td>
                    <td>{{coupon.code}}</td>
                    <td>{{coupon.name}}</td>
                    <td>{{coupon.type.name}}</td>
                    <td>{{coupon.for_type.name}}</td>
                    <td>{{coupon.description}}</td>
                    <td>{{coupon.uses}}</td>
                    <td>{{coupon.maxUses}}</td>
                    <td>{{coupon.maxUserUses}}</td>
                    <td>
                      <span v-if="coupon.isFixed==1">₹</span>
                      {{coupon.discountAmount}}
                      <span
                        v-if="coupon.isFixed==0"
                      >%</span>
                    </td>
                    <td>{{coupon.return_type.name}}</td>
                    <td>{{coupon.restriction_type.name}}</td>
                    <td>{{coupon.startsAt}}</td>
                    <td>{{coupon.expiresAt}}</td>

                    <td class="text-dark">
                      <div>
                        <a
                          class="fa fa-trash"
                          href="#"
                          title="delete this coupon"
                          @click="deleteCoupon(index)"
                        ></a>

                        <router-link
                          :to="{name: 'editCoupon', params: {code: coupon.code}}"
                          tag="a"
                          target="_blank"
                        >
                          <i class="fa fa-pencil-alt pl-2"></i>
                        </router-link>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>

        <Loader :disable="loading" :success="success" :message="message" />
      </div>
    </div>

    <Footer @fetchCoupons="fetchCoupons" />
  </div>
</template>

<script>
import axios from "axios";
import firebase from "firebase";
import Vue from "vue";
import { bus } from "../../../main";
import Footer from "./CouponFooter.vue";
import Loader from "../../Loader";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
export default {
  name: "CouponDashBoard",
  components: {
    Footer,
    Loader
  },

  data() {
    return {
      coupons: [],

      search: null,
      searchArray: [],

      message: null,
      success: null,
      loading: false
    };
  },

  computed: {
    user() {
      return store.state.user;
    },

    size() {
      return store.state.couponPageSize;
    }
  },

  created() {
    this.fetchCoupons();

    bus.$on("paginateCoupon", data => {
      this.paginateCoupon(data["page"]);
    });
  },

  methods: {
    paginateCoupon(page) {
      this.$emit("fetchCoupons", page);
    },

    deleteCoupon(index) {
      if (this.user.designation == "admin") {
        if (confirm("You are about to delete " + this.coupons[index].name)) {
          this.loading = true;

          let url = domain + "coupon/" + this.coupons[index].code;
          axios
            .post(
              url,
              {
                _method: "delete"
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              if (res.data == "done") {
                this.coupons.splice(index, 1);
                this.loading = false;
              } else {
                alert(res.data);
              }
            });
        }
      } else {
        alert("You are not allowed to delete this");
      }
    },
    fetchCoupons(page) {
      if (this.user.designation == "admin") {
        {
          this.coupons = [];
          let url = domain + "coupon";

          if (!page) {
            page = 1;
          }

          axios
            .get(url, {
              params: {
                page: page,
                limit: this.size,
                type: "bring",
                forType: "bring",
                returnType: "bring",
                restrictionType: "bring",
                order: this.orderItems,
                orderWay: this.orderWay
              }
            })
            .then(res => {
              res.data.data.forEach(doc => {
                this.coupons.push(doc);
              });
              this.searchArray = this.coupons;

              store.dispatch("patchcouponPageCount", res.data.last_page);
              store.dispatch("patchcouponPageNumber", res.data.current_page);
              store.dispatch("patchtotalCoupons", res.data.total);
              this.loading = false;
            });
        }
      }
    },

    searching() {
      if (this.search) {
        this.coupons = this.searchArray.filter(
          v => v.code.indexOf(this.search) != -1
        );
      } else {
        this.coupons = this.searchArray;
      }
    }
  }
};
</script>


<style src="@/assets/css/tableContent.css" scoped>
</style>



