<template>
  <div class="container-fluid">
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <h4>UPLOADER's DETAILS</h4>
      </div>
      <div class="col-6 content-header small">
        <span>doorstep > uploaders</span>
      </div>
    </div>
    <div class="row">
      <div class="col-lg-2"></div>
      <div class="col-lg-8 col-sm-12">
        <!-- <Add uplodader/> -->
        <div class="card content border-1 shadow-sm">
          <div class="card-body">
            <div class="row pt-2">
              <div class="col-sm-12">
                <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                  <i class="fas fa-exclamation-triangle pr-2"></i>Fields marked with ( * ) is important and is to be filled
                </div>
                <form class="form pb-5 mb-4">
                  <div class="form-row">
                    <div class="col-sm-12">
                      <label>
                        User Id*
                        <small>(provided in Firebase Auth as uuid)</small>
                      </label>
                      <input
                        type="text"
                        v-model="userId"
                        class="form-control form-control-sm"
                        required
                        :disabled="disable"
                      />
                      <span
                        class="error text-danger font-weight-bold"
                        v-if="errors[1]"
                      >{{errors[1]}}</span>
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="col-sm-12">
                      <label>Name</label>
                      <input
                        type="text"
                        v-model="name"
                        class="form-control form-control-sm"
                        required
                        :disabled="disable"
                      />
                      <span
                        class="error text-danger font-weight-bold"
                        v-if="errors[2]"
                      >{{errors[2]}}</span>
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="col-sm-12">
                      <label>Email</label>
                      <input
                        type="text"
                        v-model="email"
                        class="form-control form-control-sm"
                        required
                        :disabled="disable"
                      />
                      <span
                        class="error text-danger font-weight-bold"
                        v-if="errors[3]"
                      >{{errors[3]}}</span>
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="col-sm-12">
                      <label>Phone Number</label>
                      <input
                        type="number"
                        v-model.number="phoneNumber"
                        class="form-control form-control-sm"
                        required
                        :disabled="disable"
                      />
                      <!-- <span
                              class="error text-danger font-weight-bold"
                              v-if="errors[4]"
                      >{{errors[4]}}</span>-->
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="col-sm-12">
                      <label>Country</label>
                      <select
                        v-model="country"
                        class="form-control form-control-sm"
                        :disabled="disable"
                      >
                        <option disabled="disabled" class="font-weight-bold">Choose country</option>
                        <option value="india" class="font-weight-bold" selected="selected">India</option>
                      </select>
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="col-sm-12">
                      <label>Package</label>
                      <select
                        v-model="Cpackage"
                        class="form-control form-control-sm"
                        :disabled="disable"
                      >
                        <option
                          disabled="disabled"
                          selected="selected"
                          class="font-weight-bold"
                        >Choose a package</option>
                        <option value="1" class="font-weight-bold">₹1</option>
                        <option value="3" class="font-weight-bold">₹3</option>
                      </select>
                      <span
                        class="error text-danger font-weight-bold"
                        v-if="errors[4]"
                      >{{errors[4]}}</span>
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="col-sm-12">
                      <label>Designation</label>
                      <select
                        v-model="designation"
                        class="form-control form-control-sm"
                        :disabled="disable"
                      >
                        <option disabled class="font-weight-bold">Designation</option>
                        <option selected value="user" class="font-weight-bold">user</option>
                        <option value="Admin" class="font-weight-bold">Admin</option>
                      </select>
                      <span
                        class="error text-danger font-weight-bold"
                        v-if="errors[5]"
                      >{{errors[5]}}</span>
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="col text-center">
                      <button
                        class="btn btn-sm btn-go-green font-weight-bold d-inline"
                        type="submit"
                        @click.prevent="add"
                        :disabled="disable"
                      >
                        <i class="fas fa-save pr-1"></i>
                        SAVE
                      </button>
                      <router-link to="/inventory" class="btn btn-sm btn-secondary">
                        <i class="fas fa-ban pr-1"></i>
                        <div class="font-weight-bold d-inline">CANCEL</div>
                      </router-link>
                    </div>
                  </div>

                  <div class="form-row" v-if="errors[1]">
                    <div class="col text-center text-danger">{{errors[0]}}</div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-lg-2"></div>
    </div>

    <Loader :disable="disable" :success="success" :message="message" />
  </div>
</template>
<script>
import axios from "axios";
import Vue from "vue";
import db from "../firebaseInit";
import firebase from "firebase";
import { domain } from "@/store/variable";
import Loader from "../Loader";

export default {
  name: "AddUploader",
  components: {
    Loader
  },

  data() {
    return {
      userId: null,
      name: null,
      email: null,
      phoneNumber: null,
      country: null,
      Cpackage: null,
      designation: null,

      //success
      message: null,
      disable: false,
      disableMessage: null,
      success: false,
      errors: []
    };
  },

  beforeRouteLeave(to, from, next) {
    if (
      this.userId ||
      this.name ||
      this.email ||
      this.Cpackage ||
      this.designation
    ) {
      if (confirm("Your changes will be lost.Do you want to leave the page?")) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },

  methods: {
    add() {
      let url = domain + "user/uploader";
      axios
        .post(
          url,
          {
            userId: this.userId,
            name: this.name,
            email: this.email,
            phoneNumber: this.phoneNumber,
            country: this.country,
            package: this.Cpackage,
            designation: this.designation
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": "application/json",
              "Access-Control-Allow-Origin": "*",
              "X-Requested-With": "XMLHttpRequest",
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          //// to show
          this.message = "Uploader Added";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        })
        .catch(err => {
          /// to show
          this.message = "Upload Failed";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        });
    },
    validate() {
      this.errors = [];
      if (
        this.userId &&
        this.name &&
        this.email &&
        this.Cpackage &&
        this.designation
      ) {
        return true;
      } else {
        if (!this.userId) {
          Vue.set(this.errors, 1, "Fill valid User Id");
        }
        if (!this.name) {
          Vue.set(this.errors, 2, "Fill in the name");
        }
        if (!this.email) {
          Vue.set(this.errors, 3, "Fill valid email");
        }

        if (!this.Cpackage) {
          Vue.set(this.errors, 4, "Fill valid email");
        }
        if (!this.designation) {
          Vue.set(this.errors, 5, "Fill valid email");
        }

        Vue.set(
          this.errors,
          0,
          "Something is missing!! Check out what you left"
        );
        return false;
      }
    }
  }
};
</script>


<style src="@/assets/css/tableContent.css" scoped>
</style>



