                <div class="modal-body">
                  <strong style="opacity:0.75">
                    Are you sure you wanna upload
                    <span
                      class="font-weight-bold text-success"
                    >₹ {{ currentPrice }}</span> as current price ?
                  </strong>
                  <p class="mt-3">
                    This will incur a
                    <span class="text-danger font-weight-bold">
                      loss of ₹
                      {{ this.profitLoss }}
                    </span> that is
                    <span class="font-weight-bold text-danger">{{ profitLossPer }}%</span> to doorstep
                  </p>
                </div>
                <div class="modal-footer">
                  <div class="mr-auto">
                    <a href="#" class="font-weight-bold text-dark">
                      <i class="fas fa-question pr-1"></i>Get Help
                    </a>
                  </div>
                  <button
                    type="button"
                    class="btn btn-secondary btn-sm font-weight-bold"
                    data-dismiss="modal"
                  >
                    <i class="fas fa-ban pr-2" @click="this.mrp=0"></i>CANCEL
                  </button>
                  <button
                    type="button"
                    class="btn btn-danger btn-sm font-weight-bold"
                    data-dismiss="modal"
                  >
                    <i class="fas fa-cloud-upload-alt pr-1 font-weight-bold"></i>UPLOAD
                  </button>
                </div>
              </div>
            </div>
          </div>
          <!-- modal end -->
        </div>
        <div class="col-lg-2"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import $ from "jquery";
import Vue from "vue";
import db from "../firebaseInit";
import firebase from "firebase";
import { firestorage } from "../firebaseInit";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";import store from "@/store/store";
import Loader from "../Loader";
import LoaderGif from "../LoaderGif";

export default {
  name: "addItem",
  components: {
    Loader,
    LoaderGif
  },
  data() {
    return {
      // ckeditor
      editor: ClassicEditor,
      editorData: "<p>Description</p>",
      editorConfig: {
        // The configuration of the editor.
      },
      // for item cats
      ModalContentLoading: false,

      // item details
      active: true,
      itemId: null,
      color: null,
      currentPrice: 0,
      name: null,
      image: null,
      detailImages: [],
      thumbnail: null,
      discount: 0,
      unit: null,
      unit2: null,
      mrp: 0,
      msp: 0,
      stock: 0,
      home: "hcat",
      allcat: "acat",
      disPriority: 0,
      description: "<p>Description</p>",
      qty: 0,
      sku: null,
      mfgDate: null,
      expiryDate: null,
      shelfLife: null,
      brand: [],
      address: null,
      ean: 0,
      profitLoss: 0,
      profitLossPer: 0,
      dropShip: false,
      dropShipAddress: null,
      maxLimit: 0,
      ingredients: "<p></p>",
      packingType: null,
      features: "<p></p>",
      similarProductId: 0,

      media: [], // for making image
      Discalculate: false, // disabling text field to calculate discount value
      imageOption: true,
      thumbnailOption: true,
      detailImageOption: true,
      disVal: false,
      message: null,
      disable: false,
      disableMessage: null,
      success: false,
      similarItems: [],
      errors: [],
      duplicate: true,
      othersimilarProduct: false,

      searchingItem: false,
      searchProductID: null,
      category: null,
      subcategory: null,
      cocategory: null,
      categories: [],
      subcategories: [],
      cocategories: [],

      availableBrands: [],
      enterBrand: false,

      units: [],
      priceBand: 10
    };
  },

  created() {
    this.ModalContentLoading = true;
    this.fetchCategories();
    this.getBrands();
    this.getUnits();
    this.ModalContentLoading = false;
  },

  computed: {
    user() {
      return store.state.user;
    }
  },
  beforeRouteLeave(to, from, next) {
    if (
      this.name ||
      this.discount > 0 ||
      this.mrp > 0 ||
      this.msp > 0 ||
      this.stock > 0 ||
      this.disPriority > 0 ||
      this.description != "description" ||
      this.qty > 0 ||
      this.maxLimit > 0
    ) {
      if (confirm("Your changes will be lost.Do you want to leave the page?")) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },

  methods: {
    async add() {
      // binging preloader on
      this.duplicate = await this.checkItem();
      let flag = true;

      if (
        this.validate() &&
        this.duplicate == false &&
        ((this.user.designation == "admin" ||
          this.user.designation == "webappUploader") &&
          this.user.status == "active")
      ) {
        this.disable = true;
        this.disableMessage = "Uploading Data, Kripya Shanti banaye rkhe...";

        // add brand
        if (this.enterBrand) {
          await this.addBrand();
          if (!isNaN(this.brand)) {
            flag = false;
          }
        }

        if (
          this.media[0] &&
          this.media[0] != null &&
          this.media[0] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active"
          ) {
            await this.uploadImage().then(downloadURL => {
              this.image = downloadURL;
            });
          } else {
            if (this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            }
            flag = false;
            this.disable = false;

            return;
          }
        }

        if (
          this.media[1] &&
          this.media[1] != null &&
          this.media[1] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active"
          ) {
            await this.uploadThumbnail().then(downloadURL => {
              this.thumbnail = downloadURL;
            });
          } else {
            if (this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            }
            flag = false;
            this.disable = false;

            return;
          }
        }

        if (
          this.media[2] &&
          this.media[2] != null &&
          this.media[2] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active" &&
            this.image != null &&
            this.image != ""
          ) {
            let files = this.$refs.detailImages.files;
            let mediaURL = [];
            this.detailImages = [];
            for (var i = 0; i < files.length; i++) {
              let file = files[i];

              await this.uploadDetailImage(file).then(downloadURL => {
                this.detailImages.push(downloadURL);
              });
            }
          } else {
            if (this.user.package != 3 && this.user.designation != "admin") {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            } else if (this.image == null || this.image == "") {
              alert("You need to upload the main image first");
            }
            flag = false;
            this.disable = false;
            return;
          }
        }

        if (flag == true) {
          if (
            (this.image && this.user.package == 3) ||
            this.user.package == 1 ||
            this.user.designation == "admin"
          ) {
            this.uploadData();
          } else {
            if (this.image && this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (!this.image && this.user.package == 3) {
              alert("Your package demands to upload image with it");
            }
            this.disable = false;
          }
        }
      }
    },

    addBrand() {
      if (
        (this.user.designation == "admin" ||
          this.user.designation == "webappUploader") &&
        this.user.status == "active"
      ) {
        let url = domain + "item/brand";
        axios
          .post(
            url,
            {
              name: this.brand
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            //// to show
            if (res.data) {
              let brand = {
                id: res.data.id,
                name: res.data.name
              };
              this.availableBrands.unshift(brand);

              this.brand = brand;
            }
          })
          .catch(err => {
            // delete in firebase
            /// to show
            this.message = "Uploading Brand Failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },

    change(type) {
      let files = null;
      if (type == "image") {
        files = this.$refs.image.files;
      } else if (type == "thumbnail") {
        files = this.$refs.thumbnail.files;
      } else if (type == "detailImages") {
        files = this.$refs.detailImages.files;
      }

      if (!files.length) {
        return;
      } else {
        if (type == "image") {
          this.media[0] = null;
          this.createImage(files[0], 0);
        } else if (type == "thumbnail") {
          this.media[1] = null;
          this.createImage(files[0], 1);
        } else if (type == "detailImages") {
          // if (this.media.length >= 2) {
          //   for (var i = 2; i < this.media.length; i++) {
          //     this.media.splice(i,1);
          //   }
          // }

          for (var i = 0; i < files.length; i++) {
            if (this.media.length <= 2) {
              this.createImage(files[i], 2 + i);
            } else {
              this.createImage(files[i], this.media.length + i);
            }
          }
        }
      }
    },

    checkItem() {
      let url = domain + "item";
      axios
        .get(url, {
          params: {
            name: this.name,
            unit: this.unit,
            qty: this.qty,
            count: "laoDo"
          }
        })
        .then(res => {
          if (res.data) {
            /// to show
            this.message =
              this.name +
              " phele hi daala jaa chuka hai. If you think it's wrong, please contact the management";
            this.success = true;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
            this.duplicate = true;
          } else {
            this.duplicate = false;
          }
        });
      return this.duplicate;
    },

    checkSimilarName() {
      this.searchingItem = true;
      let url = domain + "search";
      this.similarItems = [];
      axios
        .get(url, {
          params: {
            table: "items",
            data: this.processContent("string", this.name)
          }
        })
        .then(res => {
          if (res.data.exactSearch != "" && res.data.exactSearch != null) {
            /// to show
            res.data.exactSearch.forEach(doc => {
              this.similarItems.push(doc);
            });
          } else {
            this.similarItems.push({
              name: "No item Available"
            });

            this.setSimilarId("no", "item");
          }

          this.searchingItem = false;
        });
    },

    createImage(file, index) {
      let reader = new FileReader();
      reader.onload = e => {
        Vue.set(this.media, index, e.target.result);
        if (index == 0) {
          this.image = null;
        } else if (index == 1) {
          this.thumbnail = null;
        } else {
          this.detailImages = null;
        }
      };
      reader.readAsDataURL(file);
    },

    discountCal() {
      if (
        this.mrp != null &&
        this.currentPrice != null &&
        this.mrp != "" &&
        this.currentPrice != "" &&
        this.currentPrice <= this.mrp
      ) {
        this.discount = (
          ((this.mrp - this.currentPrice) / this.mrp) *
          100
        ).toFixed(0);
      } else {
        this.discount = 0;
      }
    },

    generateId() {
      let now = new Date();
      let nowTime = now.getTime();
      let nowDate =
        now.getUTCDate() + "" + now.getUTCMonth() + "" + now.getUTCFullYear();
      let id = "xxxxxxxx".replace(/[x]/g, function(c) {
        let r = (nowTime + Math.random() * 8) % 8 | 0;
        nowTime = Math.floor(nowTime / 16);
        return (c === "x" ? r : (r & 0x1) | 0x5).toString(8);
      });

      id =
        "PRO" +
        this.user.uuid.substring(0, 3).toUpperCase() +
        "-" +
        this.user.name.substring(0, 3).toUpperCase() +
        "-" +
        nowDate +
        "-" +
        id;
      return id;
    },

    generateSKU() {
      //let d = new Date().getTime();
      // let id = "xxxxxxxx".replace(/[x]/g, function(c) {
      //   let r = (d + Math.random() * 8) % 8 | 0;
      //   d = Math.floor(d / 16);
      //   return (c === "x" ? r : (r & 0x1) | 0x5).toString(8);
      // });

      let nameCode = this.name.split(" ");
      let nameId = "";
      nameCode.forEach(word => {
        var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = initialNameLetter.charCodeAt(0);
        if (
          !(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          nameId = nameId + initialNameLetter;
        }
      });

      let cat = this.categories[this.category].name
        .substring(0, 3)
        .toUpperCase()
        .split("");
      let catId = "";

      cat.forEach(word => {
        //var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = word.charCodeAt(0);
        if (
          !(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          catId = catId + word;
        }
      });

      let subcat = this.subcategories[this.subcategory].name
        .substring(0, 3)
        .toUpperCase()
        .split("");
      let subcatId = "";

      subcat.forEach(word => {
        //var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = word.charCodeAt(0);
        if (
          !(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          subcatId = subcatId + word;
        }
      });

      let cocat = this.cocategories[this.cocategory].name
        .substring(0, 3)
        .toUpperCase()
        .split("");
      let cocatId = "";

      cocat.forEach(word => {
        //var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = word.charCodeAt(0);
        if (
          !(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          cocatId = cocatId + word;
        }
      });

      let id = catId + "-" + subcatId + "-" + cocatId; //"-" + nameId

      return id;
    },

    getBrands() {
      let url = domain + "item/brand";
      let flag = 0;
      axios.get(url).then(res => {
        res.data.forEach((doc, index) => {
          let brand = {
            id: doc.id,
            name: doc.name
          };
          this.availableBrands.push(brand);
        });
      });
    },

    getCoCategories() {
      this.ModalContentLoading = true;
      this.cocategories = [];
      let url = domain + "cocat";
      axios
        .get(url, {
          params: {
            coid: this.subcategories[this.subcategory].coid
          }
        })
        .then(res => {
          if (res.data) {
            res.data.forEach(doc => {
              const cocat = {
                name: doc.name,
                itemId: doc.item_id,
                priceBandValue: doc.priceBandValue
              };
              this.cocategories.push(cocat);
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.ModalContentLoading = false;
    },

    getSubCategories() {
      this.ModalContentLoading = true;
      this.subcategories = [];
      this.cocategories = [];
      let url = domain + "subcat";
      axios
        .get(url, {
          params: {
            subcatid: this.categories[this.category].subcatid
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            const subcat = {
              name: doc.name,
              coid: doc.coid
            };
            this.subcategories.push(subcat);
          });
        })
        .catch(err => {
          console.log(err);
        });
      this.ModalContentLoading = false;
    },

    goBack() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push("/inventory");
    },

    getUnits() {
      this.units = [];
      let url = domain + "item/units";
      axios
        .get(url)
        .then(res => {
          if (res.data) {
            res.data.forEach(doc => {
              this.units.push(doc);
            });
          }
        })
        .catch(err => {
          this.units = [];
          console.log(err);
        });
    },

    fetchCategories() {
      let url = domain + "cat";
      let categories = [];
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            const cat = {
              name: doc.name,
              subcatid: doc.subcatcode
            };
            categories.push(cat);
          });
          this.categories = categories;
        })
        .catch(err => {
          console.log(err);
        });
    },

    pricaBandCalculator(change) {
      //calculating price band
      if (change == "priceBand" && this.mrp != null && this.mrp != "") {
        //calculating current price
        let takeout = Math.round(this.mrp * (this.priceBand / 100));
        this.currentPrice = this.mrp - takeout;

        //calculating msp
        let ctakeout = Math.round(this.currentPrice * (this.priceBand / 100));
        this.msp = this.currentPrice - ctakeout;
      } else if (change == "mrp" && this.priceBand != null) {
        // this.priceBand = (
        //   ((this.mrp - this.currentPrice) / this.mrp) *
        //   100
        // ).toFixed(0);

        let takeout = Math.round(this.mrp * (this.priceBand / 100));
        this.currentPrice = this.mrp - takeout;

        //calculating msp
        let ctakeout = Math.round(this.currentPrice * (this.priceBand / 100));
        this.msp = this.currentPrice - ctakeout;
      } else if (change == "currentPrice" && this.priceBand != null) {
        // this.priceBand = (
        //   ((this.mrp - this.currentPrice) / this.mrp) *
        //   100
        // ).toFixed(0);

        //calculating msp
        let ctakeout = Math.round(this.currentPrice * (this.priceBand / 100));
        this.msp = this.currentPrice - ctakeout;
      } else {
        //ghanta
      }

      this.discountCal();
    },

    processContent(type, data) {
      if (data) {
        if (type == "string") {
          return data.trim();
        } else if (type == "html") {
          let lastIndex = data.lastIndexOf("<p>&nbsp;</p>");
          if (lastIndex != -1 && data.length - 14 == lastIndex) {
            // will remove everywhere
            // data = data.replace(
            //   new RegExp(this.escapeRegExp("<p>&nbsp;</p>"), "g"),
            //   ""
            // );
            data = data.substring(0, lastIndex);
          }
          return data;
        }
      }
      return data;
    },

    profitLossCal() {
      if (
        this.currentPrice != null &&
        this.msp != null &&
        this.currentPrice != "" &&
        this.msp != ""
      ) {
        this.profitLoss = this.currentPrice - this.msp;
        this.profitLossPer = ((this.profitLoss / this.msp) * 100).toFixed(0);
        if (this.profitLoss < 0) {
          this.profitLossPer = Math.abs(this.profitLossPer);
          this.profitLoss = Math.abs(this.profitLoss);
          $("#profitLossModal").modal("show");
        }
      }
    },

    removeImage(type, index) {
      if (type == "image") {
        if(!isNaN(index)){
          Vue.set(this.media, 0, undefined);
        }
        else{
          this.image = null;  
        }
      }
      else if (type == "thumbnail") {
        if(!isNaN(index)){
          Vue.set(this.media, 1, undefined);
        }
        else{
          this.thumbnail = null;  
        }
      } else {
        //detail images start at pos 2 so that's why add 2
        if(!isNaN(index)){
          this.media.splice(index+2, 1);
        }
        else{
          this.detailImages = [];  
        }
      }
    },

    searchItem() {
      if (this.searchProductID) {
        let url = domain + "item";
        this.disable = true;
        this.disableMessage = "Checking our Databases...";
        axios
          .get(url, {
            params: {
              productId: this.searchProductID.trim()
            }
          })
          .then(res => {
            if (res.data.length > 0) {
              res.data.forEach(doc => {
                this.productId = doc.productId;
                this.itemId = doc.itemId;
                this.currentPrice = doc.currentPrice;
                this.name = doc.name;
                this.image = doc.image;
                this.thumbnail = doc.thumbnail;
                this.detailImages = doc.detailImages;
                this.discount = doc.discount;
                this.unit = doc.unit;
                this.unit2 = doc.unit2;
                this.color = doc.color;
                this.mrp = doc.MRP;
                this.stock = doc.stock;
                this.home = doc.home; // doubt;
                this.allcat = doc.allcat; //doubt;
                this.itemId = doc.itemId;
                this.sku = doc.sku;

                if (doc.dis_val == 1) {
                  this.disVal = true;
                } else {
                  this.disVal = false;
                }

                this.disPriority = doc.disPriority; //dou;
                this.description = doc.description;
                this.ingredients = doc.ingredients;
                this.features = doc.features;
                this.msp = doc.msp;
                this.qty = doc.qty;
                this.mfgDate = doc.mfgDate;
                this.expiryDate = doc.expiryDate;
                this.shelfLife = doc.shelfLife;
                this.brand = doc.item_brand;
                this.address = doc.address;
                this.ean = doc.EAN;
                this.similarProductId = doc.similarProductId;
                this.uploadedBy = doc.uploadedBy;
                this.uploadedAt = doc.uploadedAt;
                this.updatedAt = doc.updatedAt;
                this.maxLimit = doc.maxLimit;
                this.priceBand = Math.round(
                  ((this.currentPrice - this.msp) / this.currentPrice) * 100
                );

                if (doc.dropShipAddress) {
                  this.dropShip = true;
                  this.dropShipAddress = doc.dropShipAddress;
                }
                if (doc.active == 1) {
                  this.active = true;
                } else {
                  this.active = false;
                }

                //

                // fetching category cocat subcat
                let courl = domain + "cocat";
                axios
                  .get(courl, {
                    params: {
                      item_id: this.itemId
                    }
                  })
                  .then(res => {
                    if (res.data) {
                      this.cocategories = res.data;
                      // pusing just 1 cocategory to array and setting it's index
                      this.cocategory = 0;
                      this.priceBand = this.cocategories[
                        this.cocategory
                      ].priceBandValue;
                      //subcat
                      let suburl = domain + "subcat";
                      axios
                        .get(suburl, {
                          params: {
                            coid: res.data[0].coid
                          }
                        })
                        .then(ressub => {
                          if (ressub.data) {
                            this.subcategories = ressub.data;
                            this.subcategory = 0;
                            // fetching category
                            let caturl = domain + "cat";
                            axios
                              .get(caturl, {
                                params: {
                                  subcatcode: ressub.data[0].subcatid
                                }
                              })
                              .then(rescat => {
                                if (rescat.data) {
                                  this.categories = rescat.data;
                                  this.category = 0;
                                }
                              });
                          }
                        });
                    }
                  });
              });
              this.disable = false;
              this.disableMessage =
                "Uploading Data, Kripya Shanti banaye rkhe...";
            } else {
              this.message = "This product Don't exist in database";
              this.success = true;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);

              this.disable = false;
              this.disableMessage =
                "Uploading Data, Kripya Shanti banaye rkhe...";
            }
          });
      } else {
        if (
          this.itemId &&
          this.name &&
          this.discount >= 0 &&
          this.currentPrice > 0 &&
          this.unit &&
          this.mrp > 0 &&
          this.msp > 0 &&
          this.stock > 0 &&
          this.allcat &&
          this.disPriority > 0 &&
          this.description &&
          (this.disVal == true || this.disVal == false) &&
          this.qty > 0
        ) {
          // agr koi data hai tho gayb na kre, dimag ka bhosda ho jaega agr delete hua itna mehnat se likha data//

          this.category = null;
          this.subcategory = null;
          this.cocategory = null;
          this.categories = [];
          this.subcategories = [];
          this.cocategories = [];
          this.fetchCategories();
        } else {
          ///////otherwise udha do sb///////
          this.currentPrice = 0;
          this.name = null;
          this.image = null;
          this.thumbnail = null;
          this.discount = 0;
          this.unit = null;
          this.unit2 = null;
          this.mrp = 0;
          this.msp = 0;
          this.stock = 0;
          this.home = "hcat"; // doubted
          this.allcat = "acat"; //doubted
          this.itemId = null;
          this.disPriority = 0; //doubt
          this.qty = 0;
          this.sku = null;
          this.description = null;
          this.media = [];
          this.mfgDate = null;
          this.expiryDate = null;
          this.shelfLife = null;
          this.brand = null;
          this.address = null;
          this.ean = 0;
          this.disVal = false;
          this.dropShip = false;
          this.dropShipAddress = null;
          this.maxLimit = 0;
          this.ingredients = "<p></p>";
          this.packingType = null;
          this.features = "<p></p>";
          this.category = null;
          this.subcategory = null;
          this.cocategory = null;
          this.categories = [];
          this.subcategories = [];
          this.cocategories = [];
          this.fetchCategories();
        }
      }
    },

    setBrand(brand) {
      this.enterBrand = false;
      this.brand = brand;
    },

    setCocategory() {
      this.itemId = this.cocategories[this.cocategory].itemId;
      this.priceBand = this.cocategories[this.cocategory].priceBandValue;
    },

    setCurrentPrice() {
      this.discountCal();
      this.pricaBandCalculator("currentPrice");
    },

    setMrp() {
      this.discountCal();
      this.pricaBandCalculator("mrp");
    },

    setSimilarId(type, data) {
      if (type) {
        if (type == "id") {
          this.similarProductId = this.similarItems[data].similarProductId;
        } else if (type == "productId") {
          let url = domain + "item";
          this.searchingItem = true;

          let id = data.target.value;
          axios
            .get(url, {
              params: {
                productId: id
              }
            })
            .then(res => {
              res.data.forEach(doc => {
                this.similarProductId = doc.similarProductId;
              });
              this.searchingItem = false;
            });
        } else {
          let url = domain + "item";
          this.searchingItem = true;
          axios
            .get(url, {
              params: {
                max: "similarProductId"
              }
            })
            .then(res => {
              this.similarProductId = Number(res.data) + 1;
              this.searchingItem = false;
            });
        }
      }
    },

    uploadData() {
      let productId = this.generateId();
      this.sku = this.generateSKU();

      this.name = this.name.toLowerCase().replace(/(\s+.)|^\w/gi, function(v) {
        return v.toUpperCase();
      });
      if (!this.dropShip) {
        this.dropshipAddress = null;
      }

      let url = domain + "item";
          axios
            .post(
              url,
              {
                productId: productId,
                currentPrice: this.currentPrice,
                name: this.processContent("string", this.name),
                image: this.image,
                detailImages: this.detailImages,
                thumbnail: this.thumbnail,
                discount: this.discount,
                ingredients: this.processContent("html", this.ingredients),
                features: this.processContent("html", this.features),
                packingType: this.packingType,
                unit: this.unit,
                unit2: this.unit2,
                color: this.processContent("string", this.color),
                MRP: this.mrp,
                msp: this.msp,
                stock: this.stock,
                home: this.home, // doubted
                allcat: this.allcat, //doubted
                itemId: this.itemId,
                dis_val: this.disVal,
                disPriority: this.disPriority, //doubt
                description: this.processContent("string", this.description),
                qty: this.processContent("string", this.qty),
                sku: this.sku,
                mfgDate: this.processContent("string", this.mfgDate),
                expiryDate: this.processContent("string", this.expiryDate),
                shelfLife: this.processContent("string", this.shelfLife),
                brand: this.brand.name,
                brandName: this.brand.id,
                address: this.processContent("string", this.address),
                ean: this.ean,
                active: this.active,
                dropShipAddress: this.processContent(
                  "string",
                  this.dropShipAddress
                ),
                similarProductId: this.similarProductId,
                maxLimit: this.maxLimit,
                uploadedAt: new Date(),
                updatedAt: new Date()
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {

              db.collection("items")
              .doc(productId)
              .set({
                productId: productId,
                currentPrice: this.currentPrice,
                name: this.processContent("string", this.name),
                image: this.image,
                detailImages: this.detailImages,
                thumbnail: this.thumbnail,
                discount: this.discount,
                ingredients: this.processContent("html", this.ingredients),
                features: this.processContent("html", this.features),
                packingType: this.packingType,
                unit: this.unit,
                unit2: this.unit2,
                color: this.processContent("string", this.color),
                MRP: this.mrp,
                msp: this.msp,
                stock: this.stock,
                home: this.home, // doubted
                allcat: this.allcat, //doubted
                itemId: this.itemId,
                dis_val: this.disVal,
                disPriority: this.disPriority, //doubt
                description: this.processContent("string", this.description),
                qty: this.processContent("string", this.qty),
                sku: this.sku,
                mfgDate: this.processContent("string", this.mfgDate),
                expiryDate: this.processContent("string", this.expiryDate),
                shelfLife: this.processContent("string", this.shelfLife),
                brand: this.brand,
                address: this.processContent("string", this.address),
                ean: this.ean,
                active: this.active,
                dropShipAddress: this.processContent("string", this.dropShipAddress),
                similarProductId: this.similarProductId,
                maxLimit: this.maxLimit,
                uploadedAt: new Date(),
                updatedAt: new Date()
              })
              .then(res => {
                //// to show
                this.message = "Data Upload Success";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch(err => {
                console.log(err);

                this.message = "Data Upload In Firebase Failed";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
              
            })
            .catch(err => {
              
              //// to show
              this.message = "Data Upload Failed";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);

            });

      
    },

    uploadImage() {
      if (this.media[0] != null && this.media[0] != undefined) {
        let file = this.$refs.image.files[0];
        this.image = null;
        return firestorage
          .ref("items/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "Image Upload Failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    },

    uploadThumbnail() {
      if (this.media[1] != null && this.media[1] != undefined) {
        let fileT = this.$refs.thumbnail.files[0];
        this.thumbnail = null;
        return firestorage
          .ref("thumbnails/" + fileT.name)
          .put(fileT)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "Thumbnail Upload failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    },

    uploadDetailImage(file) {
      if (file) {
        return firestorage
          .ref("items/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "More Images Upload failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    },

    validate() {
      this.errors = [];
      if (
        this.itemId &&
        this.name &&
        this.discount >= 0 &&
        this.currentPrice > 0 &&
        this.unit &&
        this.mrp > 0 &&
        this.msp > 0 &&
        this.stock > 0 &&
        this.allcat &&
        this.disPriority > 0 &&
        this.description &&
        (this.disVal == true || this.disVal == false) &&
        this.qty &&
        this.similarProductId > 0 &&
        this.brand
      ) {
        return true;
      } else {
        if (!this.itemId) {
          Vue.set(this.errors, 0, "Fill valid Item Id");
        }
        if (!this.name) {
          Vue.set(this.errors, 1, "Fill in the name");
        }
        if (!this.unit) {
          Vue.set(this.errors, 2, "Choose Valid Unit");
        }
        if (this.mrp <= 0) {
          Vue.set(this.errors, 3, " MRP can't be 0");
        }

        if (this.currentPrice <= 0) {
          Vue.set(this.errors, 4, "Current Price can't be 0");
        }
        if (this.msp <= 0) {
          Vue.set(this.errors, 5, "MSP can't be 0");
        }
        if (this.stock <= 0) {
          Vue.set(this.errors, 6, "Stock Value can't be 0");
        }
        if (!this.allcat) {
          Vue.set(this.errors, 8, "Fill in the All category");
        }
        if (this.disPriority <= 0) {
          Vue.set(this.errors, 10, "Fill in the Priority");
        }
        if (this.disVal != true && this.disVal != false) {
          Vue.set(this.errors, 9, "Choose valid Discount Value");
        }
        if (!this.description) {
          Vue.set(this.errors, 12, "Fill in the Description");
        }
        if (!this.qty) {
          Vue.set(this.errors, 13, "Quantity can't be 0");
        }

        if (this.qty <= 0) {
          Vue.set(this.errors, 15, "You need to choose one option");
        }
        if (this.brand.length == 0 || this.brand == null) {
          Vue.set(this.errors, 16, "Choose a Brand");
        }

        Vue.set(
          this.errors,
          14,
          "Something is missing!! Check out what you left"
        );
        return false;
      }
    }
  }
};
</script>

<style scoped>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>





