<template>
  <div class="container-fluid">
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <h4>
          Order ID :
          <span class="response">#{{orderId}}</span>
        </h4>
      </div>
      <div class="col-6 content-header text-center small">
        <span>doorstep > orders > #{{orderId}}</span>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <!-- order -->
        <div class="card content border-1 shadow-sm mb-4">
          <div class="card-body container-fluid">
            <div class="row order-content">
              <div class="col-4 order-by-header">
                <!-- order guy -->
                <div class="media pt-2">
                  <img
                    src="https://vignette.wikia.nocookie.net/joshua-eatons-stupid-jokes/images/9/97/C80902787040a7562566bd10cb4a44e0--college-hairstyles-hairstyles-for-guys.jpg/revision/latest?cb=20170809214246"
                    class="img-fluid rounded-circle align-self-center"
                  />
                  <div class="media-body pl-4">
                    <h6>{{deliveryAddress.name}}</h6>
                    <span>{{deliveryAddress.mobileNumber}}</span>
                  </div>
                </div>
              </div>
              <div class="col-4" style="font-size:small;">
                <div>
                  <span class="font-weight-bold">Order Address</span>
                  <span
                    class="response"
                  >#{{deliveryAddress.houseOfficeNo}}/{{deliveryAddress.street}}, {{deliveryAddress.city}},{{deliveryAddress.state}},{{deliveryAddress.pincode}}</span>
                </div>
                <div>
                  <span class="font-weight-bold">Ordered At</span>
                  <span class="response">{{orderDate}}, {{orderTime}}</span>
                </div>
              </div>
              <div class="col-4" style="font-size:small;">
                <div>
                  <span class="font-weight-bold">Payment Mode</span>
                  <span class="response">{{paymentMode}}</span>
                </div>
                <div>
                  <span class="font-weight-bold">Paid Amount</span>
                  <span class="response">₹{{paidAmount}}</span>
                </div>
                <div>
                  <span class="badge badge-delivery-success pl-2 pr-2">{{status.status}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- status -->
        <div class="card content border-1 shadow-sm mb-4">
          <div class="card-body container-fluid">
            <div class="row order-content">
              <div class="col-12">
                <OrderStatus :statuses="track_status"></OrderStatus>
              </div>
            </div>
          </div>
        </div>

        <!-- -->
        <div class="card content border-1 shadow-sm">
          <div class="card-body">
            <div class="row pt-2">
              <div class="col-6">
                <form class="form-inline">
                  <div class="input-group content-search border">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">
                        <i class="fa fa-search"></i>
                      </span>
                    </div>
                    <input
                      type="text"
                      v-model="search"
                      class="form-control hz-input"
                      placeholder="Search Here..."
                      aria-label="Search Here..."
                      aria-describedby="basic-addon1"
                      @keyup="searching"
                    />
                  </div>
                </form>
              </div>
              <div class="col-6">
                <div class="float-right">
                  <button
                    class="btn btn-sm btn-go-green font-weight-bold d-inline"
                    @click.prevent="checkAvailability()"
                    v-if="status.id==4 || status.id==5"
                  >Check Availability</button>
                  <!-- order select not for return, cancelled orders -->
                  <form
                    class="form-inline"
                    v-if="status && (status.id==4 || status.id==5 || status.id==2 || status.id==6)"
                  >
                    <span class="font-weight-semibold pr-3">Select Order Status</span>
                    <select
                      v-model="action"
                      name="status"
                      class="custom-select"
                      @change="updateAction()"
                    >
                      <option disabled="disabled" selected="selected">Choose Action</option>
                      <!-- <option value="1">Cancel</option> -->
                      <option value="4" v-if="status.id==3">Processing</option>
                      <option value="5" v-if="status.id==4">Packed</option>
                      <option value="6" v-if="status.id==5">Ready For Pickup</option>
                      <option value="7" v-if="status.id==6">Delivered</option>
                      <option value="9" v-if="status.id==2">Return Approved</option>
                      <option value="8">Failed</option>

                      <!-- <option
                      :value="status.id"
                      v-for="status in orderStatus"
                      :key="status.id"
                      >{{status.status}}</option>-->
                      <!-- <option value="delete">Delete</option> -->
                    </select>
                  </form>
                </div>
              </div>
            </div>
            <div class="table-responsive pt-4">
              <table class="table table-hover table-sm table-borderless table-content text-left">
                <thead>
                  <tr>
                    <th>
                      <form>
                        <div class="custom-control custom-checkbox">
                          <input type="checkbox" class="custom-control-input" id="customCheck" />
                          <label class="custom-control-label" for="customCheck"></label>
                        </div>
                      </form>
                    </th>
                    <th>Product Id</th>
                    <th>Products</th>
                    <th>Product Name</th>
                    <th>Cost Per Item</th>
                    <th>Ordered Quantity</th>
                    <th>Unit</th>
                    <th>Total Cost</th>
                    <th v-if="status.id==4 || status.id==5">Availability Status</th>
                    <th v-if="status.id==4 || status.id==5">Current Availability</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="product in products" :key="product.id">
                    <td :class="product.rowClass">
                      <form>
                        <div class="custom-control custom-checkbox">
                          <input type="checkbox" class="custom-control-input" id="product" />
                          <label class="custom-control-label" for="product"></label>
                        </div>
                      </form>
                    </td>
                    <td>{{product.productId}}</td>
                    <td>
                      <div class="d-flex">
                        <img :src="product.image" class="img-fluid content-image" />
                      </div>
                    </td>
                    <td>{{product.name}}</td>
                    <td>₹{{product.pivot.currentPrice}}</td>
                    <td>{{product.pivot.quantity}}</td>
                    <td class="text-dark">{{product.qty}}{{product.unit}}</td>
                    <td>{{product.pivot.quantity * product.pivot.currentPrice}}</td>
                    <td class="text-dark" v-if="status.id==4 || status.id==5">
                      <span
                        class="bagde badge-paid pl-1 pr-1"
                        v-if="product.pivot.quantity<=product.stock"
                      >Available</span>
                      <span class="bagde badge-failed pl-1 pr-1" v-else>Not Available</span>
                    </td>
                    <td class="text-dark" v-if="status.id==4 || status.id==5">{{product.stock}}</td>
                  </tr>
                  <tr>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line" v-if="status.id==4 || status.id==5"></td>
                    <td class="thick-line" v-if="status.id==4 || status.id==5"></td>
                    <td class="thick-line text-center">
                      <strong>Subtotal</strong>
                    </td>
                    <td class="thick-line text-right">₹{{totalDiscountedAmount}}</td>
                  </tr>
                  <tr>
                    <td class="no-line"></td>
                    <td class="no-line"></td>
                    <td class="no-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line" v-if="status.id==4 || status.id==5"></td>
                    <td class="thick-line" v-if="status.id==4 || status.id==5"></td>
                    <td class="no-line text-center">
                      <strong>Delivery Charges</strong>
                    </td>
                    <td class="no-line text-right">₹{{deliveryCharges}}</td>
                  </tr>
                  <tr>
                    <td class="no-line"></td>
                    <td class="no-line"></td>
                    <td class="no-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line" v-if="status.id==4 || status.id==5"></td>
                    <td class="thick-line" v-if="status.id==4 || status.id==5"></td>
                    <td class="no-line text-center">
                      <strong>Total</strong>
                    </td>
                    <td class="no-line text-right">₹{{totalPayableAmount}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Loader :disable="loading" :success="success" :message="message" />
  </div>
</template>
<script>
//import orderView from "./SingleView.vue";
import axios from "axios";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import firebase from "firebase";
import OrderStatus from "./orderStatus.vue";
import Loader from "../../Loader";

export default {
  name: "ViewOrder",
  components: {
    Loader,
    OrderStatus
  },

  data() {
    return {
      orderId: null,
      status: null,
      track_status: [],
      orderStatus: [],
      products: [],
      deliveryAddress: null,
      orderDate: null,
      orderTime: null,
      totalDiscountedAmount: 0,
      totalPayableAmount: 0,
      deliveryCharges: 0,
      paymentMode: null,
      paidAmount: 0,

      select: false,
      action: null,

      message: null,
      success: null,
      loading: false,

      search: null,
      searchArray: []
    };
  },

  beforeRouteEnter(to, from, next) {
    let url = domain + "order";
    axios
      .get(url, {
        params: {
          id: to.params.orderId,
          products: "budhbak",
          trackStatus: "budhbak",
          payment_mode: "budhbak",
          userAddress: "budhbak"
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          let dateTime = new Date(doc.orderTime);
          let date =
            dateTime.getDate() +
            "/" +
            dateTime.getMonth() +
            "/" +
            dateTime.getFullYear();
          var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
          var hours = dateTime.getHours() % 12 || 12;
          let time =
            hours +
            ":" +
            dateTime.getMinutes() +
            ":" +
            dateTime.getSeconds() +
            " " +
            AmOrPm;
          next(vm => {
            vm.orderId = doc.id;
            vm.deliveryAddress = doc.user_address;
            vm.orderDate = date;
            vm.orderTime = time;
            vm.status = doc.order_status;
            vm.track_status = doc.track_status;
            vm.totalDiscountedAmount = doc.totalDiscountedAmount;
            vm.totalPayableAmount = doc.totalPayableAmount;
            vm.deliveryCharges = doc.deliveryCharges;
            vm.paymentMode = doc.payment_mode.mode;
            vm.paidAmount = doc.paidAmount;
            //products
            doc.products.forEach(prod => {
              vm.products.push(prod);
            });
            vm.searchArray = vm.products;
          });
        });
      })
      .catch(err => {
        console.log(err.message);
      });
  },

  watch: {
    $route: "fetchOrder"
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  created() {
    this.getOrderStatus();
  },

  methods: {
    checkAvailability() {
      if (this.status.id == 4 || this.status.id == 5) {
        let url = domain + "order/check/availability/" + this.orderId;
        axios
          .get(url)
          .then(res => {
            if (res.data == true) {
              this.message = "All Items Available, You can proceed to pack";
              this.success = true;
              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 3000);
            } else {
              this.message = "All Items Not Available, User has been notified";
              this.success = true;
              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 3000);
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        alert("This function is not valid now");
      }
    },
    fetchOrder() {
      let url = domain + "order";
      axios
        .get(url, {
          params: {
            id: this.$route.params.orderId,
            products: "budhbak",
            trackStatus: "budhbak",
            paymentMode: "budhbak"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            let dateTime = new Date(doc.orderTime);
            let date =
              dateTime.getDate() +
              "/" +
              dateTime.getMonth() +
              "/" +
              dateTime.getFullYear();
            var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
            var hours = dateTime.getHours() % 12 || 12;
            let time =
              hours +
              ":" +
              dateTime.getMinutes() +
              ":" +
              dateTime.getSeconds() +
              " " +
              AmOrPm;

            // putting data
            this.orderId = doc.id;
            this.deliveryAddress = doc.user_address;
            this.orderDate = date;
            this.orderTime = time;
            this.status = doc.order_status;
            this.track_status = doc.track_status;
            this.totalDiscountedAmount = doc.totalDiscountedAmount;
            this.totalPayableAmount = doc.totalPayableAmount;
            this.deliveryCharges = doc.deliveryCharges;
            this.paymentMode = doc.payment_mode.mode;
            this.paidAmount = doc.paidAmount;
            //products
            doc.products.forEach(prod => {
              this.products.push(prod);
            });
            this.searchArray = this.products;
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    },

    getOrderStatus() {
      let url = domain + "order/status";
      axios
        .get(url)
        .then(res => {
          this.orderStatus = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },

    updateAction() {
      if (this.user.designation == "admin") {
        this.loading = true;
        if (this.action) {
          let url = domain + "order/" + this.orderId;
          axios
            .post(
              url,
              {
                _method: "PUT",
                status: this.action
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              this.message = "Success";
              this.success = true;
              this.loading = false;

              var status = {};
              this.orderStatus.forEach(state => {
                if (state.id == this.action) {
                  status = state;
                }
              });

              this.status = status;

              //colsing inputs

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              this.message = "Some error occured";
              this.success = true;
              this.loading = false;
              console.log(err.message);

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        }
      } else {
        confirm("You are not allowed to make changes");
      }
    },

    searching() {
      if (this.search) {
        this.products = this.searchArray.filter(
          v => v.name.toLowerCase().indexOf(this.search.toLowerCase()) != -1
        );
      } else {
        this.products = this.searchArray;
      }
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped>
</style>

<style scoped>
.order-by-header h6 {
  color: rgba(47, 48, 61, 1);
  font-weight: 700;
  margin-bottom: 0.2em;
}

.order-by-header .heading {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
}

.order-by-header .location {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
  font-size: smaller;
}

.order-content {
  font-size: small;
}

.order-content img {
  width: 4em;
}

.response {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 700;
}
</style>



