<template>
  <div class="container-fluid">
    Your Download will being in 5 secs
    <div id="bill" ref="content">
      <div class="row">
        <div class="col-sm-12">
          <div class="invoice-title">
            <h3 class="d-inline-block align-self-center">
              <img
                src="../../../../assets/appLogo.png"
                alt="DoorStep"
                style="width:2em"
                class="align-self-center pr-2"
              />
              FasDo
            </h3>
            <!-- <div class="float-right"> -->
            <h5 class="float-right pt-2 mr-2">Invoice</h5>
            <br />
            <!-- <small>(Duplicate Copy)</small> -->
            <!-- </div> -->
          </div>
          <hr />
          <div class="row mr-1">
            <div class="col-sm-6">
              <address>
                <strong>Billed To:</strong>
                <span v-if="user.name != 'Fasdorian'">
                  <br />
                  {{user.name}}
                </span>
                <span v-if="user.email != 'Fasdorian'">
                  <br />
                  {{user.email}}
                </span>
                <br />
                {{user.mobileNumber}}
              </address>
            </div>
            <div class="col-sm-6">
              <address class="float-right text-right">
                <strong>Shipped To:</strong>
                <br />
                {{deliveryAddress.name}}
                <br />
                {{deliveryAddress.houseOfficeNo}}, {{deliveryAddress.street}}
                <br />
                {{deliveryAddress.landmark}}
                <br />
                {{deliveryAddress.city}},{{deliveryAddress.state}},{{deliveryAddress.pincode}}
                <br />
                {{deliveryAddress.mobileNumber}}
              </address>
            </div>
          </div>
          <div class="row mr-1">
            <div class="col-sm-6">
              <address>
                <strong>Order Number:</strong>
                <br />
                {{orderId}}
                <br />
                <strong>Order Date:</strong>
                <br />
                {{orderDate}}
                <br />
                <br />
              </address>
            </div>
            <div class="col-sm-6">
              <address class="text-right">
                <strong>Payment Method:</strong>
                <br />
                {{paymentMode}}
              </address>
            </div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-md-12">
          <div class="panel panel-default">
            <div class="panel-heading">
              <h4 class="panel-title pt-4 pb-4">
                <strong>Order summary</strong>
              </h4>
            </div>
            <div class="panel-body">
              <div class="table-responsive">
                <table class="table table-condensed">
                  <thead>
                    <tr>
                      <th>S. No.</th>
                      <td>
                        <strong>Item</strong>
                      </td>
                      <td class="text-center">
                        <strong>Unit Price</strong>
                      </td>
                      <td class="text-center">
                        <strong>Quantity</strong>
                      </td>
                      <td class="text-right">
                        <strong>Total Amount</strong>
                      </td>
                    </tr>
                  </thead>
                  <tbody>
                    <!-- foreach ($order->lineItems as $line) or some such thing here -->
                    <tr v-for="(product,index) in products" :key="product.productId">
                      <td>{{index+1}}</td>
                      <td>{{product.name}}</td>
                      <td class="text-center">₹{{product.pivot.currentPrice}}</td>
                      <td class="text-center">{{product.pivot.quantity}}</td>
                      <td
                        class="text-right"
                      >₹{{product.pivot.currentPrice * product.pivot.quantity}}</td>
                    </tr>
                    <tr>
                      <td class="thick-line"></td>
                      <td class="thick-line"></td>
                      <td class="thick-line"></td>
                      <td class="thick-line text-center">
                        <strong>Subtotal</strong>
                      </td>
                      <td class="thick-line text-right">₹{{totalDiscountedAmount}}</td>
                    </tr>
                    <tr>
                      <td class="no-line"></td>
                      <td class="no-line"></td>
                      <td class="no-line"></td>
                      <td class="no-line text-center">
                        <strong>Shipping Charges</strong>
                      </td>
                      <td class="no-line text-right">₹{{deliveryCharges}}</td>
                    </tr>
                    <tr>
                      <td class="no-line"></td>
                      <td class="no-line"></td>
                      <td class="no-line"></td>
                      <td class="no-line text-center">
                        <strong>Total</strong>
                      </td>
                      <td class="no-line text-right">₹{{totalPayableAmount}}</td>
                    </tr>
                  </tbody>
                </table>
                <div class="pt-4 pb-4">
                  <h6>
                    <strong>Amount In words:</strong>
                  </h6>
                  <strong>₹{{convertNumberToWords(totalPayableAmount)}} Only</strong>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-md-6 mt-4">
          <div class="media border p-3" style="width:24em;">
            <img
              src="../../../../assets/qrCode.png"
              class="img-fluid align-self-center"
              style="width:7em;"
            />
            <div class="media-body text-center pt-4">
              <h5>SCAN TO DOWNLOAD</h5>
              <span>
                <b>FASDO</b> FROM PLAYSTORE
              </span>
            </div>
          </div>
        </div>
        <div class="col-md-6 mt-4">
          <div class="pt-2 mr-2">
            <span class="float-right">Order Via:</span>
            <br />
            <span class="float-right">
              <img src="../../../../assets/whatsapp.png" class="img-fluid pr-1" style="width:1.5em" />
              <span>WhatsApp : {{number}}</span>
            </span>
            <br />
            <span class="float-right">
              <img src="../../../../assets/phone.png" class="img-fluid pr-1" style="width:1.5em" />
              <span>Phone : {{number}}</span>
            </span>
            <br />
            <span class="float-right">
              <img src="../../../../assets/sms.png" class="img-fluid pr-1" style="width:1.3em" />
              <span>SMS : {{number}}</span>
            </span>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-12 text-center mt-2">
          <small>FasDo</small>
          <br />
          <small class="font-weight-light mt-1">This bill is digitially signed</small>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { domain } from "@/store/variable";
import jsPDF from "jsPDF";
import html2canvas from "html2canvas";

export default {
  name: "OrderBill",
  data() {
    return {
      orderId: null,
      products: [],
      orderDate: null,
      orderTime: null,
      totalPayableAmount: 0,
      deliveryCharges: 0,
      totalDiscountedAmount: 0,
      user: [],
      deliveryAddress: [],
      paymentMode: null,
      number: "(+91)9123109983"
    };
  },

  beforeRouteEnter(to, from, next) {
    let url = domain + "order";
    axios
      .get(url, {
        params: {
          id: to.params.orderId,
          products: "budhbak",
          payment_mode: "budhbak",
          user: "budhbak",
          userAddress: "budhbak"
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          let dateTime = new Date(doc.orderTime);
          let date =
            dateTime.getDate() +
            "/" +
            dateTime.getMonth() +
            "/" +
            dateTime.getFullYear();
          let time =
            dateTime.getHours() +
            ":" +
            dateTime.getMinutes() +
            ":" +
            dateTime.getSeconds();
          next(vm => {
            vm.orderId = doc.id;
            vm.orderDate = date;
            vm.orderTime = time;
            vm.totalDiscountedAmount = doc.totalDiscountedAmount;
            vm.totalPayableAmount = doc.totalPayableAmount;
            vm.deliveryCharges = doc.deliveryCharges;
            vm.user = doc.user;
            vm.deliveryAddress = doc.user_address;
            vm.paymentMode = doc.payment_mode.mode;

            //products
            doc.products.forEach(prod => {
              vm.products.push(prod);
            });
          });
        });
      })
      .catch(err => {
        console.log(err.message);
      });
  },

  watch: {
    $route: "fetchOrder"
  },

  mounted() {
    window.html2canvas = html2canvas;
    setTimeout(() => {
      this.generatePdf();
    }, 5000);
  },

  methods: {
    fetchOrder() {
      let url = domain + "order";
      axios
        .get(url, {
          params: {
            id: this.$route.params.orderId,
            products: "budhbak",
            payment_mode: "budhbak",
            user: "budhbak",
            userAddress: "budhbak"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            let dateTime = new Date(doc.orderTime);
            let date =
              dateTime.getDate() +
              "/" +
              dateTime.getMonth() +
              "/" +
              dateTime.getFullYear();
            let time =
              dateTime.getHours() +
              ":" +
              dateTime.getMinutes() +
              ":" +
              dateTime.getSeconds();

            // putting data
            this.orderId = doc.id;
            this.orderDate = date;
            this.orderTime = time;
            this.totalDiscountedAmount = doc.totalDiscountedAmount;
            this.totalPayableAmount = doc.totalPayableAmount;
            this.deliveryCharges = doc.deliveryCharges;
            this.user = doc.user;
            this.deliveryAddress = doc.user_address;
            this.paymentMode = doc.payment_mode.mode;

            //products
            doc.products.forEach(prod => {
              this.products.push(prod);
            });
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    },

    async generatePdf() {
      let pdfName = this.orderId;
      let pdf = new jsPDF("p", "mm", "a4", true);
      let contentHTML = this.$refs.content.innerHTML;

      html2canvas(document.querySelector("#bill")).then(canvas => {
        var imgData = canvas.toDataURL("Image/jpeg", 1.0);

        var imgWidth = 190;
        var pageHeight = 285;
        var imgHeight = (canvas.height * imgWidth) / canvas.width;
        var heightLeft = imgHeight;
        var position = 10;
        pdf.addImage(
          canvas.toDataURL("Image/jpeg", 1.0),
          "JPEG",
          10,
          position,
          imgWidth,
          imgHeight
        );
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          pdf.addPage();
          pdf.addImage(
            canvas.toDataURL("Image/jpeg", 1.0),
            "JPEG",
            10,
            position,
            imgWidth,
            imgHeight
          );
          heightLeft -= pageHeight;
        }

        // pdf.addImage(
        //   canvas.toDataURL("Image/jpeg", 1.0),
        //   "JPEG",
        //   0,
        //   0,
        //   imgWidth,
        //   imgHeight
        // );
        pdf.save(pdfName + ".pdf");
        window.open(pdf.output("bloburl")); // to debug
      });

      //don't print ansything
      // pdf.html(contentHTML, {
      //   callback: function(pdfSave) {
      //     pdf.save(pdfName + ".pdf");
      //   }
      // });
    },

    convertNumberToWords(amount) {
      var words = new Array();
      words[0] = "";
      words[1] = "One";
      words[2] = "Two";
      words[3] = "Three";
      words[4] = "Four";
      words[5] = "Five";
      words[6] = "Six";
      words[7] = "Seven";
      words[8] = "Eight";
      words[9] = "Nine";
      words[10] = "Ten";
      words[11] = "Eleven";
      words[12] = "Twelve";
      words[13] = "Thirteen";
      words[14] = "Fourteen";
      words[15] = "Fifteen";
      words[16] = "Sixteen";
      words[17] = "Seventeen";
      words[18] = "Eighteen";
      words[19] = "Nineteen";
      words[20] = "Twenty";
      words[30] = "Thirty";
      words[40] = "Forty";
      words[50] = "Fifty";
      words[60] = "Sixty";
      words[70] = "Seventy";
      words[80] = "Eighty";
      words[90] = "Ninety";
      amount = amount.toString();
      var atemp = amount.split(".");
      var number = atemp[0].split(",").join("");
      var n_length = number.length;
      var words_string = "";
      if (n_length <= 9) {
        var n_array = new Array(0, 0, 0, 0, 0, 0, 0, 0, 0);
        var received_n_array = new Array();
        for (var i = 0; i < n_length; i++) {
          received_n_array[i] = number.substr(i, 1);
        }
        for (var i = 9 - n_length, j = 0; i < 9; i++, j++) {
          n_array[i] = received_n_array[j];
        }
        for (var i = 0, j = 1; i < 9; i++, j++) {
          if (i == 0 || i == 2 || i == 4 || i == 7) {
            if (n_array[i] == 1) {
              n_array[j] = 10 + parseInt(n_array[j]);
              n_array[i] = 0;
            }
          }
        }
        var value = "";
        for (var i = 0; i < 9; i++) {
          if (i == 0 || i == 2 || i == 4 || i == 7) {
            value = n_array[i] * 10;
          } else {
            value = n_array[i];
          }
          if (value != 0) {
            words_string += words[value] + " ";
          }
          if (
            (i == 1 && value != 0) ||
            (i == 0 && value != 0 && n_array[i + 1] == 0)
          ) {
            words_string += "Crores ";
          }
          if (
            (i == 3 && value != 0) ||
            (i == 2 && value != 0 && n_array[i + 1] == 0)
          ) {
            words_string += "Lakhs ";
          }
          if (
            (i == 5 && value != 0) ||
            (i == 4 && value != 0 && n_array[i + 1] == 0)
          ) {
            words_string += "Thousand ";
          }
          if (
            i == 6 &&
            value != 0 &&
            (n_array[i + 1] != 0 && n_array[i + 2] != 0)
          ) {
            words_string += "Hundred and ";
          } else if (i == 6 && value != 0) {
            words_string += "Hundred ";
          }
        }
        words_string = words_string.split("  ").join(" ");
      }
      return words_string;
    }
  }
};
</script>




