<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-3"></div>
      <div class="col-md-6 col-sm-12 text-center align-content-center pt-4 mt-4">
        <div class="card shadow">
          <div class="card-header font-weight-bold">WELCOME BACK, LOGIN HERE</div>
          <div class="card-body">
            <form class="form">
              <div class="form-row">
                <label for="email">
                  <i class="fas fa-envelope"></i>
                  <div class="d-inline font-weight-bold">Email</div>
                </label>
                <input
                  type="text"
                  class="form-control"
                  v-model="email"
                  placeholder="Enter the email provided by Doorstep"
                  required
                >
                <span class="text-danger" v-if="emailError">{{emailError}}</span>
              </div>
              <div class="form-row mt-3">
                <label for="password">
                  <i class="fas fa-unlock"></i>
                  <div class="d-inline font-weight-bold">Password</div>
                </label>
                <input
                  :type="passwordType"
                  class="form-control"
                  v-model="password"
                  placeholder="Enter the password provided by Doorstep"
                  data-toggle="password"
                  required
                >
                <i
                  class="fa fa-eye"
                  aria-hidden="true"
                  @click="showPassword"
                  v-if="passwordType=='password'"
                ></i>
                <i class="far fa-eye-slash" @click="showPassword" v-else></i>
                <span class="text-danger" v-if="passwordError">{{passwordError}}</span>
              </div>
              <div class="form-row mt-3">
                <button type="submit" @click.prevent="login" class="btn btn-success btn-sm">
                  <i class="fas fa-sign-in-alt pr-2"></i>
                  <div class="d-inline font-weight-bold">LOGIN</div>
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div class="col-md-3"></div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
export default {
  name: "login",
  data() {
    return {
      email: null,
      password: null,
      passwordType: "password",
      emailError: null,
      passwordError: null
    };
  },

  methods: {
    login(e) {
      if (this.validate()) {
        firebase
          .auth()
          .signInWithEmailAndPassword(this.email, this.password)
          .then(user => {
            this.$router.go({ path: this.$router.path });
          })
          .catch(err => {
            alert(err.message);
          });
      }

      e.preventDefault();
    },

    showPassword() {
      if (this.passwordType == "password") {
        this.passwordType = "text";
      } else {
        this.passwordType = "password";
      }
    },

    validate() {
      var emailPattern = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (emailPattern.test(this.email) && this.password) {
        return true;
      } else {
        this.emailError = null;
        this.passwordError = null;
        if (!this.email) {
          this.emailError = "Email Address Required";
        } else if (!emailPattern.test(this.email)) {
          this.emailError = "Invalid Email Address";
        }

        // password
        if (!this.password) {
          this.passwordError = "Password Required";
        }
        return false;
      }
    }
  }
};
</script>

<style>
label {
  font-size: 14px;
  opacity: 0.85;
  font-weight: 700;
  margin-top: 7px;
}
input::placeholder {
  font-size: 12px;
  opacity: 0.5;
  font-weight: 300;
  color: red;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>





