import Vue from "vue";
import Router from "vue-router";

//main dashboard
import MainDashBoard from "@/components/AdminPanel/Dashboard";

//about us
import Policies from "@/components/AboutUs/policies";

//coupons
import Coupons from "@/components/AdminPanel/Coupon/View";
import AddCoupon from "@/components/AdminPanel/Coupon/Add";
import EditCoupon from "@/components/AdminPanel/Coupon/Edit";
import RestrictionCoupon from "@/components/AdminPanel/Coupon/Restriction";
import TypeCoupon from "@/components/AdminPanel/Coupon/Type";
import ForTypeCoupon from "@/components/AdminPanel/Coupon/ForType";
import DiscountReturnTypeCoupon from "@/components/AdminPanel/Coupon/ReturnType";


//inventory
import CurrentInventory from "@/components/Inventory/currentInventory";
import AddItem from "@/components/Inventory/addItem";
import outOfStock from "@/components/Inventory/outOfStock";
import InactiveItems from "@/components/Inventory/InactiveItems";
import EditItem from "@/components/Inventory/editItem";
import Login from "@/components/login";
