import SuccessfulDelivery from "@/components/AdminPanel/Delivery/Successful";
import FailedDelivery from "@/components/AdminPanel/Delivery/Failed";
import ReturnPickupDelivery from "@/components/AdminPanel/Delivery/returnPickup";
import ReturnPickedDelivery from "@/components/AdminPanel/Delivery/returnPickedUp";
import ShippingDelivery from "@/components/AdminPanel/Delivery/Shipping";
import ReadyForDelivery from "@/components/AdminPanel/Delivery/ReadyForPickup";
import ViewDelivery from "@/components/AdminPanel/Delivery/SingleView";
import DeliveryGuy from "@/components/AdminPanel/Delivery/DeliveryGuy";

//orders
import AllOrder from "@/components/AdminPanel/Order/All";
import NewOrder from "@/components/AdminPanel/Order/New";
import PendingOrder from "@/components/AdminPanel/Order/Pending";
import ReturnOrder from "@/components/AdminPanel/Order/Return";
import CancelledOrder from "@/components/AdminPanel/Order/Cancelled";
import RequestedReturnOrder from "@/components/AdminPanel/Order/RequestedReturn";
import ReturnApprovedOrders from "@/components/AdminPanel/Order/ReturnApproved";
import SuccessfulOrder from "@/components/AdminPanel/Order/Successful";
import ShippingOrder from "@/components/AdminPanel/Order/Shipping";
import FailedOrder from "@/components/AdminPanel/Order/Failed";
import ViewOrder from "@/components/AdminPanel/Order/View";
import ConfirmedOrder from "@/components/AdminPanel/Order/Confirmed";
import OrderBill from "@/components/AdminPanel/Order/OrderSlips/OrderBill";
import OrderReturnSlip from "@/components/AdminPanel/Order/OrderSlips/OrderReturnSlip";
import OrderTrack from "@/components/AdminPanel/Order/TrackOrder";
import OrderPlace from "@/components/AdminPanel/Order/PlaceOrder/Dashboard";


//uploader
import Uploaders from "@/components/Uploaders/Uploaders";
import AddUploader from "@/components/Uploaders/AddUploader";
import ChangePackage from "@/components/Uploaders/ChangePackage";
import UploaderItems from "@/components/Uploaders/UploaderItems";
import UploaderTransaction from "@/components/Uploaders/Transaction";

//user
import UserFeedback from "@/components/AdminPanel/Users/ViewFeedbacks";


import UnAuthorized from "@/components/Errors/UnAuthorized";
import invalidUser from "@/components/Errors/invalidUser";

import store from "@/store/store";

import firebase from "firebase";

Vue.use(Router);

let router = new Router({
    routes: [{
            path: "/",
            name: "MainDashBoard",
            component: MainDashBoard,
            meta: {
                requiresAuth: true,
                title: 'DashBoard'
            },
        },

        //aboutUs
        {
            path: "/policy/:name",
            name: "Policies",
            component: Policies,
            meta: {
                title: "Fasdo Policies"
            },
        },

        //coupons
        {
            path: "/coupon/all",
            name: "Coupons",
            component: Coupons,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Coupons'
            },
        },
        {
            path: "/coupon/add",
            name: "AddCoupon",
            component: AddCoupon,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Add Coupon'
            },
        },
        {
            path: "/coupon/edit/:code",
            name: "editCoupon",
            component: EditCoupon,
            meta: {
                requiresAuth: true,
                requireAdmin: true,

                title: 'Edit Coupon'
            },
        },
        {
            path: "/coupon/restriction",
            name: "RestrictionCoupon",
            component: RestrictionCoupon,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Restriction Coupon'
            },
        },
        {
            path: "/coupon/type",
            name: "TypeCoupon",
            component: TypeCoupon,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Type Coupon'
            },
        },
        {
            path: "/coupon/for",
            name: "ForTypeCoupon",
            component: ForTypeCoupon,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'For Type Coupon'
            },
        },
        {
            path: "/coupon/discountReturn",
            name: "DiscountReturnTypes",
            component: DiscountReturnTypeCoupon,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Discount Return Type Coupon'
            },
        },

        //inventory
        {
            path: "/inventory/all",
            name: "current_inventory",
            component: CurrentInventory,
            meta: {
                requiresAuth: true,
                title: 'Current Inventory'
            },
        },
        {
            path: "/inventory/uploader/:uploaderId",
            name: "UploaderItems",
            component: UploaderItems,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Uploader Items'
            },
        },
        {
            path: "/login",
            name: "login",
            component: Login,
            meta: {
                requiresGuest: true,
                title: 'Login'
            },
        },
        {
            path: "/addItem",
            name: "addItem",
            component: AddItem,
            meta: {
                requiresAuth: true,
                title: 'Add Item'
            },
        },
        {
            path: "/editItem/:productId",
            name: "editItem",
            component: EditItem,
            meta: {
                requiresAuth: true,
                title: 'Edit Item'
            },
        },
        {
            path: "/inventory/outofstock",
            name: "outofstock",
            component: outOfStock,
            meta: {
                requiresAuth: true,
                title: 'Out of Stock Items'
            },
        },
        {
            path: "/inventory/inactive",
            name: "InactiveItems",
            component: InactiveItems,
            meta: {
                requiresAuth: true,
                title: 'Inactive Items'
            },
        },
        {
            path: "/category",
            name: "category",
            component: category,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Category'
            },
        },
        {
            path: "/subcategory",
            name: "subcategory",
            component: subcategory,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'SubCategory'
            },
        },
        {
            path: "/cocategory",
            name: "cocategory",
            component: cocategory,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'CoCategory'
            },
        },
        {
            path: "/inventory/brands",
            name: "inventoryBrands",
            component: itemBrands,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Brands'
            },
        },
        {
            path: "/inventory/images",
            name: "InventoryImages",
            component: InventoryImages,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Inventory Images'
            },
        },
        {
            path: "/inventory/search",
            name: "searchItem",
            component: searchItem,
            meta: {
                requiresAuth: true,
                requireAdmin: false,
                title: 'Search Item'
            },
        },
        //Delivery paths
        {
            path: "/delivery/add",
            name: "DeliveryGuy",
            component: DeliveryGuy,
            meta: {
                requiresAuth: true,
                requireDeliverer: true,
                title: 'Add Deliverer'
            },
        },
        {
            path: "/delivery/all",
            name: "AllDeliveries",
            component: AllDeliveries,
            meta: {
                requiresAuth: true,
                requireDeliverer: true,
                title: 'All Deliveries'
            },
        },
        {
            path: "/delivery/success",
            name: "SuccessfulDelivery",
            component: SuccessfulDelivery,
            meta: {
                requiresAuth: true,
                requireDeliverer: true,
                title: 'Successful Delivery'
            },
        },
        {
            path: "/delivery/ready",
            name: "ReadyForDelivery",
            component: ReadyForDelivery,
            meta: {
                requiresAuth: true,
                requireDeliverer: true,
                title: 'Ready For Delivery'
            },
        },
        {
            path: "/delivery/failed",
            name: "FailedDelivery",
            component: FailedDelivery,
            meta: {
                requiresAuth: true,
                requireDeliverer: true,
                title: 'Failed Delivery'
            },
        },
        {
            path: "/delivery/shipping",
            name: "ShippingDelivery",
            component: ShippingDelivery,
            meta: {
                requiresAuth: true,
                requireDeliverer: true,
                title: 'Out For Delivery'
            },
        },
        {
            path: "/delivery/return/pickup",
            name: "ReturnPickupDelivery",
            component: ReturnPickupDelivery,
            meta: {
                requiresAuth: true,
                requireDeliverer: true,
                title: 'Out For Delivery'
            },
        },
        {
            path: "/delivery/return/picked",
            name: "ReturnPickedDelivery",
            component: ReturnPickedDelivery,
            meta: {
                requiresAuth: true,
                requireDeliverer: true,
                title: 'Out For Delivery'
            },
        },
        {
            path: "/delivery/view/:deliveryId",
            name: "ViewDelivery",
            component: ViewDelivery,
            meta: {
                requiresAuth: true,
                requireDeliverer: true,
                title: 'Single Delivery'
            },
        },

        //order
        {
            path: "/order/all",
            name: "AllOrder",
            component: AllOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'All Orders'
            },
        },
        {
            path: "/order/new",
            name: "NewOrder",
            component: NewOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'New Orders'
            },
        },
        {
            path: "/order/pending",
            name: "PendingOrder",
            component: PendingOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Pending Orders'
            },
        },
        {
            path: "/order/return",
            name: "ReturnOrder",
            component: ReturnOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Returned Orders'
            },
        },
        {
            path: "/order/placed",
            name: "ConfirmedOrder",
            component: ConfirmedOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Confirmed Orders'
            },
        },
        {
            path: "/order/failed",
            name: "FailedOrder",
            component: FailedOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Failed Orders'
            },
        },
        {
            path: "/order/cancel",
            name: "CancelledOrder",
            component: CancelledOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Cancelled Orders'
            },
        },
        {
            path: "/order/request/return",
            name: "RequestedReturnOrder",
            component: RequestedReturnOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Requested Return Orders'
            },
        },
        {
            path: "/order/approved/return",
            name: "ReturnApprovedOrders",
            component: ReturnApprovedOrders,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Return Approved Orders'
            },
        },
        {
            path: "/order/shipping",
            name: "ShippingOrder",
            component: ShippingOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Shipping Orders'
            },
        },
        {
            path: "/order/success",
            name: "SuccessfulOrder",
            component: SuccessfulOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Successful Orders'
            },
        },
        {
            path: "/order/bill/:orderId",
            name: "OrderBill",
            component: OrderBill,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Order Bill'
            },
        },
        {
            path: "/order/returnslip/:orderId",
            name: "OrderReturnSlip",
            component: OrderReturnSlip,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Order ReturnSlip'
            },
        },
        {
            path: "/order/track",
            name: "OrderTrack",
            component: OrderTrack,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Track Order'
            },
        },
        {
            path: "/order/place",
            name: "OrderPlace",
            component: OrderPlace,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Place Order'
            },
        },
        {
            path: "/order/:orderId",
            name: "ViewOrder",
            component: ViewOrder,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Order'
            },
        },
        {
            path: "/uploader",
            name: "Uploaders",
            component: Uploaders,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Uploaders'
            },
        },
        {
            path: "/uploader/add",
            name: "AddUploader",
            component: AddUploader,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: 'Add Uploaders'
            },
        },
        {
            path: "/uploader/package",
            name: "ChangePackage",
            component: ChangePackage,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: "Change Package"
            },
        },
        {
            path: "/uploader/transaction/:uploadId",
            name: "UploaderTransaction",
            component: UploaderTransaction,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: "Uploader's Transaction"
            },
        }, {
            path: "/user/feedback",
            name: "UserFeedback",
            component: UserFeedback,
            meta: {
                requiresAuth: true,
                requireAdmin: true,
                title: "Users Feedback"
            },
        },


        //permission page
        {
            path: "/error/auth",
            name: "UnAuthorized",
            component: UnAuthorized,
            meta: {
                requiresAuth: true,
                title: "UnAuthorized"
            },
        },
        {
            path: "/error/user",
            name: "invalidUser",
            component: invalidUser,
            meta: {
                requiresAuth: true,
                title: "Invalid"
            },
        },
    ],
});

router.beforeEach((to, from, next) => {
    document.title = to.meta.title;
    if (to.matched.some(rec => rec.meta.requiresAuth)) {
        if (!firebase.auth().currentUser) {
            next({
                path: "/login",
                query: {
                    redirect: to.fullPath,
                },
            });
        } else {
            // check user
            store.dispatch("setUser", firebase.auth().currentUser);

            if (store.state.user) {
                if ( store.state.user.designation != "user" &&
                    store.state.user.status == "active"
                ) {
                    next();
                } else {

                    if (to.path != "/error/user") {
                        next({
                            path: "/error/user",
                            query: {
                                redirect: to.fullPath,
                            },
                        });
                    } else {
                        next();
                    }
                }
            } else {
                next();
            }
        }
    } else if (to.matched.some(rec => rec.meta.requiresGuest)) {
        if (firebase.auth().currentUser) {
            next({
                path: "/",
                query: {
                    redirect: to.fullPath,
                },
            });
        } else {
            next();
        }
    } else {
        next();
    }

    if (to.matched.some(rec => rec.meta.requireAdmin)) {
        if (store.state.user) {
            if (store.state.user.designation == "admin") {
                next();
            } else {
                next({
                    path: "/error/auth",
                    query: {
                        redirect: to.fullPath,
                    },
                });
            }
        } else {
            next();
        }
    }

    if (to.matched.some(rec => rec.meta.requireDeliverer)) {
        if (store.state.user) {
            if (store.state.user.designation == "admin" || store.state.user.designation == "deliverer") {
                next();
            } else {
                next({
                    path: "/error/auth",
                    query: {
                        redirect: to.fullPath,
                    },
                });
            }
        } else {
            next();
        }
    }

    //
});

export default router;

