<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-1"></div>
        <div class="col-lg-5 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>ADD NEW TYPE
          </h5>
          <div class="row">
            <div class="col-sm-12">
              <div class="row p-3">
                <div class="col-sm-12">
                  <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                    <i class="fas fa-exclamation-triangle pr-2"></i>Fields marked with ( * ) is important and is to be filled
                  </div>
                  <form class="form pb-5 mb-4">
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Name</label>
                        <input
                          type="text"
                          v-model="name"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[2]"
                        >{{errors[2]}}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Description</label>
                        <input
                          type="text"
                          v-model="description"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col text-center">
                        <button
                          class="btn btn-sm btn-go-green font-weight-bold d-inline"
                          type="submit"
                          @click.prevent="add"
                          :disabled="disable"
                        >
                          <i class="fas fa-save pr-1"></i>
                          SAVE
                        </button>
                        <router-link to="/inventory" class="btn btn-sm btn-secondary">
                          <i class="fas fa-ban pr-1"></i>
                          <div class="font-weight-bold d-inline">CANCEL</div>
                        </router-link>
                      </div>
                    </div>

                    <div class="form-row" v-if="errors[1]">
                      <div class="col text-center text-danger">{{errors[1]}}</div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-5 card shadow p-0 ml-2">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-list pl-1 pr-3"></i>Coupon TYPES
          </h5>
          <div class="row">
            <div class="col-sm-12">
              <div class="row p-3">
                <div class="col-sm-12">
                  <table>
                    <thead>
                      <tr>
                        <th>S.no</th>
                        <th>Name</th>
                        <th class="pl-2">Description</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(doc,index) in data" :key="doc.id">
                        <td>{{index+1}}</td>
                        <td>{{doc.name}}</td>
                        <td class="pl-2">{{doc.description}}</td>
                        <td class="pl-2">
                          <a
                            style="color:#424242;"
                            href="#"
                            title="delete this item"
                            @click.prevent="deleteData(index)"
                          >
                            <i class="fa fa-trash"></i>
                          </a>
                          <a
                            style="color:#424242;"
                            href="#"
                            title="delete this item"
                            @click.prevent="editData(index)"
                          >
                            <i class="fa fa-pencil-alt"></i>
                          </a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-1"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
import Vue from "vue";
import firebase from "firebase";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import Loader from "../../Loader";

import { firestorage } from "../../firebaseInit";

export default {
  name: "Types",
  components: {
    Loader
  },
  data() {
    return {
      id: 0,
      description: null,
      name: null,

      data: [],
      errors: [],
      edit: false,
      message: null,
      disable: false,
      disableMessage: null,
      success: false
    };
  },

  created() {
    this.fetchData();
  },

  methods: {
    add() {
      if (this.validate()) {
        this.disable = true;
        let url = domain + "coupon/type";

        if (this.edit == false) {
          axios
            .post(
              url,
              {
                name: this.name,
                description: this.description
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              this.description = null;
              this.name = null;
              this.id = 0;

              this.data.unshift(res.data);

              //// to show
              this.message = "Data Upload Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              console.log(err);
              //// to show
              this.message = "Data Upload Failed";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else {
          url = domain + "coupon/type/" + this.id;
          axios
            .post(
              url,
              {
                _method: "PUT",
                name: this.name,
                description: this.description
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              let late = {
                id: this.id,
                name: this.name,
                description: this.description
              };
              this.data.forEach((doc, index) => {
                if (doc.id == this.id) {
                  Vue.set(this.data, index, late);
                }
              });

              this.id = 0;
              this.description = null;
              this.name = null;

              this.edit = false;

              //// to show
              this.message = "Data Upload Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              console.log(err);

              this.message = "Data Upload Failed";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        }
      }
    },

    deleteData(index) {
      if (confirm("You are about to delete " + this.data[index].name)) {
        let url = domain + "coupon/type/" + this.data[index].id;
        axios
          .post(
            url,
            {
              _method: "delete"
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data) {
              this.data.splice(index, 1);
            }
          });
      }
    },

    editData(index) {
      if (this.data) {
        //it's editing not adding
        this.edit = true;
        this.disableId = true;
        //details
        this.id = this.data[index].id;
        this.name = this.data[index].name;
        this.description = this.data[index].description;
      }
    },

    fetchData() {
      let url = domain + "coupon/type";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            this.data.push(doc);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    goBack() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push("/inventory");
    },

    validate() {
      this.errors = [];
      if (this.name) {
        return true;
      } else {
        if (!this.name) {
          Vue.set(this.errors, 2, "Fill Name");
        }

        Vue.set(this.errors, 1, "Something is missing");
        return false;
      }
    }
  }
};
</script>

<style>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.modal-dialog {
  overflow-y: initial !important;
}
.modal-body {
  overflow-y: auto;
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>





