<template>
  <div>
    <div class="row grad">
      <div class="col-12 text-center">
        <div class="ds-heading">
          <h2 class="font-weight-bolder text-white">{{heading}}</h2>
          <span class="text-white">FasDo.</span>
        </div>
      </div>
    </div>
    <div class="row mt-4 mb-4">
      <div class="col-lg-2"></div>
      <div class="col-lg-8 col-md-12">
        <div v-if="editStatus">
          <ckeditor
            :editor="editor"
            v-model="editableContent"
            :config="editorConfig"
            class="form-control form-control-sm"
          ></ckeditor>
        </div>
        <div class="mt-2 mb-2 aboutUs" v-html="content" v-else></div>
      </div>
      <div class="col-lg-2" v-if="user && user.designation == 'admin'">
        <div class="float-right">
          <a @click="edit(true)" class="pr-2" v-if="!editStatus">
            <i class="fas fa-lg fa-pencil-alt"></i>
          </a>
          <a @click="save()" class="pr-2" v-if="editStatus">
            <i class="far fa-lg fa-save"></i>
          </a>
          <a @click="edit(false)" class="pr-2" v-if="editStatus">
            <i class="fas fa-window-close"></i>
          </a>
        </div>
      </div>
      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import store from "@/store/store";
import Loader from "@/components/Loader";
import firebase from "firebase";

export default {
  name: "Policies",

  components: {
    Loader
  },

  data() {
    return {
      heading: null,
      content: null,

      message: null,
      disable: false,
      success: false,

      editStatus: false,

      // ckeditor
      editor: ClassicEditor,
      editableContent: "<p></p>",
      editorConfig: {
        // The configuration of the editor.
      }
    };
  },

  beforeRouteEnter(to, from, next) {
    let url = domain + "aboutUs";
    axios
      .get(url, {
        params: {
          name: to.params.name
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          next(vm => {
            if (to.params.name == "termsAndCondition") {
              vm.heading = "Terms and Conditions";
            } else if (to.params.name == "privacyPolicy") {
              vm.heading = "Privacy Policy";
            } else if (to.params.name == "aboutUs") {
              vm.heading = "About US";
            }
            vm.content = doc;
            vm.editableContent = doc;
          });
        });
      })
      .catch(err => {
        console.log(err.message);
      });
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  watch: {
    $route: "fetchContent"
  },

  methods: {
    edit(status) {
      if (this.user.designation == "admin") {
        this.editStatus = status;
        if (status == true) {
          // this.editableContent = content;
        } else {
          this.editableContent = "<p></p>";
        }
      }
    },
    fetchContent() {
      if (this.$route.params.name == "termsAndCondition") {
        this.heading = "Terms and Conditions";
      } else if (this.$route.params.name == "privacyPolicy") {
        this.heading = "Privacy Policy";
      } else if (this.$route.params.name == "aboutUs") {
        this.heading = "About US";
      }

      let url = domain + "aboutUs";
      axios
        .get(url, {
          params: {
            name: this.$route.params.name
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            this.content = doc;
            this.editableContent = doc;
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    },

    save() {
      let url = domain + "aboutUs/" + this.$route.params.name;

      axios
        .post(
          url,
          {
            _method: "put",
            content: this.editableContent
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "Access-Control-Allow-Origin": AccessControlAllowOrigin,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          this.content = res.data.content;
          this.editStatus = false;
          this.editableContent = "<p></p>";
          /////
          /////
          //// to show
          this.message = "Data Update Success";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        })
        .catch(err => {
          this.editStatus = false;
          this.editableContent = "<p></p>";
          /// to show
          this.message = "Some Error Occured";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
          //});
        });
    }
  }
};
</script>

<style scoped>
.grad {
  background-image: linear-gradient(to right, #bdc3c7, #2c3e50);
}

.ds-heading {
  padding-top: 4.5em;
  padding-bottom: 4.5em;
}

.aboutUs h1,
.aboutUs h2,
.aboutUs h3,
.aboutUs h4,
.aboutUs h5,
.aboutUs h6 {
  font-size: 1.1 rem;
  font-weight: normal;
  color: #2c3e50;
}

.aboutUs strong {
  color: #606c71;
}

.aboutUs {
  font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.1 rem;
  color: #606c71;
}
</style>



