<template>
  <div>
    <Dashboard :products="items" :disable="loading" v-if="items"></Dashboard>

    <Footer @fetchItems="fetchItems" />
  </div>
</template>

<script>
import axios from "axios";
import db from "../firebaseInit";
import Dashboard from "./DashBoard.vue";
import Footer from "./Footer.vue";
import { bus } from "../../main";
import { domain } from "@/store/variable";
import store from "@/store/store";
import firebase from "firebase";

export default {
  name: "current_inventory",
  data() {
    return {
      items: [],
      loading: false,
      orderItems: "name",
      orderWay: "asc"
    };
  },

  components: {
    Footer,
    Dashboard
  },

  computed: {
    user() {
      return store.state.user;
    },

    size() {
      return store.state.inventoryPageSize;
    }
  },

  created() {
    bus.$on("filterItems", data => {
      this.order(data["On"], data["Way"], data["page"]);
    });

    bus.$on("fetchItems", data => {
      this.fetchItems(data["page"]);
    });
  },

  mounted() {
    setTimeout(() => {
      if (this.user) {
        this.fetchItems();
      }
    }, 4000);
  },

  methods: {
    fetchItems(page) {
      this.loading = true;
      this.items = [];

      // loading content
      let url = domain + "item";
      if (!page) {
        page = 1;
      }

      if (this.user.designation == "admin") {
        axios
          .get(url, {
            params: {
              page: page,
              limit: this.size,
              order: this.orderItems,
              orderWay: this.orderWay,
              stock: 0,
            category: "fetch"
            }
          })
          .then(res => {
            res.data.data.forEach(doc => {
              if (
                this.user.designation == "admin" ||
                this.user.uuid == doc.uploadedBy
              ) {
                var item = new Array();

                // image name
                let imageName = null;
                if (doc.image) {
                  let start = doc.image.indexOf("items%2F") + 8;
                  let end = doc.image.indexOf("?");
                  imageName = doc.image.substring(start, end);
                  imageName = this.truncate(imageName);
                }
                item = {
                  productId: doc.productId,
                  itemId: doc.itemId,
                  name: doc.name,
                  cat: doc.allcat,
                  stock: doc.stock,
                  unit: doc.unit,
                  mrp: doc.MRP,
                  currentPrice: doc.currentPrice,
                  discount: doc.discount,
                  image: doc.image,
                  imageName: imageName,
                  showImage: 0,
                  qty: doc.qty,
                  uploadedBy: doc.uploadedBy,
                  uploadedAt: doc.uploadedAt,

                  cocatName: doc.cocategory.name,
                  cocatId: doc.cocategory.coid,
                  subcatName: doc.cocategory.subcategory.name,
                  subcatId: doc.cocategory.subcategory.subcatid,
                  catName: doc.cocategory.subcategory.category.name
                };

                this.items.push(item);

                // getting co cat, subcat and cat
                // let Courl = domain + "cocat";
                // axios
                //   .get(Courl, {
                //     params: {
                //       item_id: item.itemId
                //     }
                //   })
                //   .then(crres => {
                //     crres.data.forEach(doc => {
                //       item["cocatName"] = doc.name;
                //       item["cocatId"] = doc.coid;

                //       let Suburl = domain + "subcat";
                //       axios
                //         .get(Suburl, {
                //           params: {
                //             coid: doc.coid
                //           }
                //         })
                //         .then(subres => {
                //           subres.data.forEach(sub => {
                //             item["subcatName"] = sub.name;
                //             item["subcatId"] = sub.subcatid;

                //             let Caturl = domain + "cat";
                //             axios
                //               .get(Caturl, {
                //                 params: {
                //                   subcatcode: sub.subcatid
                //                 }
                //               })
                //               .then(catres => {
                //                 catres.data.forEach(doc => {
                //                   item["catName"] = doc.name;
                //                 });
                //               });
                //           });
                //         });
                //     });
                //   });
              }
            });

            store.dispatch("patchinventoryPageCount", res.data.last_page);
            store.dispatch("patchinventoryPageNumber", res.data.current_page);
            store.dispatch("patchinventoryTotalItems", res.data.total);
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        axios
          .get(url, {
            params: {
              page: page,
              limit: this.size,
              order: this.orderItems,
              orderWay: this.orderWay,
              stock: 0,
              uploadedBy: firebase.auth().currentUser.uid
            }
          })
          .then(res => {
            res.data.data.forEach(doc => {
              if (
                this.user.designation == "admin" ||
                this.user.uuid == doc.uploadedBy
              ) {
                var item = new Array();

                // image name
                let imageName = null;
                if (doc.image) {
                  let start = doc.image.indexOf("items%2F") + 8;
                  let end = doc.image.indexOf("?");
                  imageName = doc.image.substring(start, end);
                  imageName = this.truncate(imageName);
                }
                item = {
                  productId: doc.productId,
                  itemId: doc.itemId,
                  name: doc.name,
                  cat: doc.allcat,
                  stock: doc.stock,
                  unit: doc.unit,
                  mrp: doc.MRP,
                  currentPrice: doc.currentPrice,
                  discount: doc.discount,
                  image: doc.image,
                  imageName: imageName,
                  showImage: 0,
                  qty: doc.qty,
                  uploadedBy: doc.uploadedBy,
                  uploadedAt: doc.uploadedAt,
                  cocatName: doc.cocategory.name,
                  cocatId: doc.cocategory.coid,
                  subcatName: doc.cocategory.subcategory.name,
                  subcatId: doc.cocategory.subcategory.subcatid,
                  catName: doc.cocategory.subcategory.category.name
                };

                this.items.push(item);

              }
            });

            store.dispatch("patchinventoryPageCount", res.data.last_page);
            store.dispatch("patchinventoryPageNumber", res.data.current_page);
            store.dispatch("patchinventoryTotalItems", res.data.total);
          })
          .catch(err => {
            console.log(err);
          });
      }

      this.loading = false;
    },

    // order(data) {
    //   console.log(data);
    //   this.$emit("order", { On: On, Way: Way });
    // },

    order(On, Way, page) {
      this.loading = true;
      //
      this.orderItems = On;
      this.orderWay = Way;

      if (On == "imageName") {
        this.orderItems = "image";
      }
      //else {
      //   this.items = _.orderBy(this.items, this.orderItems, this.orderWay);
      // }
      this.fetchItems(page);

      this.loading = false;
    },

    truncate(value) {
      const length = 10;
      return value.length <= length
        ? value
        : value.substring(0, length) + "...";
    }
  },

  sockets: {
    message(data) {
      console.log(data);
    }
  }
};
</script>




