<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-2"></div>
        <div class="col-lg-8 col-md-12 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>EDIT COUPON
          </h5>
          <div class="row p-3">
            <div class="col-sm-12">
              <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                <i class="fas fa-exclamation-triangle pr-2"></i>Fields marked with ( * ) is important and is to be filled
              </div>

              <form class="form pb-5 mb-4">
                <!-- name -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>Coupon Code*</label>
                    <input
                      type="text"
                      v-model="code"
                      class="form-control form-control-sm"
                      required
                      :disabled="disable"
                      data-length="10"
                    />
                    <span class="error text-danger font-weight-bold" v-if="errors[3]">{{errors[3]}}</span>
                  </div>
                </div>

                <!-- name, active -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>Name of the Coupon*</label>
                    <input
                      type="text"
                      v-model="name"
                      class="form-control form-control-sm"
                      required
                      :disabled="disable"
                      data-length="10"
                    />
                    <span class="error text-danger font-weight-bold" v-if="errors[2]">{{errors[2]}}</span>
                  </div>
                  <div class="col-sm-6">
                    <label>Active*</label>
                    <input type="checkbox" v-model="active" required :disabled="disable" />
                  </div>
                </div>

                <!-- decsription -->
                <div class="form-row">
                  <div class="col-sm-12">
                    <label>Description*</label>
                    <input
                      type="text"
                      v-model="description"
                      class="form-control form-control-sm"
                      required
                      :disabled="disable"
                      data-length="10"
                    />
                  </div>
                </div>

                <!-- type , for -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>Type*</label>
                    <select class="form-control" v-model="type">
                      <option v-for="data in types" :value="data.id" :key="data.id">{{data.name}}</option>
                    </select>
                    <span class="error text-danger font-weight-bold" v-if="errors[4]">{{errors[4]}}</span>
                  </div>

                  <div class="col-sm-6">
                    <label>For*</label>
                    <select class="form-control" v-model="forType">
                      <option v-for="data in forTypes" :value="data.id" :key="data.id">{{data.name}}</option>
                    </select>
                    <span class="error text-danger font-weight-bold" v-if="errors[5]">{{errors[5]}}</span>
                  </div>
                </div>

                <!-- uses -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>Uses*</label>
                    <input
                      type="number"
                      v-model.number="uses"
                      class="form-control form-control-sm"
                      required
                      :disabled="disable"
                      data-length="10"
                    />
                    <span class="error text-danger font-weight-bold" v-if="errors[6]">{{errors[6]}}</span>
                  </div>
                </div>

                <!-- max uses, max user uses -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>Max Uses*</label>
                    <input
                      type="number"
                      v-model.number="maxUses"
                      class="form-control form-control-sm"
                      required
                      :disabled="disable"
                      data-length="10"
                    />
                    <span class="error text-danger font-weight-bold" v-if="errors[7]">{{errors[7]}}</span>
                  </div>

                  <div class="col-sm-6">
                    <label>Max User Uses*</label>
                    <input
                      type="number"
                      v-model.number="maxUserUses"
                      class="form-control form-control-sm"
                      required
                      :disabled="disable"
                      data-length="10"
                    />
                    <span class="error text-danger font-weight-bold" v-if="errors[8]">{{errors[8]}}</span>
                  </div>
                </div>

                <!-- discount, -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>Discount Amount*</label>
                    <div class="d-flex">
                      <input
                        type="number"
                        v-model.number="discountAmount"
                        class="form-control form-control-sm mr-1"
                        required
                        :disabled="disable"
                        data-length="10"
                      />
                      <select class="form-control" v-model="isFixed" style="width:4em">
                        <option value="1">₹</option>
                        <option value="0">%</option>
                      </select>
                    </div>
                    <span class="error text-danger font-weight-bold" v-if="errors[9]">{{errors[9]}}</span>
                  </div>
                </div>

                <!-- return , restriction -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>Return type*</label>
                    <select class="form-control" v-model="returnType">
                      <option
                        v-for="data in discountReturnTypes"
                        :value="data.id"
                        :key="data.id"
                      >{{data.name}}</option>
                    </select>
                    <span class="error text-danger font-weight-bold" v-if="errors[10]">{{errors[7]}}</span>
                  </div>

                  <div class="col-sm-6">
                    <label>Restriction Type*</label>
                    <select class="form-control" v-model="restrictionType">
                      <option
                        v-for="data in restrictionTypes"
                        :value="data.id"
                        :key="data.id"
                      >{{data.name}}</option>
                    </select>
                    <span class="error text-danger font-weight-bold" v-if="errors[11]">{{errors[8]}}</span>
                  </div>
                </div>

                <!-- start expire at -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <datetime v-model="startsAt" type="datetime">
                      <label for="expiriesAt" slot="before">Starts At</label>
                      <small class="description" slot="before">(When cupoun is to added)</small>
                    </datetime>
                  </div>

                  <div class="col-sm-6">
                    <datetime v-model="expiriesAt" type="datetime">
                      <label for="expiriesAt" slot="before">Expires At</label>
                      <small class="description" slot="before">(When item was last updated)</small>
                    </datetime>
                  </div>
                </div>

                <!-- buttons -->
                <div class="form-row">
                  <div class="col text-center">
                    <button
                      class="btn btn-sm btn-go-green font-weight-bold d-inline"
                      type="submit"
                      @click.prevent="edit()"
                      :disabled="disable"
                    >
                      <i class="fas fa-save pr-1"></i>
                      SAVE
                    </button>
                    <button class="btn btn-sm btn-secondary" type="reset" :disabled="disable">
                      <i class="fas fa-ban pr-1"></i>
                      <div class="font-weight-bold d-inline">CANCEL</div>
                    </button>
                  </div>
                </div>

                <div class="form-row" v-if="errors[1]">
                  <div class="col text-center text-danger">{{errors[1]}}</div>
                </div>
              </form>
            </div>
          </div>
        </div>
        <div class="col-lg-2"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
import Vue from "vue";
import firebase from "firebase";
import { Datetime } from "vue-datetime";
import db from "../../firebaseInit";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import Loader from "../../Loader";

export default {
  name: "editCoupon",
  components: {
    Loader,
    datetime: Datetime
  },
  data() {
    return {
      code: null,
      name: null,
      description: null,
      type: null,
      forType: null,
      uses: 0,
      maxUses: 0,
      maxUserUses: 0,
      active: true,
      isFixed: 0,
      discountAmount: 0,
      returnType: null,
      restrictionType: null,
      restriction: [],
      startsAt: null,
      expiriesAt: null,

      errors: [],
      forTypes: [],
      types: [],
      restrictionTypes: [],
      discountReturnTypes: [],

      message: null,
      success: false,
      disable: false
    };
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  mounted() {
    this.getTypes();
    this.getRestrictionTypes();
    this.getDiscountReturnTypes();
    this.getForTypes();
  },

  beforeRouteEnter(to, from, next) {
    let url = domain + "coupon";
    axios
      .get(url, {
        params: {
          code: to.params.code
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          next(vm => {
            vm.code = doc.code;
            vm.description = doc.description;
            vm.type = doc.type;
            vm.name = doc.name;
            vm.forType = doc.for;
            vm.uses = doc.uses;
            vm.maxUses = doc.maxUses;
            vm.maxUserUses = doc.maxUserUses;
            vm.isFixed = doc.isFixed;
            vm.discountAmount = doc.discountAmount;
            vm.returnType = doc.returnType;
            vm.restrictionType = doc.restrictionType;
            vm.restriction = doc.restriction;
            vm.startsAt = doc.startsAt; // doubt
            vm.expiriesAt = doc.expiriesAt;

            if (doc.active == 1) {
              vm.active = true;
            } else {
              vm.active = false;
            }
          });
        });
      });
  },
  beforeRouteLeave(to, from, next) {
    if (this.name) {
      if (confirm("Your changes will be lost.Do you want to leave the page?")) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },

  watch: {
    $route: "fetchData"
  },

  methods: {
    edit() {
      if (this.validate() && this.user.designation == "admin") {
        this.disable = true;

        let url = domain + "coupon/" + this.code;
        if (this.expiriesAt) {
          var expiry = new Date(this.expiriesAt);
          var formatExpiry =
            expiry.getFullYear() +
            "-" +
            expiry.getMonth() +
            "-" +
            expiry.getDate() +
            " " +
            expiry.getHours() +
            ":" +
            expiry.getMinutes() +
            ":" +
            expiry.getSeconds();
        } else {
          let formatExpiry = null;
        }

        if (this.startsAt) {
          var start = new Date(this.startsAt);
          var formatStart =
            start.getFullYear() +
            "-" +
            start.getMonth() +
            "-" +
            start.getDate() +
            " " +
            start.getHours() +
            ":" +
            start.getMinutes() +
            ":" +
            start.getSeconds();
        } else {
          let formatStart = null;
        }

        axios
          .post(
            url,
            {
              _method: "PUT",
              name: this.name,
              description: this.description,
              type: this.type,
              for: this.forType,
              uses: this.uses,
              maxUses: this.maxUses,
              maxUserUses: this.maxUserUses,
              discountAmount: this.discountAmount,
              active: this.active,
              isFixed: this.isFixed,
              returnType: this.returnType,
              restrictionType: this.restrictionType,
              restriction: this.restriction,
              startsAt: formatStart,
              expiresAt: formatExpiry
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            //// to show
            if (res.data == "Success") {
              this.message = "Data Upload Success";
            } else {
              this.message = "Some error occured";
            }
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          })
          .catch(err => {
            /// to show
            this.message = "Data Upload Failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },

    fetchData() {
      let url = domain + "coupon";
      axios
        .get(url, {
          params: {
            code: this.$route.params.code
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            this.code = doc.code;
            this.description = doc.description;
            this.type = doc.type;
            this.name = doc.name;
            this.forType = doc.for;
            this.uses = doc.uses;
            this.maxUses = doc.maxUses;
            this.maxUserUses = doc.maxUserUses;
            this.isFixed = doc.isFixed;
            this.discountAmount = doc.discountAmount;
            this.returnType = doc.returnType;
            this.restrictionType = doc.restrictionType;
            this.restriction = doc.restriction;
            this.startsAt = new Date(doc.startsAt); // doubt
            if (this.expiriesAt) {
              this.expiriesAt = new Date(doc.expiriesAt);
            } else {
              this.expiriesAt = null;
            }

            if (doc.active == 1) {
              this.active = true;
            } else {
              this.active = false;
            }
          });
        });
    },

    getTypes() {
      let url = domain + "coupon/type";
      this.types = [];
      axios.get(url).then(res => {
        res.data.forEach(doc => {
          this.types.push(doc);
        });
      });
    },

    getRestrictionTypes() {
      let url = domain + "coupon/restriction";
      this.restrictionTypes = [];
      axios.get(url).then(res => {
        res.data.forEach(doc => {
          this.restrictionTypes.push(doc);
        });
      });
    },

    getDiscountReturnTypes() {
      let url = domain + "coupon/discountReturn";
      this.discountReturnTypes = [];
      axios.get(url).then(res => {
        res.data.forEach(doc => {
          this.discountReturnTypes.push(doc);
        });
      });
    },

    getForTypes() {
      let url = domain + "coupon/for";
      this.forTypes = [];
      axios.get(url).then(res => {
        res.data.forEach(doc => {
          this.forTypes.push(doc);
        });
      });
    },

    validate() {
      this.errors = [];
      if (
        this.code &&
        this.name &&
        this.forType &&
        this.type &&
        this.uses >= 0 &&
        this.maxUses >= 0 &&
        this.maxUserUses >= 0 &&
        this.discountAmount >= 0
      ) {
        return true;
      } else {
        Vue.set(
          this.errors,
          1,
          "Something is missing!! Check out what you left"
        );
        if (!this.name) {
          Vue.set(this.errors, 2, "Fill in the name");
        }

        if (!this.code) {
          Vue.set(this.errors, 3, "Fill valid Coupon Code");
        }

        if (!this.type) {
          Vue.set(this.errors, 4, "Fill valid type");
        }

        if (!this.forType) {
          Vue.set(this.errors, 5, "Fill valid for type");
        }

        if (this.uses < 0) {
          Vue.set(this.errors, 6, "Fill valid value");
        }

        if (this.maxUses < 0) {
          Vue.set(this.errors, 7, "Fill valid value");
        }

        if (this.maxUserUses < 0) {
          Vue.set(this.errors, 8, "Fill valid value");
        }

        if (this.discountAmount < 0) {
          Vue.set(this.errors, 9, "Fill valid value");
        }

        if (this.returnType < 0) {
          Vue.set(this.errors, 10, "Fill valid value");
        }

        if (this.restrictionType < 0) {
          Vue.set(this.errors, 11, "Fill valid value");
        }

        return false;
      }
    }
  }
};
</script>

<style scoped>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>





