<template>
  <nav id="usersettingSidebar" class="collapse d-md-block shadow-sm">
    <div id="settingSidebarBoard">
      <ul class="list-unstyled components" v-if="isLoggedIn">
        <li class="settingSidebar-list-item nav-item p-2 setting-heading">
          <div class="nav-link settingSidebar-link">
            <span>Settings</span>
            <span class="float-right" @click="switchClass">X</span>
          </div>
        </li>

        <!-- user -->
        <li class="settingSidebar-list-item nav-item p-2 mt-4 text-center">
          <div>
            <img
              class="card-img-top rounded-circle"
              src="https://api.adorable.io/avatars/182/abott@adorable.png"
              alt="Image"
              style="width:5em;"
            >
            <div class="card-body small">
              <h5 class="card-title mb-0">{{user.name}}</h5>
              <p class="card-text">{{user.email}}</p>
            </div>
          </div>
        </li>
        <hr>
      </ul>
    </div>
  </nav>
</template>

<script>
import axios from "axios";
import db from "./firebaseInit";
import firebase from "firebase";
import { domain } from "@/store/variable";
import store from "@/store/store";
import { bus } from "../main";

export default {
  name: "Setting",
  data() {
    return {
      isLoggedIn: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
    }
  },
  computed: {
    user() {
      return store.state.user;
    }
  },

  methods: {
    switchClass() {
      bus.$emit("switchClass");
    }
  }
};
</script>


<style scoped>
#settingSidebarBoard {
  overflow-y: auto;
  height: 100vh;
  white-space: nowrap;
  padding-bottom: 100px;
}

.settingSidebar-icons {
  width: 28px;
  fill: #98c564;
}

#usersettingSidebar {
  z-index: 999;
  min-width: 270px;
  max-width: 270px;
  min-height: 100vh;
  right: 0;
  position: fixed;
  transition: all 0.5s ease-in-out;
  background-color: #ffffff;
  box-shadow: 0 3 6px #000000;
}

.setting-heading {
  background-color: #f2f2f2;
}

.setting-heading span {
  font-weight: 700;
  color: #1f241f;
}

.setting-heading span:active {
  color: #1f241f;
}
</style>



