<template>
  <div class="container-fluid">
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <h4>FAILED ORDERS</h4>
      </div>
      <div class="col-6 content-header small">
        <span>doorstep > orders > failed</span>
      </div>
    </div>

    <DashBoard :newOrders="orders" :disable="disable" :status="status" @fetchOrders="fetchOrders" />
  </div>
</template>
<script>
import DashBoard from "./DashBoard.vue";
import axios from "axios";
import Vue from "vue";
import db from "../../firebaseInit";
import firebase from "firebase";
import { firestorage } from "../../firebaseInit";
import { domain } from "@/store/variable";
import { utcHours } from "@/store/variable";
import { utcMins } from "@/store/variable";
import store from "@/store/store";

export default {
  name: "FailedOrders",
  components: {
    DashBoard
  },

  data() {
    return {
      status: 8,
      orders: [],
      disable: false
    };
  },

  mounted() {
    this.fetchOrders();
  },

  computed: {
    size() {
      return store.state.orderPageSize;
    }
  },

  methods: {
    fetchOrders(page) {
      let url = domain + "order";
      if (!page) {
        page = 1;
      }
      this.disable = true;
      axios
        .get(url, {
          params: {
            status: this.status,
            user: "budhbak",
            products: "budhbak",
            payment_mode: "budhbak",
            userAddress: "budhbak",
            order: "updated_at",
            orderWay: "desc",
            page: page,
            limit: this.size
          }
        })
        .then(res => {
          let ordersList = [];
          res.data.data.forEach(doc => {
            let dateTime = new Date(doc.orderTime);
            dateTime.setHours(dateTime.getHours() + utcHours);
            dateTime.setMinutes(dateTime.getMinutes() + utcMins);
            let date =
              dateTime.getDate() +
              "/" +
              dateTime.getMonth() +
              "/" +
              dateTime.getFullYear();
            var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
            var hours = dateTime.getHours() % 12 || 12;
            let time =
              hours +
              ":" +
              dateTime.getMinutes() +
              ":" +
              dateTime.getSeconds() +
              " " +
              AmOrPm;

            let order = {
              orderId: doc.id,
              products: doc.products,
              orderedItemsCount: doc.orderedItemsCount,
              paymentMode: doc.payment_mode,
              orderDate: date,
              orderTime: time,
              payment: doc.totalPayableAmount,
              paidAmount: doc.paidAmount,
              orderStatus: doc.order_status,
              deliveryAddress: doc.user_address,
              user: doc.user,
              refundId: doc.refundId,

              checked: false
            };
            ordersList.push(order);

            store.dispatch("patchorderPageCount", res.data.last_page);
            store.dispatch("patchorderPageNumber", res.data.current_page);
            store.dispatch("patchtotalOrders", res.data.total);
          });

          this.orders = ordersList;
        });

      this.disable = false;
    }
  }
};
</script>


<style src="@/assets/css/tableContent.css" scoped>
</style>



