//import DeliveryView from "./SingleView.vue";
import axios from "axios";
import Vue from "vue";
import db from "../firebaseInit";
import firebase from "firebase";
import { domain } from "@/store/variable";
import store from "@/store/store";
export default {
  name: "Uploaders",

  data() {
    return {
      uploaders: [],
      editUploader: [],
      loading: false,

      //search
      search: null,
      searchArray: [],

      select: false,
      action: null
    };
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  mounted() {
    this.fetchuploaders();
  },

  methods: {
    banUploader(index, packIndex, status) {
      if (this.user.designation == "admin") {
        this.loading = true;
        let url =
          domain +
          "user/uploader/" +
          this.uploaders[index].package[packIndex].id;
        axios
          .post(
            url,
            {
              _method: "put",
              status: status
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Origin": "*",
                "X-Requested-With": "XMLHttpRequest",
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            // updating data
            Vue.set(
              this.uploaders[index].package[packIndex],
              "status",
              res.data.status
            );
            this.loading = false;
          })
          .catch(err => {
            console.log(err.message);
          });
      }
    },

    fetchuploaders() {
      let url = domain + "user";

      this.loading = true;
      axios
        .get(url, {
          params: {
            designation: "webappUploader",
            package: "kuchLaiye",
            transactions: "kuchLaiye"
          }
        })
        .then(res => {
          let uploadersList = [];
          res.data.forEach(doc => {
            let totalUploads = 0;
            let totalPay = 0;
            let amountPaid = 0;
            let amountDue = 0;

            // package ka detail nikal liya
            doc.package.forEach(element => {
              totalUploads = totalUploads + Number(element.totalUploads);
              if (element.package == 3) {
                totalPay = totalPay + 3 * Number(element.totalUploads);
              } else {
                totalPay = totalPay + Number(element.totalUploads);
              }
            });

            //trnasaction ka nikalo
            doc.upload_transactions.forEach(element => {
              amountPaid = amountPaid + Number(element.amount);
            });

            amountDue = totalPay - amountPaid;

            let uploader = {
              id: doc.uuid,
              uploaderId: doc.userId,
              name: doc.name,
              email: doc.email,
              package: doc.package,
              totalPay: totalPay,
              totalUploads: totalUploads,
              transactions: doc.upload_transactions.length,
              amountPaid: amountPaid,
              amountDue: amountDue,

              checked: false
            };

            uploadersList.push(uploader);
          });

          this.uploaders = uploadersList;

          //array for search
          this.searchArray = this.uploaders;

          // for edit creating array
          var Anarray = [];
          for (var i = 0; i < this.uploaders.length; i++) {
            Anarray.push({
              //paymentMode: false,
              amountPaid: false
            });
          }
          this.editUploader = Anarray;
        });

      this.loading = false;
    },

    deleteUploader(index, packIndex) {
      if (this.user.designation == "admin") {
        //only admin can delete
        if (
          confirm(
            "You are about to delete " +
              this.uploaders[index].name +
              " from the records"
          )
        ) {
          let url =
            domain +
            "user/uploader/" +
            this.uploaders[index].package[packIndex].id;
          this.loading = true;

          axios
            .post(
              url,
              {
                _method: "delete",
                uploaderId: this.uploaders[index].package[packIndex].uploaderId
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": "application/json",
                  "Access-Control-Allow-Origin": "*",
                  "X-Requested-With": "XMLHttpRequest",
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              if (res.data == "done") {
                this.uploaders[index].package.splice(packIndex, 1);
              }
            })
            .catch(err => {
              console.log(err.message);
            });

          this.loading = false;
        }
      }
    },

    // edit(index, key) {
    //   let url = domain + "user/uploader/" + this.uploaders[index].id;
    //   //else if (key == "paymentMode") {
    //   //   axios
    //   //     .post(
    //   //       url,
    //   //       {
    //   //         _method: "put",
    //   //         paymentMode: this.uploaders[index].paymentMode
    //   //       },
    //   //       {
    //   //         withCredentials: true,
    //   //         headers: {
    //   //           "Content-Type": "application/json",
    //   //           "Access-Control-Allow-Origin": "*",
    //   //           "X-Requested-With": "XMLHttpRequest",
    //   //           "X-api-token": firebase.auth().currentUser.uid
    //   //         }
    //   //       }
    //   //     )
    //   //     .then(res => {
    //   //       let doc = res.data;
    //   //       let total = doc.totalUploads * doc.package;
    //   //       let payable = total - doc.amountPaid;
    //   //       // updating data
    //   //       Vue.set(this.uploaders[index], "amountDue", payable);
    //   //       Vue.set(this.uploaders[index], "amountPaid", doc.amountPaid);
    //   //       Vue.set(this.uploaders[index], "totalPay", total);
    //   //       Vue.set(this.uploaders[index], "paymentMode", doc.paymentMode);

    //   //       //colsing inputs
    //   //       Vue.set(this.editUploader[index], "paymentMode", false);
    //   //       this.loading = false;
    //   //     })
    //   //     .catch(err => {
    //   //       console.log(err.message);
    //   //     });
    //   // }
    //   else {
    //     console.log("Sorry " + key + "is not allowed to update");
    //   }
    // },

    // updateAction() {
    //   this.uploaders.forEach(uploader => {
    //     if (uploader.checked) {
    //       let url = domain + "user/uploader/" + uploader.id;
    //       axios
    //         .post(
    //           url,
    //           {
    //             _method: "put",
    //             amountPaid: uploader.totalPay
    //           },
    //           {
    //             withCredentials: true,
    //             headers: {
    //               "Content-Type": "application/json",
    //               "Access-Control-Allow-Origin": "*",
    //               "X-Requested-With": "XMLHttpRequest",
    //               "X-api-token": firebase.auth().currentUser.uid
    //             }
    //           }
    //         )
    //         .then(res => {
    //           let doc = res.data;
    //           let total = doc.totalUploads * doc.package;
    //           let payable = total - doc.amountPaid;

    //           // updating data
    //           uploader.amountDue = payable;
    //           uploader.amountPaid = doc.amountPaid;
    //           uploader.totalPay = total;
    //           uploader.checked = false;

    //           //colsing inputs
    //           this.loading = false;
    //         })
    //         .catch(err => {
    //           console.log(err.message);
    //         });
    //     }
    //   });
    // },

    searching() {
      if (this.search) {
        this.uploaders = this.searchArray.filter(
          v => v.name.toLowerCase().indexOf(this.search.toLowerCase()) != -1
        );
      } else {
        this.uploaders = this.searchArray;
      }
    },

    selectAll() {
      this.uploaders.forEach(uploader => {
        console.log(this.select);
        if (!this.select) {
          uploader.checked = true;
          //Vue.set(uploader, checked, true);
        } else {
          uploader.checked = false;
        }
      });
    }
  }
};
</script>


<style src="@/assets/css/tableContent.css" scoped>
</style>



