<template>
  <ol class="progress-tracker">
    <!-- return -->
    <li class="step active" v-for="status in statuses">
      <span class="step-name">{{status.status}}</span>
    </li>
  </ol>
</template>

<script>
export default {
  name: "orderStatus",

  props: {
    statuses: Array
  },

  data() {
    return {
      //code
    };
  },
};
</script>

<style>
/* PROGRESS TRACKER */
.progress-tracker {
  display: block;
  margin: 0;
  counter-reset: item;
  list-style-type: none;
  overflow: auto;
  position: relative;
  width: 100%;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.075em;
}
.progress-tracker .step:before {
  background: rgb(248, 160, 97);
  border-radius: 40px;
  color: #ffffff;
  display: list-item;
  text-align: center;
  line-height: 1.8em;
  padding: 0 0 0 13px;
  position: absolute;
  width: 35px;
  height: 35px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  content: counter(item) "  ";
  counter-increment: item;
}
.progress-tracker .step {
  display: block;
  float: left;
  list-style-position: inside;
  width: 20%;
}
.progress-tracker .step .step-name {
  border-top: 8px solid rgb(248, 160, 97);
  display: block;
  margin: 15px 0;
  padding: 20px 0;
}
/*last step*/
.progress-tracker .step:last-child {
  width: auto;
}
.progress-tracker .step:last-child .step-name {
  border-color: transparent;
}

/*active step*/
.progress-tracker .step.active .step-name {
  color: rgb(255, 106, 2);
}
.progress-tracker .step.active:before {
  background: rgb(248, 160, 97);
}
</style>



