                              >{{item[selectedAvailbilityOfItem[index]].name}}</h6>
                              <span class="media-heading">
                                by
                                <span
                                  href="#"
                                >{{item[selectedAvailbilityOfItem[index]].item_brand.name}}</span>
                              </span>
                              <span
                                v-if="item[selectedAvailbilityOfItem[index]].stock>0"
