                            </div>
                          </div>
                        </td>
                        <td class="col-sm-1 col-md-1" style="text-align: center">
                          <div class="d-flex">
                            <button
                              type="button"
                              class="btn btn-sm btn-outline-success"
                              @click.prevent="changeQuantity('+',index, 'items')"
                            >+</button>
                            <span
