                            @click.prevent="selectSize(index,sizeIndex)"
                          >
                            <small>{{size.qty}}{{size.unit}}</small>
                          </button>
                        </td>
                        <td class="col-sm-1 col-md-1 text-center">
                          <span>₹{{item[selectedAvailbilityOfItem[index]].currentPrice}}</span>
                        </td>
                        <td class="col-sm-1 col-md-1 text-center">
                          <strong>₹{{item[selectedAvailbilityOfItem[index]].quantityToBuy*item[selectedAvailbilityOfItem[index]].currentPrice}}</strong>
                        </td>
                        <td class="col-sm-1 col-md-1" style="position:relative">
                          <button
                            type="button"
                            class="btn btn-outline-danger"
                            @click.prevent="addToCart(index)"
                          >
                            <i
                              class="fas fa-edit"
                              v-if="item[selectedAvailbilityOfItem[index]].inCart==true"
                            ></i>
                            <i class="fas fa-cart-plus" v-else></i>
                          </button>

                          <span class="pr-2" style="position:absolute; bottom:1em; width: 2.15em;">
                            <img
                              src="../../../../assets/shopping-cart-green.svg"
                              class="img-fluid"
                              v-if="item[selectedAvailbilityOfItem[index]].inCart==true"
                            />
                          </span>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <div class="mt-3 text-center" v-else>
                  <!-- searching -->
                  <div class="mt-4" v-if="loading==true && searchInput">
                    <img
                      src="https://cdn.iconscout.com/icon/premium/png-256-thumb/searching-item-905087.png"
                      class="img-fluid"
                    />
                    <h6 class="mt-4 text-dark">Searching</h6>
                  </div>
                  <!-- no items -->
                  <div class="mt-4" v-else-if="searchInput">
                    <img
                      src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTPCJBtquhbvbrpVCkaga1GYXyd2CIpZQRe6dQfQj_lmKkZBGpu9A"
                      class="img-fluid"
                    />
                    <h6 class="mt-4 text-dark">No item Found</h6>
                  </div>
                  <!-- search something -->
                  <div class="mt-4" v-else>
                    <img
                      src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQg344AjZp9BxSiQD_GY08b5YiW2Fb8YM8SY3RMz5d7a3gtP6q-AA"
                      class="img-fluid"
                    />
                    <h6 class="mt-4 text-dark">Searched Something and place order</h6>
                  </div>
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>
            <!-- cart -->
            <div class="row collapse multi-collapse" id="collapseCart">
              <div class="col-sm-12 col-md-8" v-if="cart.length>0">
                <div class="row">
                  <div class="col d-flex justify-content-start h-100">
                    <div>
                      <h5 v-if="cart.length==0">Cart</h5>
                      <button
                        type="button"
                        class="btn btn-success btn-sm"
                        v-else
                        @click.prevent="placeOrder()"
                      >Place Order</button>
                    </div>
                  </div>
                </div>
                <div class="row">
                  <div class="col d-flex justify-content-end h-100">
                    <div>
                      <table class="table table-hover">
                        <thead>
                          <tr>
                            <th>Product</th>
                            <th>Quantity</th>
                            <th class="text-center">Price</th>
                            <th class="text-center">Total</th>
                            <th></th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="(item,index) in cart" :key="item.productId">
                            <td class="col-sm-8 col-md-6">
                              <div class="media">
                                <a class="thumbnail pull-left" href="#">
                                  <img
                                    class="media-object"
                                    :src="item.image"
                                    style="width: 72px; height: 72px;"
                                  />
                                </a>
                                <div class="media-body pl-2">
                                  <h6 class="media-heading">{{item.name}}</h6>
                                  <span class="media-heading">
                                    by
                                    <span href="#">{{item.item_brand.name}}</span>
                                  </span>
                                  <span
                                    v-if="item.stock>0"
                                    class="text-success float-right"
                                  >{{item.stock-item.quantityToBuy}} In Stock</span>
                                  <span v-else class="text-danger float-right">Out of Stock</span>
                                </div>
                              </div>
                            </td>
                            <td class="col-sm-1 col-md-1" style="text-align: center">
                              <div class="d-flex">
                                <button
                                  type="button"
                                  class="btn btn-sm btn-outline-success"
                                  @click.prevent="changeQuantity('+',index, 'cart')"
                                >+</button>
                                <span class="pr-1 pl-1">{{item.quantityToBuy}}</span>

                                <button
                                  type="button"
                                  class="btn btn-sm btn-outline-success"
                                  @click.prevent="changeQuantity('-',index, 'cart')"
                                >-</button>
                              </div>
                            </td>
                            <td class="col-sm-1 col-md-1 text-center">
                              <span>₹{{item.currentPrice}}</span>
                            </td>
                            <td class="col-sm-1 col-md-1 text-center">
                              <strong>₹{{item.quantityToBuy*item.currentPrice}}</strong>
                            </td>
                            <td class="col-sm-1 col-md-1">
                              <button
                                type="button"
                                class="btn btn-outline-danger"
                                @click.prevent="removeFromCart(index)"
                              >
                                <i class="far fa-trash-alt"></i>
                              </button>
                            </td>
                          </tr>
                          <tr>
                            <td class="thick-line"></td>
                            <td class="thick-line"></td>
                            <td class="thick-line"></td>
                            <td class="no-line text-center">
                              <strong>SubTotal</strong>
                            </td>
                            <td class="no-line text-right">₹{{subTotal}}</td>
                          </tr>
                          <tr>
                            <td class="thick-line"></td>
                            <td class="thick-line"></td>
                            <td class="thick-line"></td>
                            <td class="no-line text-center">
                              <strong>Delivery Charges</strong>
                            </td>
                            <td class="no-line text-right">₹{{shippingCharges}}</td>
                          </tr>
                          <tr>
                            <td class="thick-line"></td>
                            <td class="thick-line"></td>
                            <td class="thick-line"></td>
                            <td class="no-line text-center">
                              <strong>Total</strong>
                            </td>
                            <td class="no-line text-right">₹{{totalPayableAmount}}</td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-sm-12 col-md-8 mt-3 text-center" v-else>
                <img
                  src="https://youreats.co.uk/public/front/images/empty-cart.png"
                  class="img-fluid"
                />
                <div class="mt-4">
                  <h6 class="text-dark">Cart Empty</h6>
                </div>
              </div>
              <div class="col-md-4">
                <cartRightPanel ref="cartRightPanel" v-if="cart.length>0"></cartRightPanel>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Loader :disable="loading" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Vue from "vue";
import _ from "lodash";
import $ from "jquery";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith,
  thresholdShippingCost
} from "@/store/variable";
import store from "@/store/store";
import cartRightPanel from "./cartRightPanel.vue";
import Loader from "@/components/Loader";
import firebase from "firebase";

export default {
  name: "PlaceOrderDashboard",

  components: {
    cartRightPanel,
    Loader
  },

  data() {
    return {
      searchInput: null,
      loading: false,
      itemsOrCart: true,
      items: [],
      selectedAvailbilityOfItem: [],
      cart: [],
      subTotal: 0,
      totalPayableAmount: 0,
      shippingCharges: 0,

      success: false,
      message: null
    };
  },

  created() {
    window.addEventListener("beforeunload", function(event) {
      event.returnValue =
        "You have some items in cart. Leaving might loose them.Wanna Stay?";
    });
  },

  beforeRouteLeave(to, from, next) {
    if (this.cart.length > 0) {
      if (
        confirm(
          "You have some items in cart. Leaving might loose them.Wanna Leave?"
        )
      ) {
        next();
      } else {
        next(false);
      }
    } else {
      next(false);
    }
  },

  methods: {
    addOrder(ordererId, deliveryAddress) {
      let orderedItems = null;
      let quantity = "";

      this.cart.forEach((item, index) => {
        if (orderedItems == null) {
          orderedItems = item.productId;
        } else {
          orderedItems = orderedItems + "," + item.productId;
        }

        if (quantity == null || quantity == "") {
          quantity = item.quantityToBuy;
        } else {
          quantity = quantity + "," + item.quantityToBuy;
        }
      });

      let url = domain + "order";
      axios
        .post(
          url,
          {
            userId: ordererId,
            orderedItems: orderedItems,
            quantity: quantity,
            paidAmount: 0,
            paymentMode: this.$refs.cartRightPanel.paymentMode,
            deliveryAddress: deliveryAddress,
            placedFrom: this.$refs.cartRightPanel.placedFrom,
            transationId: null
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "Access-Control-Allow-Origin": AccessControlAllowOrigin,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          if (res.data) {
            //order placed ka message

            this.items = [];
            this.subTotal = 0;
            this.totalPayableAmount = 0;
            this.shippingCharges = 0;
            this.$refs.cartRightPanel.deliveryAddress = 0;
            this.$refs.cartRightPanel.ordererId = null;
            this.$refs.cartRightPanel.ordererFound = false;
            this.$refs.cartRightPanel.deliveryAddress = [];

            this.message = "Order has been Placed successfully";
            this.success = true;
            this.loading = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          }
        })
        .catch(err => {
          this.message = "Some error occured";
          this.success = true;
          this.loading = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        });
    },

    addToCart(index) {
      //adding to cart
      if (
        this.items[index][this.selectedAvailbilityOfItem[index]].quantityToBuy >
        0
      ) {
        if (
          this.items[index][this.selectedAvailbilityOfItem[index]].inCart ==
          false
        ) {
          this.cart.push(
            this.items[index][this.selectedAvailbilityOfItem[index]]
          );
          Vue.set(
            this.items[index][this.selectedAvailbilityOfItem[index]],
            "inCart",
            true
          );
        } else {
          this.cart.forEach(element => {
            if (
              element.productId ==
              this.items[index][this.selectedAvailbilityOfItem[index]].productId
            ) {
              element = this.items[index][
                this.selectedAvailbilityOfItem[index]
              ];
            }
          });
        }
        this.total();
      } else {
        if (
          this.items[index][this.selectedAvailbilityOfItem[index]]
            .quantityToBuy == 0
        ) {
          alert("Please add some quantity to add it in cart");
        }
      }
    },

    calculateQuantity(index) {
      var quantity = Math.floor(
        this.items[index][this.selectedAvailbilityOfItem[index]].priceToBuy /
          this.items[index][this.selectedAvailbilityOfItem[index]].currentPrice
      );

      if (
        this.items[index][this.selectedAvailbilityOfItem[index]].stock <
        quantity
      ) {
        if (
          confirm(
            "Only " +
              this.items[index][this.selectedAvailbilityOfItem[index]].stock +
              " are available right now"
          )
        ) {
          quantity = this.items[index][this.selectedAvailbilityOfItem[index]]
            .stock;
        } else {
          quantity = 0;
        }
      }

      Vue.set(
        this.items[index][this.selectedAvailbilityOfItem[index]],
        "quantityToBuy",
        quantity
      );
    },

    changeQuantity(incDec, index, array) {
      var value = 0;
      if (array == "cart") {
        if (incDec == "+") {
          value = this.cart[index].quantityToBuy + 1;
        } else {
          value = this.cart[index].quantityToBuy - 1;
        }
        if (value >= 0 && value <= this.cart[index].stock) {
          Vue.set(this.cart[index], "quantityToBuy", value);
          this.total();
        }
      } else {
        if (incDec == "+") {
          value =
            this.items[index][this.selectedAvailbilityOfItem[index]]
              .quantityToBuy + 1;
        } else {
          value =
            this.items[index][this.selectedAvailbilityOfItem[index]]
              .quantityToBuy - 1;
        }

        if (
          value >= 0 &&
          value <=
            this.items[index][this.selectedAvailbilityOfItem[index]].stock
        ) {
          Vue.set(
            this.items[index][this.selectedAvailbilityOfItem[index]],
            "quantityToBuy",
            value
          );
          this.total();
        }
      }
    },

    removeFromCart(index) {
      this.items.forEach(element => {
        element.forEach(item => {
          if (item.productId == this.cart[index].productId) {
            Vue.set(item, "quantityToBuy", 0);
            Vue.set(item, "inCart", false);
          }
        });
      });
      this.cart.splice(index, 1);
      this.total();
    },

    async placeOrder() {
      this.loading = true;
      let deliveryAddress = this.$refs.cartRightPanel.deliveryAddress;
      let ordererId = this.$refs.cartRightPanel.ordererId;
      if (ordererId == null) {
        //add user first
        await this.$refs.cartRightPanel
          .addOrderer()
          .then(res => {
            ordererId = res.data.uuid;
          })
          .catch(err => {
            this.message = "There's some problem in storing user";
            this.success = true;
            this.loading = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }

      if (deliveryAddress == "-1" && ordererId) {
        //add user first
        await this.$refs.cartRightPanel
          .addAddress(ordererId)
          .then(res => {
            deliveryAddress = res.data.id;
          })
          .catch(err => {
            this.message = "There's some problem in storing address";
            this.success = true;
            this.loading = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }

      if (deliveryAddress != null && deliveryAddress > 0 && ordererId) {
        this.addOrder(ordererId, deliveryAddress);
      } else {
        this.loading = false;
        if (!ordererId) {
          alert("Have you checked the user first?");
        } else if (deliveryAddress == null || deliveryAddress <= 0) {
          alert("Please select a valid address");
        }
      }
    },

    searchItems() {
      this.loading = true;
      this.items = [];

      // loading content
      let url = domain + "search";

      axios
        .get(url, {
          params: {
            data: this.searchInput,
            table: "items",
            offset: "0",
            category: "fetch"
          },
          headers: {
            from: "webappOrder"
          }
        })
        .then(res => {
          if (res.data) {
            this.items = [];
            this.selectedAvailbilityOfItem = [];
            res.data.forEach(doc => {
              {
                var itemArray = new Array();
                let itemUrl = domain + "item";
                axios
                  .get(itemUrl, {
                    params: {
                      similarProductId: doc.similarProductId
                    }
                  })
                  .then(query => {
                    if (query.data) {
                      query.data.forEach(similarDoc => {
                        {
                          let inCart = false;
                          this.cart.forEach(element => {
                            if (element.productId == similarDoc.productId) {
                              inCart = true;
                            }
                          });
                          var item = new Array();
                          // image name
                          let imageName = null;
                          if (similarDoc.image) {
                            let start =
                              similarDoc.image.indexOf("items%2F") + 8;
                            let end = similarDoc.image.indexOf("?");
                            imageName = similarDoc.image.substring(start, end);
                            imageName = this.truncate(imageName);
                          }
                          item = {
                            productId: similarDoc.productId,
                            itemId: similarDoc.itemId,
                            name: similarDoc.name,
                            cat: similarDoc.allcat,
                            stock: similarDoc.stock,
                            unit: similarDoc.unit,
                            mrp: similarDoc.MRP,
                            currentPrice: similarDoc.currentPrice,
                            discount: similarDoc.discount,
                            image: similarDoc.image,
                            imageName: imageName,
                            qty: similarDoc.qty,
                            uploadedBy: similarDoc.uploadedBy,
                            uploadedAt: similarDoc.uploadedAt,
                            cocatName: doc.cocategory.name,
                            cocatId: doc.cocategory.coid,
                            subcatName: doc.cocategory.subcategory.name,
                            subcatId: doc.cocategory.subcategory.subcatid,
                            catName: doc.cocategory.subcategory.category.name,
                            quantityToBuy: 0,
                            priceToBuy: 0,
                            inCart: inCart,
                            item_brand: similarDoc.item_brand,
                            active: similarDoc.active
                          };

                          itemArray.push(item);
                        }
                      });
                      this.items.push(itemArray);
                      this.selectedAvailbilityOfItem.push(0);
                    }
                  });
              }
            });

            this.loading = false;
          }
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
        });
    },

    selectSize(index, innerIndex) {
      Vue.set(this.selectedAvailbilityOfItem, index, innerIndex);
    },

    total() {
      this.subTotal = 0;
      this.shippingCharges = 0;
      this.totalPayableAmount = 0;
      this.cart.forEach(doc => {
        this.subTotal = this.subTotal + doc.quantityToBuy * doc.currentPrice;
      });
      if (this.subTotal > thresholdShippingCost && this.subTotal > 0) {
        this.shippingCharges = 0;
      } else {
        this.shippingCharges = 35;
      }

      this.totalPayableAmount = this.subTotal + this.shippingCharges;
    },

    truncate(value) {
      const length = 10;
      return value.length <= length
        ? value
        : value.substring(0, length) + "...";
    }
  }
};
</script>

<style scoped>
.searchbar {
  margin-bottom: auto;
  margin-top: auto;
  height: 60px;
  background-color: #cdced1;
  border-radius: 30px;
  padding: 10px;
}

.search_input {
  color: white;
  border: 0;
  outline: 0;
  background: none;
  width: 0;
  caret-color: transparent;
  line-height: 40px;
  transition: width 0.4s linear;
}

.searchbar:hover > .search_input {
  padding: 0 10px;
  width: 450px;
  caret-color: red;
  transition: width 0.4s linear;
}

.searchbar:hover > .search_icon {
  background: white;
  color: #e74c3c;
}

.search_icon {
  height: 40px;
  width: 40px;
  float: right;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: white;
}

.table-fix-size {
  max-height: 10px;
  height: 10px;
  overflow-y: auto;
}
</style>



