                    class="img-fluid rounded-circle align-self-center"
                  />
                  <div class="media-body pl-4">
                    <h6>{{deliveryBy.user.name}}</h6>
                    <div>
                      <i class="fa fa-map-marker pr-2 fa-sm" aria-hidden="true"></i>
                      <span class="location">{{deliveryBy.area}}</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-4 border-vh-r" style="font-size:medium;">
                <div class="text-center heading small">Delivery Details</div>
                <div>
                  <span class="font-weight-bold">Delivered Status:</span>
                  <span class="badge badge-cancelled pl-2 pr-2" v-if="deliveryStatus==1">Cancelled</span>
                  <span
                    class="badge badge-processing pl-2 pr-2"
