                    v-if="deliveryStatus==3"
                  >Ready To Pickup</span>
                  <span class="badge badge-shipping pl-2 pr-2" v-if="deliveryStatus==4">Shipping</span>
                  <span class="badge badge-delivered pl-2 pr-2" v-if="deliveryStatus==5">Delivered</span>
                  <span
                    class="badge badge-request-return pl-2 pr-2"
