              </div>
              <div class="col-4" style="font-size:medium;">
                <div class="text-center heading small">Order Details</div>
                <div>
                  <span class="font-weight-bold">Order At:</span>
                  <span class="response">{{orderDate}} , {{orderTime}}</span>
                </div>
                <div>
                  <span class="font-weight-bold">Payment Recieved:</span>
                  <span class="response">₹ {{paidAmount}}</span>
