                <div>
                  <span class="font-weight-bold">Payment Status:</span>
                  <span class="response">
                    <span class="bagde badge-waiting pl-1 pr-1" v-if="paidAmount==0">Not Paid</span>
                    <span
                      class="bagde badge-paid pl-1 pr-1"
                      v-else-if="paidAmount==totalPayableAmount"
                    >Paid</span>
                    <span class="bagde badge-failed pl-1 pr-1" v-else>Pending</span>
                  </span>
                </div>
