                  <div class="input-group content-search border">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">
                        <i class="fa fa-search"></i>
                      </span>
                    </div>
                    <input
                      type="text"
                      class="form-control hz-input"
                      placeholder="Search Here..."
                      aria-label="Search Here..."
                      aria-describedby="basic-addon1"
                    />
                  </div>
                </form>
              </div>
              <div class="col-6"></div>
            </div>
            <div class="table-responsive pt-4">
              <table class="table table-hover table-sm table-borderless table-content text-left">
                <thead>
                  <tr>
                    <th>Product Id</th>
                    <th>Products</th>
                    <th>Product Name</th>
                    <th>Cost Per Item</th>
                    <th>Ordered Quantity</th>
                    <th>Unit</th>
                    <th>Total Cost</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="product in products" :key="product.id">
                    <td>{{product.productId}}</td>
                    <td>
                      <div class="d-flex">
                        <img :src="product.image" class="img-fluid content-image" />
                      </div>
                    </td>
                    <td>{{product.name}}</td>
                    <td>₹{{product.pivot.currentPrice}}</td>
                    <td>{{product.pivot.quantity}}</td>
                    <td class="text-dark">{{product.qty}}{{product.unit}}</td>
                    <td>{{product.pivot.quantity * product.pivot.currentPrice}}</td>
                  </tr>
                  <tr>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line text-center">
                      <strong>Subtotal</strong>
                    </td>
                    <td class="thick-line text-right">₹{{totalDiscountedAmount}}</td>
                  </tr>
                  <tr>
                    <td class="no-line"></td>
                    <td class="no-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="no-line text-center">
                      <strong>Delivery Charges</strong>
                    </td>
                    <td class="no-line text-right">₹{{deliveryCharges}}</td>
                  </tr>
                  <tr>
                    <td class="no-line"></td>
                    <td class="no-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="thick-line"></td>
                    <td class="no-line text-center">
                      <strong>Total</strong>
                    </td>
                    <td class="no-line text-right">₹{{totalPayableAmount}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Vue from "vue";
import {
  domain,
  utcMins,
  utcHours,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";

export default {
  name: "ViewDelivery",

  data() {
    return {
      orderId: null,
      products: [],
      orderId: null,
      deliveryAddress: null,
      paidAmount: 0,
      deliveryId: null,
      deliveryBy: null,
      orderDate: null,
      orderTime: null,
      deliveryDate: null,
      deliveryTime: null,
      deliveryStatus: null,
      totalDiscountedAmount: 0,
      totalPayableAmount: 0,
      deliveryCharges: 0,

      search: null,
      searchArray: []
    };
  },

  beforeRouteEnter(to, from, next) {
    let url = domain + "delivery";
    axios
      .get(url, {
        params: {
          id: to.params.deliveryId,
          by: "budhbak",
          userAddress: "budhbak",
          inventoryAddress: "budhbak"
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          let orderDateTime = new Date(doc.order.orderTime);
          orderDateTime.setHours(orderDateTime.getHours() + utcHours);
          orderDateTime.setMinutes(orderDateTime.getMinutes() + utcMins);
          let orderDate =
            orderDateTime.getDate() +
            "/" +
            orderDateTime.getMonth() +
            "/" +
            orderDateTime.getFullYear();
          let orderTime =
            orderDateTime.getHours() +
            ":" +
            orderDateTime.getMinutes() +
            ":" +
            orderDateTime.getSeconds();
          let deliveryDate = null;
          let deliveryTime = null;
          if (doc.deliveredAt) {
            let deliveryDateTime = new Date(doc.deliveredAt);
            deliveryDateTime.setHours(deliveryDateTime.getHours() + utcHours);
            deliveryDateTime.setMinutes(
              deliveryDateTime.getMinutes() + utcMins
            );
            deliveryDate =
              deliveryDateTime.getDate() +
              "/" +
              deliveryDateTime.getMonth() +
              "/" +
              deliveryDateTime.getFullYear();
            deliveryTime =
              deliveryDateTime.getHours() +
              ":" +
              deliveryDateTime.getMinutes() +
              ":" +
              deliveryDateTime.getSeconds();
          }
          next(vm => {
            vm.orderId = doc.orderId;
            vm.deliveryId = doc.id;
            vm.deliveryBy = doc.by;
            vm.deliveryAddress = doc.order.user_address;
            vm.orderDate = orderDate;
            vm.orderTime = orderTime;
            vm.deliveryDate = deliveryDate;
            vm.deliveryTime = deliveryTime;
            vm.paidAmount = doc.order.paidAmount;
            vm.deliveryStatus = doc.status;
            vm.totalDiscountedAmount = doc.order.totalDiscountedAmount;
            vm.totalPayableAmount = doc.order.totalPayableAmount;
            vm.deliveryCharges = doc.order.deliveryCharges;
            //products
            doc.order.products.forEach(prod => {
              vm.products.push(prod);
            });
            vm.searchArray = vm.products;
          });
        });
      })
      .catch(err => {
        console.log(err.message);
      });
  },

  watch: {
    $route: "fetchDelivery"
  },

  methods: {
    fetchDelivery() {
      let url = domain + "delivery";
      axios
        .get(url, {
          params: {
            id: this.$route.params.deliveryId,
            by: "budhbak",
            userAddress: "budhbak",
            inventoryAddress: "budhbak"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            let orderDateTime = new Date(doc.order.orderTime);
            orderDateTime.setHours(orderDateTime.getHours() + utcHours);
            orderDateTime.setMinutes(orderDateTime.getMinutes() + utcMins);
            let orderDate =
              orderDateTime.getDate() +
              "/" +
              orderDateTime.getMonth() +
              "/" +
              orderDateTime.getFullYear();
            let orderTime =
              orderDateTime.getHours() +
              ":" +
              orderDateTime.getMinutes() +
              ":" +
              orderDateTime.getSeconds();
            let deliveryDate = null;
            let deliveryTime = null;
            if (doc.deliveredAt) {
              let deliveryDateTime = new Date(doc.deliveredAt);
              deliveryDateTime.setHours(deliveryDateTime.getHours() + utcHours);
              deliveryDateTime.setMinutes(
                deliveryDateTime.getMinutes() + utcMins
              );
              deliveryDate =
                deliveryDateTime.getDate() +
                "/" +
                deliveryDateTime.getMonth() +
                "/" +
                deliveryDateTime.getFullYear();
              deliveryTime =
                deliveryDateTime.getHours() +
                ":" +
                deliveryDateTime.getMinutes() +
                ":" +
                deliveryDateTime.getSeconds();
            }

            // putting data
            this.orderId = doc.orderId;
            this.deliveryId = doc.id;
            this.deliveryBy = doc.by;
            this.deliveryAddress = doc.order.user_address;
            this.orderDate = orderDate;
            this.orderTime = orderTime;
            this.deliveryDate = deliveryDate;
            this.deliveryTime = deliveryTime;
            this.paidAmount = doc.order.paidAmount;
            this.deliveryStatus = doc.status;
            this.totalDiscountedAmount = doc.order.totalDiscountedAmount;
            this.totalPayableAmount = doc.order.totalPayableAmount;
            this.deliveryCharges = doc.order.deliveryCharges;

            //products
            doc.products.forEach(prod => {
              this.products.push(prod);
            });
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    },

    searching() {
      if (this.search) {
        this.products = this.searchArray.filter(
          v => v.name.toLowerCase().indexOf(this.search.toLowerCase()) != -1
        );
      } else {
        this.products = this.searchArray;
      }
    },

    updateAction() {
      if (this.user.designation == "admin") {
        this.loading = true;
        if (this.action == "delete") {
          if (confirm("You are about to delete #" + this.deliveryId)) {
            let url = domain + "delivery/" + this.deliveryId;
            axios
              .post(
                url,
                {
                  _method: "DELETE"
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                if (res.data) {
                  this.status = res.data.status;
                }
              })
              .catch(err => {
                console.log(err.message);
              });
          }
        } else {
          let url = domain + "delivery/" + delivery.deliveryId;
          axios
            .post(
              url,
              {
                _method: "PUT",
                status: this.action
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              //colsing inputs
              res.data.forEach(snap => {
                Vue.set(this.deliveries[index], status, snap.status);
              });
            })
            .catch(err => {
              console.log(err.message);
            });
        }
        this.loading = false;
      } else {
        confirm("You are not allowed to make changes");
      }
    }
  }
};
</script>

<style scoped>
.delivery-by-header h6 {
  color: rgba(47, 48, 61, 1);
  font-weight: 700;
  margin-bottom: 0.2em;
}

.heading {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
}

.delivery-by-header .location {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
  font-size: smaller;
}

.delivery-content {
  font-size: small;
}

.delivery-content img {
  width: 4em;
}

.response {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
}
</style>
<style src="@/assets/css/tableContent.css" scoped>



