<template>
  <div class="container-fluid">
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <h4>UPLOADER's DETAILS</h4>
      </div>
      <div class="col-6 content-header small">
        <span>doorstep > uploaders</span>
      </div>
    </div>
    <div class="row">
      <div class="col-lg-2"></div>
      <div class="col-lg-8 col-sm-12">
        <!-- <Add uplodader/> -->
        <div class="card content border-1 shadow-sm">
          <div class="card-body">
            <div class="row pt-2">
              <div class="col-sm-12">
                <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                  <i class="fas fa-exclamation-triangle pr-2"></i>Fields marked with ( * ) is important and is to be filled
                </div>
                <form class="form pb-5 mb-4">
                  <div class="form-row">
                    <div class="col-sm-12">
                      <label>Enter UserId</label>
                      <input
                        type="text"
                        v-model="userId"
                        class="form-control form-control-sm"
                        :disabled="disable"
                        @blur="checkUserId"
                      />
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="col-sm-12">
                      <label>Change package</label>
                      <select
                        v-model="Cpackage"
                        class="form-control form-control-sm"
                        :disabled="disable"
                      >
                        <option disabled="disabled" class="font-weight-bold">Package</option>
                        <option value="1" class="font-weight-bold">₹1</option>
                        <option value="3" class="font-weight-bold">₹3</option>
                      </select>
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="col text-center">
                      <button
                        class="btn btn-sm btn-go-green font-weight-bold d-inline"
                        type="submit"
                        @click.prevent="change"
                        :disabled="disable"
                      >
                        <i class="fas fa-save pr-1"></i>
                        CHANGE
                      </button>
                      <router-link to="/inventory" class="btn btn-sm btn-secondary">
                        <i class="fas fa-ban pr-1"></i>
                        <div class="font-weight-bold d-inline">CANCEL</div>
                      </router-link>
                    </div>
                  </div>

                  <div class="form-row" v-if="errors[1]">
                    <div class="col text-center text-danger">{{errors[0]}}</div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-lg-2"></div>
    </div>

    <div
      style="width:100%; background: rgba(250,250,250 ,.9); position:fixed; top:0; bottom:0; left:0; right:0;"
      v-if="disable || success"
    >
      <div class="loading-content card p-4 shadow">
        <div class="d-flex justify-content-center" v-if="disable">
          <div class="spinner-grow text-danger" role="status"></div>
        </div>
        <div class="d-flex justify-content-center" v-if="disable">
          <div class="text-center">
            <h6 class="text-dark font-weight-medium">{{disableMessage}}</h6>
            <div class="font-weight-bold bg-light-red-bg text-danger p-3 mt-3">
              <i class="fas fa-exclamation-triangle pr-2"></i>Please Don't Press Back button while we are working on data
            </div>
          </div>
        </div>

        <h4 v-if="success" class="font-weight-bold">{{ message }}</h4>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import Vue from "vue";
import db from "../firebaseInit";
import firebase from "firebase";
import { domain } from "@/store/variable";
import store from "@/store/store";
import Loader from "../Loader";

export default {
  name: "ChangePackage",

  data() {
    return {
      userId: null,
      userDesignation: null,
      Cpackage: null,

      //success
      message: null,
      disable: false,
      disableMessage: "Uploading data kripya shanti bnaye rkhiye",
      success: false,
      errors: []
    };
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  beforeRouteLeave(to, from, next) {
    if (this.userId) {
      if (confirm("Your changes will be lost.Do you want to leave the page?")) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },

  methods: {
    change() {
      if (
        this.userId &&
        this.user &&
        this.user.designation == "admin" &&
        this.userId != this.user.uuid
      ) {
        if (this.userDesignation == "admin") {
          if (confirm("You are about to make changes to an admin")) {
            this.disable = true;
            let url = domain + "user/uploader/" + this.userId;
            axios
              .post(
                url,
                {
                  _method: "PUT",
                  package: this.Cpackage
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": "application/json",
                    "Access-Control-Allow-Origin": "*",
                    "X-Requested-With": "XMLHttpRequest",
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                //// to show

                this.message = "Changes Made";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch(err => {
                /// to show
                this.message = "Upload Failed";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          }
        } else {
          this.disable = true;
          let url = domain + "user/uploader/" + this.userId;
          axios
            .post(
              url,
              {
                _method: "PUT",
                package: this.Cpackage
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": "application/json",
                  "Access-Control-Allow-Origin": "*",
                  "X-Requested-With": "XMLHttpRequest",
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              //// to show

              this.message = "Changes Made";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              /// to show
              this.message = "Upload Failed";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        }
      }
    },

    checkUserId() {
      if (
        this.userId &&
        this.user &&
        this.user.designation == "admin" &&
        this.userId != this.user.uuid
      ) {
        this.disable = true;
        this.disableMessage = "User ki khoj chalu hai";

        let url = domain + "user/uploader";
        axios
          .get(url, {
            params: {
              userId: this.userId.trim()
            }
          })
          .then(res => {
            if (res && res.data.length <= 1) {
              res.data.forEach(doc => {
                this.Cpackage = doc.package;
                this.userDesignation = doc.designation;
              });
            } else {
              if (res.data.length > 1) {
                this.errors[0] = "Sorry, this user already have both packages";
              } else {
                this.errors[0] = "User don't exist";
              }
            }
            this.disable = false;
            this.disableMessage = "Uploading data kripya shanti bnaye rkhiye";
          })
          .catch(err => {
            console.log(err.message);
            this.disable = false;
            this.disableMessage = "Uploading data kripya shanti bnaye rkhiye";
          });
      } else if (this.userId == this.user.uuid) {
        alert(
          "Aap khud ka hi package change krna chahte hai, kamal hai.. Pr nhi kr skte"
        );
      }
    }
  }
};
</script>


<style src="@/assets/css/tableContent.css" scoped>
</style>



