<template>
  <div class="container-fluid">
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <h4>All Deliveries</h4>
      </div>
      <div class="col-6 content-header text-center small">
        <span>doorstep > deliveries > all</span>
      </div>
    </div>

    <DashBoard :newDeliveries="deliveries" :disable="loading" @fetchDeliveries="fetchDeliveries"/>
  </div>
</template>
<script>
import axios from "axios";
import Vue from "vue";
import { domain } from "@/store/variable";
import { utcHours } from "@/store/variable";
import { utcMins } from "@/store/variable";
import DashBoard from "./DashBoard.vue";
import store from "@/store/store";

export default {
  name: "AllDeliveries",
  components: {
    DashBoard
  },
  data() {
    return {
      deliveries: [],
      loading: false
    };
  },

  computed: {
    size() {
      return store.state.deliveryPageSize;
    }
  },

  mounted() {
    this.fetchDeliveries();
  },

  methods: {
    fetchDeliveries(page) {
      let url = domain + "delivery";
      if (!page) {
        page = 1;
      }

      this.loading = true;
      axios
        .get(url, {
          params: {
            by: "with",
            page: page,
            limit: this.size
          }
        })
        .then(res => {
          let deliveryList = [];
          res.data.data.forEach(doc => {
            if (doc.order) {
              let dateTime = new Date(doc.created_at);
              dateTime.setHours(dateTime.getHours() + utcHours);
              dateTime.setMinutes(dateTime.getMinutes() + utcMins);
              let date =
                dateTime.getDate() +
                "/" +
                dateTime.getMonth() +
                "/" +
                dateTime.getFullYear();
              let time =
                dateTime.getHours() +
                ":" +
                dateTime.getMinutes() +
                ":" +
                dateTime.getSeconds();

              let delivery = {
                deliveryId: doc.id,
                orderId: doc.orderId,
                products: doc.order.products,
                amount: doc.order.totalPayableAmount,
                status: doc.status,
                paidAmount: doc.order.paidAmount,
                deliveryBy: doc.by.user.name,

                checked: false
              };

              deliveryList.push(delivery);

              store.dispatch("patchdeliveryPageCount", res.data.last_page);
              store.dispatch("patchdeliveryPageNumber", res.data.current_page);
              store.dispatch("patchtotalDeliveries", res.data.total);
            }
          });

          this.deliveries = deliveryList;
        });

      this.loading = false;
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped>
</style>



