<template>
  <nav class="navbar fixed-bottom footer bg-dark shadow inventoryFooter">
    <div>
      <button
        class="fas fa-sync-alt fa-xs text-white pl-2 pr-4 button-trans"
        @click="refresh()"
        data-toggle="tooltip"
        data-placement="right"
        title="Click to Refresh this page"
      ></button>
      
      <div class="d-inline-block pl-4">
        <input type="number" v-model.number="size" class="form-control-sm" style="width:70px;" @blur="updateSize()">
      </div>
    </div>

    <div class="text-center">
      <button :class="classFirst" @click="prevPage" :disabled="pageNumber==1" aria-hidden="true"></button>
      <button class="fa fa-backward fa-xs text-white button-trans" @click="firstPage" :disabled="pageNumber==0" aria-hidden="true"></button>
      <span class="text-white">|</span>
      <span class="text-white pl-2 pr-2">Page {{pageNumber}} of {{pageCount}}</span>
      <span class="text-white">|</span>
      <button class="fa fa-forward fa-xs text-white  button-trans" @click="lastPage"
          :disabled="pageNumber  >= pageCount" aria-hidden="true"></button>
      <button :class="classLast" @click="nextPage"
          :disabled="pageNumber  >= pageCount" aria-hidden="true"></button>
    </div>


    
   <div class="float-right">
      <span class="font-weight-bold text-white mr-2 small">Total Deliveries: {{totalDeliveries}}</span>
      
   </div>
  </nav>
</template>
<style>
.add-item-button {
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 800;
}

.button-trans {
    background-color: transparent;
    background-repeat:no-repeat;
    border: none;
    cursor:pointer;
    overflow: hidden;
}

.inventoryFooter{
  background:#E0E0E0;
  /* margin-left:220px */
}

/* @media (max-width: 768px) {
  .inventoryFooter {
    margin-left: 0;
  }
} */
</style>
<script>
import { bus } from "@/main";
import store from "@/store/store";
export default {
  name: "deliveryFooter",

data() {
  return {
    classLast: 'fa fa-step-forward fa-xs text-white  button-trans',
    classFirst: 'fa fa-step-backward fa-xs text-white button-trans'
  }
},
  computed: {

    pageCount(){
      return store.state.deliveryPageCount
    },
    pageNumber(){
      return store.state.deliveryPageNumber
    },
    size:{
      get(){
      return store.state.deliveryPageSize
      },
      set(newValue){
      store.dispatch("patchdeliveryPageSize", newValue);
      }
    },
    totalDeliveries(){
      return store.state.totalDeliveries
    }

  },


  methods: {
    // order(On, Way) {
    //   let page=this.pageNumber;
    //   bus.$emit("filterItems", { On, Way, page});
    // },

    prevPage(){
      let page = this.pageNumber-1;
      bus.$emit("paginateDelivery", {page});
    },

    nextPage(){
      let page = this.pageNumber+1;
      bus.$emit("paginateDelivery", {page});
    },

    firstPage(){
      let page = 1;
      bus.$emit("paginateDelivery", {page});
    },

    lastPage(){
      let page = this.pageCount;
      bus.$emit("paginateDelivery", {page});
    },

    refresh(){
      let page = this.pageNumber;
      bus.$emit("paginateDelivery", {page});
    },

    updateSize(){
      store.dispatch("patchdeliveryPageSize", this.size);
      let page = 1;
      bus.$emit("paginateDelivery", {page});
    },
  }
};
</script>




