<template>
  <nav class="navbar fixed-bottom footer bg-dark shadow inventoryFooter">
    <div>
      <i
        class="fas fa-sync-alt fa-xs text-white pl-2 pr-4"
        @click="$emit('fetchItems')"
        data-toggle="tooltip"
        data-placement="right"
        title="Click to Refresh this page"
      ></i>
      <div class="dropdown dropup d-inline-block" @change="order()">
        <i
          class="fas fa-filter fa-xs text-white"
          data-toggle="dropdown"
          data-target="#filterDropdown"
          aria-haspopup="true"
          aria-expanded="false"
          id="dropdownMenuButton"
        ></i>
        <div
          id="filterDropdown"
          class="dropdown-menu dropdown-menu-left"
          aria-labelledby="dropdownMenuButton"
        >
          <h6 class="dropdown-header small">Ascending</h6>
          <div class="dropdown-divider"></div>
          <span @click="order('name','asc')" class="dropdown-item small">Name</span>
          <span @click="order('category','asc')" class="dropdown-item small">Category</span>
          <span @click="order('subcategory','asc')" class="dropdown-item small">SubCategory</span>
          <span @click="order('cocategory','asc')" class="dropdown-item small">CoCategory</span>
          <span @click="order('uploadedAt','asc')" class="dropdown-item small">Uploaded At</span>
          <span @click="order('imageName','asc')" class="dropdown-item small">Image</span>
          <span @click="order('brand','asc')" class="dropdown-item small">Brand</span>
          <div class="dropdown-divider"></div>
          <h6 class="dropdown-header small">Descending</h6>
          <div class="dropdown-divider"></div>
          <span @click="order('name','desc')" class="dropdown-item small">Name</span>
          <span @click="order('category','desc')" class="dropdown-item small">Category</span>
          <span @click="order('subcategory','desc')" class="dropdown-item small">SubCategory</span>
          <span @click="order('cocategory','desc')" class="dropdown-item small">CoCategory</span>
          <span@click="order('uploadedAt','desc')" class="dropdown-item small">Uploaded At</span>
          <span@click="order('brand','desc')" class="dropdown-item small">Brand</span>
          <span
            @click="order('imageName','desc')"
            class="dropdown-item small"
          >Image</span>
        </div>
      </div>
      <div class="d-inline-block pl-4">
        <input type="number" v-model.number="size" class="form-control-sm" style="width:70px;" @blur="updateSize()">

      </div>
    </div>

    <div class="text-center">
      <button :class="classFirst" @click="prevPage" :disabled="pageNumber==1" aria-hidden="true"></button>
      <button class="fa fa-backward fa-xs text-white button-trans" @click="firstPage" :disabled="pageNumber==0" aria-hidden="true"></button>
      <span class="text-white">|</span>
      <span class="text-white pl-2 pr-2">Page {{pageNumber}} of {{pageCount}}</span>
      <span class="text-white">|</span>
      <button class="fa fa-forward fa-xs text-white  button-trans" @click="lastPage"
          :disabled="pageNumber  >= pageCount - 1" aria-hidden="true"></button>
      <button :class="classLast" @click="nextPage"
          :disabled="pageNumber  >= pageCount - 1" aria-hidden="true"></button>
    </div>


    <!-- <div
      class="font-weight-bold text-white d-none d-sm-block float-right"
      style="font-size:12px; opacity:45%;letter-spacing:1.2px"
    ></div> -->
   <div class="float-right">
      <span class="font-weight-bold text-white mr-2 small">Total Items: {{totalItems}}</span>
      <router-link to="/addItem" class="btn btn-danger btn-sm shadow add-item-button">
        <i class="fa fa-plus fa-xs"></i> ADD ITEM
      </router-link>
   </div>
  </nav>
</template>
<style>
.add-item-button {
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 800;
}

.button-trans {
    background-color: transparent;
    background-repeat:no-repeat;
    border: none;
    cursor:pointer;
    overflow: hidden;
}

.inventoryFooter{
  background:#E0E0E0;
  /* margin-left:220px */
}

/* @media (max-width: 768px) {
  .inventoryFooter {
    margin-left: 0;
  }
} */
</style>
<script>
import { bus } from "../../main";
import store from "@/store/store";
export default {
  name: "inventoryFooter",

data() {
  return {
    classLast: 'fa fa-step-forward fa-xs text-white  button-trans',
    classFirst: 'fa fa-step-backward fa-xs text-white button-trans'
  }
},
  computed: {

    pageCount(){
      return store.state.inventoryPageCount
    },
    pageNumber(){
      return store.state.inventoryPageNumber
    },
    size:{
      get(){
      return store.state.inventoryPageSize
      },
      set(newValue){
      store.dispatch("patchinventoryPageSize", newValue);
      }
    },
    totalItems(){
      return store.state.inventoryTotalItems
    }

  },


  methods: {
    order(On, Way) {
      let page=this.pageNumber;
      bus.$emit("filterItems", { On, Way, page});
    },

    prevPage(){
      let page = this.pageNumber-1;
      bus.$emit("fetchItems", {page});
    },

    nextPage(){
      let page = this.pageNumber+1;
      bus.$emit("fetchItems", {page});
    },

    firstPage(){
      let page = 1;
      bus.$emit("fetchItems", {page});
    },

    lastPage(){
      let page = this.pageCount;
      bus.$emit("fetchItems", {page});
    },

    updateSize(){
      store.dispatch("patchinventoryPageSize", this.size);
      let page = 1;
      bus.$emit("fetchItems", {page});
    },
  }
};
</script>




