<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-1"></div>
        <div class="col-lg-5 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>
            <span v-if="edit">EDIT CATEGORY - {{name}}</span>
            <span v-else>ADD NEW CATEGORY</span>
          </h5>
          <div class="row">
            <div class="col-sm-12">
              <div class="row p-3">
                <div class="col-sm-12">
                  <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                    <i class="fas fa-exclamation-triangle pr-2"></i>Fields marked with ( * ) is important and is to be filled
                  </div>
                  <form class="form pb-5 mb-4">
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Allcat</label>
                        <input
                          type="text"
                          v-model="allcat"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Homecat</label>
                        <input
                          type="text"
                          v-model="homecat"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Name</label>
                        <input
                          type="text"
                          v-model="name"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[3]"
                        >{{errors[3]}}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>SubcatCode</label>
                        <input
                          type="text"
                          v-model="subcatcode"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable || disableId"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[2]"
                        >{{errors[2]}}</span>
                      </div>
                    </div>

                    <!--acttive -->
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Active</label>
                        <input
                          type="checkbox"
                          v-model="active"
                          class="form-control-sm align-self-center"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>

                    <div class="form-row">
                      <!-- image -->
                      <div class="col-sm-12 text-center">
                        <div class="switch">
                          <label>
                            Add Path *
                            <input
                              type="checkbox"
                              v-model="imageOption"
                              @click="media=null"
                              :disables="disable"
                            />
                            <span class="lever"></span>
                            Upload
                          </label>
                        </div>

                        <div class="col-12" v-if="imageOption==false">
                          <label>Image Path</label>
                          <input
                            type="text"
                            v-model="image"
                            :disabled="disable"
                            class="form-control form-control-sm"
                          />
                        </div>

                        <div class="col-12 text-center" v-else>
                          <label class="btn btn-sm btn-info text-center" style="color:#fff">
                            <i class="fas fa-image pr-2"></i>
                            <div class="d-inline font-weight-bold">ADD IMAGE</div>
                            <input type="file" class="file" @change="change('image')" ref="image" />
                          </label>
                          <img :src="media" class="img-fluid" v-if="media" />
                          <img :src="image" class="img-fluid" v-else-if="image" />
                        </div>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col text-center">
                        <button
                          class="btn btn-sm btn-go-green font-weight-bold d-inline"
                          type="submit"
                          @click="add"
                          :disabled="disable"
                        >
                          <i class="fas fa-save pr-1"></i>
                          SAVE
                        </button>
                        <button
                          type="reset"
                          class="btn btn-sm btn-secondary"
                          @click="disableId=false"
                        >
                          <i class="fas fa-ban pr-1"></i>
                          <div class="font-weight-bold d-inline">CANCEL</div>
                        </button>
                      </div>
                    </div>

                    <div class="form-row" v-if="errors[1]">
                      <div class="col text-center text-danger">{{errors[1]}}</div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-5">
          <div class="card shadow mb-4" v-if="subcategories.length>0">
            <h5 class="card-header font-weight-bold" data-toggle="collapse" data-target="#details">
              <i class="fas fa-list pl-1 pr-3"></i>DETAILS
            </h5>
            <span id="details" class="card-body collapse">
              <i class="fas fa-list pl-1 pr-3"></i>Sub Category
              <div class="row">
                <div class="col-sm-12">
                  <div class="row p-3">
                    <div class="col-sm-12">
                      <table>
                        <tr>
                          <th>Coid</th>
                          <th class="pl-2">Name</th>
                        </tr>
                        <tr v-for="(subcategory,index) in subcategories" :key="subcategory.coid">
                          <td>{{subcategory.coid}}</td>
                          <td class="pl-2">{{subcategory.name}}</td>
                        </tr>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </span>
          </div>

          <div class="card shadow p-0 ml-2">
            <h5 class="card-header font-weight-bold">
              <i class="fas fa-list pl-1 pr-3"></i>CATEGORIES
            </h5>
            <div class="row">
              <div class="col-sm-12">
                <div class="row p-3">
                  <div class="col-sm-12">
                    <table>
                      <tr>
                        <th></th>
                        <th>Subcatcode</th>
                        <th class="pl-2">Name</th>
                        <th class="pl-2">Actions</th>
                      </tr>
                      <tr v-for="(category,index) in categories" :key="category.subcatcode">
                        <td>
                          <i
                            class="fas fa-circle text-success"
                            aria-hidden="true"
                            v-if="category.active==true"
                          ></i>
                          <i class="fas fa-circle text-danger" aria-hidden="true" v-else></i>
                        </td>
                        <td>{{category.subcatcode}}</td>
                        <td class="pl-2">{{category.name}}</td>
                        <td class="pl-2">
                          <a
                            style="color:#424242;"
                            href="#"
                            title="delete this item"
                            @click.prevent="deleteCo(index)"
                          >
                            <i class="fa fa-trash"></i>
                          </a>
                          <a
                            style="color:#424242;"
                            href="#"
                            title="delete this item"
                            @click.prevent="editCategory(index)"
                          >
                            <i class="fa fa-pencil-alt"></i>
                          </a>
                        </td>
                      </tr>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-1"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
import Vue from "vue";
import db from "../firebaseInit";
import firebase from "firebase";
import { firestorage } from "../firebaseInit";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import Loader from "../Loader";

export default {
  name: "category",
  components: {
    Loader
  },
  data() {
    return {
      allcat: null,
      homecat: null,
      name: null,
      subcatcode: null,
      active: true,
      image: null,
      imageOption: false,
      media: null,
      categories: [],
      subcategories: [],

      errors: [],
      edit: false,
      message: null,
      disable: false,
      disableId: false,
      disableMessage: null,
      success: false,
      success: false
    };
  },

  mounted() {
    this.fetchCategories();
  },

  methods: {
    add() {
      this.disable = true;
      this.disableMessage = "Uploading Data, Kripya Shanti banaye rkhe...";

      if (this.media) {
        this.uploadImage()
          .then(downloadURL => {
            this.image = downloadURL;
            return this.image;
          })
          .then(() => {
            this.uploadData();
          });
      } else {
        this.uploadData();
      }
    },

    change() {
      let files = null;
      files = this.$refs.image.files;

      if (!files.length) {
        return;
      } else {
        this.createImage(files[0]);
      }
    },

    createImage(file) {
      let reader = new FileReader();
      reader.onload = e => {
        this.media = e.target.result;
      };
      reader.readAsDataURL(file);
    },

    deleteCo(index) {
      if (confirm("You are about to delete " + this.categories[index].name)) {
        this.disable = true;

        let url = domain + "cat/" + this.categories[index].subcatcode;
        axios
          .post(
            url,
            {
              _method: "delete"
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data) {
              db.collection("category ")
                .where("subcatcode", "==", this.categories[index].subcatcode)
                .get()
                .then(res => {
                  res.forEach(doc => {
                    doc.ref.delete();
                    return "done";
                  });
                })
                .then(rep => {
                  this.message = "Data Delete Success";
                  this.success = true;
                  this.disable = false;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                })
                .catch(err => {
                  this.message = "Something Went Wrong";
                  this.success = true;
                  this.disable = false;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
              this.categories.splice(index, 1);
            } else {
              this.message = "Something Went wrong";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            }
          });
      }
    },

    editCategory(index) {
      if (this.categories) {
        //it's editing not adding
        this.edit = true;
        this.disableId = true;
        //details
        this.allcat = this.categories[index].allcat;
        this.homecat = this.categories[index].homecat;
        this.name = this.categories[index].name;
        this.subcatcode = this.categories[index].subcatcode;
        this.image = this.categories[index].image;
        this.active = this.categories[index].active;
        this.imageOption = true;
        this.media = null;

        this.fetchSubCategories(this.categories[index].subcatcode);
      }
    },

    fetchCategories() {
      let url = domain + "cat";
      axios.get(url).then(res => {
        if (res.data.length) {
          res.data.forEach(doc => {
            let active = true;
            if (doc.active == "0") {
              active = false;
            }
            let cat = {
              allcat: doc.allcat,
              homecat: doc.homecat,
              name: doc.name,
              image: doc.image,
              active: active,
              subcatcode: doc.subcatcode
            };
            this.categories.push(cat);
          });
        }
      });
    },

    fetchSubCategories(subcatid) {
      let url = domain + "subcat";
      axios
        .get(url, {
          params: {
            subcatid: subcatid
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            this.subcategories.push(doc);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    goBack() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push("/inventory");
    },

    uploadImage() {
      let file = this.$refs.image.files[0];
      this.image = null;
      return firestorage
        .ref("o/" + file.name)
        .put(file)
        .then(res => {
          //downloading url
          let mediaURL = res.ref.getDownloadURL();
          return mediaURL;
        })
        .catch(err => {
          console.log(err);
          return null;
        });
    },

    uploadData() {
      let Cactive = 0;
      if (this.active == true) {
        Cactive = 1;
      } else {
        Cactive = 0;
      }
      if (this.edit != true) {
        {
          db.collection("category ")
            .add({
              allcat: this.allcat,
              homecat: this.homecat,
              subcatcode: this.subcatcode,
              name: this.name,
              image: this.image,
              active: Cactive,
              uploadedAt: new Date(),
              updatedAt: new Date()
            })
            .then(res => {
              let url = domain + "cat";
              axios
                .post(
                  url,
                  {
                    allcat: this.allcat,
                    homecat: this.homecat,
                    subcatcode: this.subcatcode,
                    name: this.name,
                    active: Cactive,
                    image: this.image
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": ContentType,
                      "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                      "X-Requested-With": XRequestedWith,
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  this.categories.unshift(res.data);

                  //// to show

                  this.message = "Data Upload Success";
                  this.success = true;
                  this.disable = false;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
            })
            .catch(err => {
              console.log(err);
            });
        }
      } else {
        {
          db.collection("category ")
            .where("subcatcode", "==", this.subcatcode)
            .get()
            .then(snap => {
              return snap.docs[0].ref.update({
                allcat: this.allcat,
                homecat: this.homecat,
                subcatcode: this.subcatcode,
                name: this.name,
                image: this.image,
                active: Cactive,
                updatedAt: new Date()
              });
            })
            .then(res => {
              let url = domain + "cat/" + this.subcatcode;
              axios
                .post(
                  url,
                  {
                    _method: "PUT",
                    allcat: this.allcat,
                    homecat: this.homecat,
                    name: this.name,
                    active: Cactive,
                    image: this.image
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": ContentType,
                      "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                      "X-Requested-With": XRequestedWith,
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  this.categories.forEach((doc, index) => {
                    if (doc.subcatcode == res.data.subcatcode) {
                      Vue.set(this.categories, index, res.data);
                    }
                  });
                  //// to show

                  this.message = "Data Upload Success";
                  this.success = true;
                  this.disable = false;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
            })
            .catch(err => {
              console.log(err);
            });
        }
      }
    },

    validate() {
      this.errors = [];
      if (this.subcatcode && this.name) {
        return true;
      } else {
        if (!this.subcatcode) {
          Vue.set(this.errors, 2, "Fill valid Sub cat Id");
        }
        if (!this.name) {
          Vue.set(this.errors, 3, "Fill Name");
        }

        Vue.set(this.errors, 1, "Something is missing");
        return false;
      }
    }
  }
};
</script>

<style>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.modal-dialog {
  overflow-y: initial !important;
}
.modal-body {
  overflow-y: auto;
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>





