<template>
  <div>
    <Dashboard :products="items" :loading="loading" v-if="items"></Dashboard>

    <Footer @fetchItems="fetchItems" />
  </div>
</template>

<script>
import axios from "axios";
import db from "../firebaseInit";
import Dashboard from "@/components/Inventory/DashBoard.vue";
import Footer from "@/components/Inventory/Footer.vue";
import { bus } from "@/main";
import { domain } from "@/store/variable";
import store from "@/store/store";
import firebase from "firebase";

export default {
  name: "UploaderItems",
  data() {
    return {
      uploader: [],
      items: [],
      loading: false,
      orderItems: "name",
      orderWay: "asc"
    };
  },

  components: {
    Footer,
    Dashboard
  },

  beforeRouteEnter(to, from, next) {
    // loading content
    let url = domain + "user";

    axios
      .get(url, {
        params: {
          uuid: to.params.uploaderId,
          designation: "webappUploader",
          package: "kuchLaiye",
          transactions: "kuchLaiye"
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          next(vm => {
            let totalUploads = 0;
            let totalPay = 0;
            let amountPaid = 0;
            let amountDue = 0;

            // package ka detail nikal liya
            doc.package.forEach(element => {
              totalUploads = totalUploads + Number(element.totalUploads);
              if (element.package == 3) {
                totalPay = totalPay + 3 * Number(element.totalUploads);
              } else {
                totalPay = totalPay + Number(element.totalUploads);
              }
            });

            //trnasaction ka nikalo
            doc.upload_transactions.forEach(element => {
              amountPaid = amountPaid + Number(element.amount);
            });

            amountDue = totalPay - amountPaid;

            let details = {
              uploaderId: doc.uuid,
              name: doc.name,
              email: doc.email,
              totalUploads: totalUploads,
              amountPaid: amountPaid,
              totalPay: totalPay,
              amountDue: amountDue,
              transactions: doc.upload_transactions.length
            };

            vm.uploader = details;
          });
        });
      })
      .catch(err => {
        console.log(err);
      });
  },

  computed: {
    user() {
      return store.state.user;
    },

    size() {
      return store.state.inventoryPageSize;
    }
  },

  created() {
    bus.$on("filterItems", data => {
      this.order(data["On"], data["Way"], data["page"]);
    });

    bus.$on("fetchItems", data => {
      this.fetchItems(data["page"]);
    });
  },

  mounted() {
    setTimeout(() => {
      if (this.user) {
        this.fetchItems();
      }
    }, 4000);
  },

  watch: {
    $route: "fetchUploader"
  },

  methods: {
    fetchItems(page) {
      this.loading = true;
      this.items = [];

      // loading content
      let url = domain + "item";
      if (!page) {
        page = 1;
      }

      axios
        .get(url, {
          params: {
            page: page,
            limit: this.size,
            order: this.orderItems,
            orderWay: this.orderWay,
            uploadedBy: this.uploader.uploaderId
          }
        })
        .then(res => {
          res.data.data.forEach(doc => {
            var item = new Array();

            // image name
            let imageName = null;
            if (doc.image) {
              let start = doc.image.indexOf("items%2F") + 8;
              let end = doc.image.indexOf("?");
              imageName = doc.image.substring(start, end);
              imageName = this.truncate(imageName);
            }
            item = {
              productId: doc.productId,
              itemId: doc.itemId,
              name: doc.name,
              cat: doc.allcat,
              stock: doc.stock,
              unit: doc.unit,
              mrp: doc.MRP,
              currentPrice: doc.currentPrice,
              discount: doc.discount,
              image: doc.image,
              imageName: imageName,
              showImage: 0,
              qty: doc.qty,
              uploadedBy: doc.uploadedBy,
              uploadedAt: doc.uploadedAt

              // cocatName: doc.cocategory.name,
              // cocatId: doc.cocategory.coid,
              // subcatName: doc.cocategory.subcategory.name,
              // subcatId: doc.cocategory.subcategory.subcatid,
              // catName: doc.cocategory.subcategory.category.name
            };

            this.items.push(item);

            // getting co cat, subcat and cat
            let Courl = domain + "cocat";
            axios
              .get(Courl, {
                params: {
                  item_id: item.itemId
                }
              })
              .then(crres => {
                crres.data.forEach(doc => {
                  item["cocatName"] = doc.name;
                  item["cocatId"] = doc.coid;

                  let Suburl = domain + "subcat";
                  axios
                    .get(Suburl, {
                      params: {
                        coid: doc.coid
                      }
                    })
                    .then(subres => {
                      subres.data.forEach(sub => {
                        item["subcatName"] = sub.name;
                        item["subcatId"] = sub.subcatid;

                        let Caturl = domain + "cat";
                        axios
                          .get(Caturl, {
                            params: {
                              subcatcode: sub.subcatid
                            }
                          })
                          .then(catres => {
                            catres.data.forEach(doc => {
                              item["catName"] = doc.name;
                            });
                          });
                      });
                    });
                });
              });
          });

          store.dispatch("patchinventoryPageCount", res.data.last_page);
          store.dispatch("patchinventoryPageNumber", res.data.current_page);
          store.dispatch("patchinventoryTotalItems", res.data.total);
        })
        .catch(err => {
          console.log(err);
        });

      this.loading = false;
    },

    fetchUploader() {
      let url = domain + "user/uploader";

      axios
        .get(url, {
          params: {
            uuid: this.$route.params.uploadId,
            designation: "webappUploader",
            package: "kuchLaiye",
            transactions: "kuchLaiye"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            let totalUploads = 0;
            let totalPay = 0;
            let amountPaid = 0;
            let amountDue = 0;

            // package ka detail nikal liya
            doc.package.forEach(element => {
              totalUploads = totalUploads + Number(element.totalUploads);
              if (element.package == 3) {
                totalPay = totalPay + 3 * Number(element.totalUploads);
              } else {
                totalPay = totalPay + Number(element.totalUploads);
              }
            });

            //trnasaction ka nikalo
            doc.upload_transactions.forEach(element => {
              amountPaid = amountPaid + Number(element.amount);
            });

            amountDue = totalPay - amountPaid;

            let details = {
              uploaderId: doc.uuid,
              name: doc.name,
              email: doc.email,
              totalUploads: totalUploads,
              amountPaid: amountPaid,
              totalPay: totalPay,
              amountDue: amountDue,
              transactions: doc.upload_transactions.length
            };

            this.uploader = details;
            this.fetchItems(1);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    order(On, Way, page) {
      this.loading = true;
      //
      this.orderItems = On;
      this.orderWay = Way;

      if (On == "imageName") {
        this.orderItems = "image";
      }
      //else {
      //   this.items = _.orderBy(this.items, this.orderItems, this.orderWay);
      // }
      this.fetchItems(page);

      this.loading = false;
    },

    truncate(value) {
      const length = 10;
      return value.length <= length
        ? value
        : value.substring(0, length) + "...";
    }
  },

  sockets: {
    message(data) {
      console.log(data);
    }
  }
};
</script>




