<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-1"></div>
        <div class="col-lg-5 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>
            <span v-if="edit">EDIT BRAND - {{name}}</span>
            <span v-else>ADD NEW BRAND</span>
          </h5>
          <div class="row">
            <div class="col-sm-12">
              <div class="row p-3">
                <div class="col-sm-12">
                  <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                    <i class="fas fa-exclamation-triangle pr-2"></i>Fields marked with ( * ) is important and is to be filled
                  </div>
                  <form class="form pb-5 mb-4">
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Name</label>
                        <input
                          type="text"
                          v-model="name"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>
                    <!-- <div class="form-row">
                      <div class="col-sm-12">
                        <label>Type</label>
                        <input
                          type="text"
                          v-model="type"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>-->
                    <div class="form-row">
                      <div class="col-sm-6">
                        <label>
                          Active
                          <input type="checkbox" v-model="active" :disables="disable" />
                        </label>
                      </div>
                      <div class="col-sm-6">
                        <label>
                          Visible
                          <input type="checkbox" v-model="visible" :disables="disable" />
                        </label>
                      </div>
                    </div>

                    <!-- image -->
                    <div class="form-row">
                      <div class="col-sm-12 text-center">
                        <div class="switch">
                          <label>
                            Add Path *
                            <input
                              type="checkbox"
                              v-model="imageOption"
                              @click="media=null"
                              :disables="disable"
                            />
                            <span class="lever"></span>
                            Upload
                          </label>
                        </div>

                        <div class="col-12" v-if="imageOption==false">
                          <label>Image Path</label>
                          <input
                            type="text"
                            v-model="image"
                            :disabled="disable"
                            class="form-control form-control-sm"
                          />
                        </div>

                        <div class="col-12 text-center" v-else>
                          <label class="btn btn-sm btn-info text-center" style="color:#fff">
                            <i class="fas fa-image pr-2"></i>
                            <div class="d-inline font-weight-bold">ADD IMAGE</div>
                            <input type="file" class="file" @change="change('image')" ref="image" />
                          </label>
                          <img :src="media" class="img-fluid" v-if="media" />
                          <img :src="image" class="img-fluid" v-else-if="image" />
                        </div>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col text-center">
                        <button
                          class="btn btn-sm btn-go-green font-weight-bold d-inline"
                          type="submit"
                          @click="add"
                          :disabled="disable"
                        >
                          <i class="fas fa-save pr-1"></i>
                          SAVE
                        </button>
                        <button type="reset" class="btn btn-sm btn-secondary" @click="edit=false">
                          <i class="fas fa-ban pr-1"></i>
                          <div class="font-weight-bold d-inline">CANCEL</div>
                        </button>
                      </div>
                    </div>

                    <div class="form-row" v-if="errors[1]">
                      <div class="col text-center text-danger">{{errors[1]}}</div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-6">
          <div class="pl-2">
            <div class="row" style="height: 65vh; max-height: 65vh; overflow-y: auto;">
              <div class="col-4" v-for="(brand,index) in brands" :key="brand.id">
                <a
                  class="float-right"
                  style="color:#424242;"
                  href="#"
                  title="delete this brand"
                  @click.prevent="deleteBrand(index)"
                >
                  <i class="fa fa-sm fa-times"></i>
                </a>
                <div class="brand-image" @click.prevent="editBrand(index)">
                  <img :src="brand.image" class="img-fluid card-img-top" v-if="brand.image!=null" />
                  <img
                    src="../../assets/doorstepLogo.png"
                    class="img-fluid card-img-top bw-greyscale"
                    v-else
                  />
                  <div class="card-body">
                    <i
                      class="fas fa-xs fa-circle text-success"
                      aria-hidden="true"
                      v-if="brand.active==1"
                    ></i>
                    <i class="fas fa-xs fa-circle text-danger" aria-hidden="true" v-else></i>

                    <span class="text-center small" v-if="brand.visible==1">{{brand.name}}</span>
                    <strike class="text-center small" v-else>{{brand.name}}</strike>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
import Vue from "vue";
import db from "../firebaseInit";
import firebase from "firebase";
import { firestorage } from "../firebaseInit";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import Loader from "../Loader";

export default {
  name: "ItemBrands",
  components: {
    Loader
  },
  data() {
    return {
      brandId: 0,
      name: null,
      image: null,
      imageOption: false,
      media: null,
      visible: true,
      active: true,
      brands: [],

      errors: [],
      edit: false,
      message: null,
      disable: false,
      disableId: false,
      disableMessage: null,
      success: false
    };
  },

  mounted() {
    this.fetchBrands();
  },

  methods: {
    add() {
      this.disable = true;
      this.disableMessage = "Uploading Data, Kripya Shanti banaye rkhe...";

      if (this.media) {
        this.uploadImage()
          .then(downloadURL => {
            this.image = downloadURL;
            return this.image;
          })
          .then(() => {
            this.uploadData();
          });
      } else {
        this.uploadData();
      }
    },

    change() {
      let files = null;
      files = this.$refs.image.files;

      if (!files.length) {
        return;
      } else {
        this.createImage(files[0]);
      }
    },

    createImage(file) {
      let reader = new FileReader();
      reader.onload = e => {
        this.media = e.target.result;
      };
      reader.readAsDataURL(file);
    },

    deleteBrand(index) {
      if (confirm("You are about to delete " + this.brands[index].name)) {
        this.disable = true;
        let url = domain + "item/brand/" + this.brands[index].id;
        axios
          .post(
            url,
            {
              _method: "delete"
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data) {
              this.brands.splice(index, 1);
              this.message = "Data Delete Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            } else {
              this.message = "Something Went wrong";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            }
          });
      }
    },

    editBrand(index) {
      if (this.brands) {
        //it's editing not adding
        this.edit = true;
        //details
        this.brandId = this.brands[index].id;
        this.active = this.brands[index].active;
        this.name = this.brands[index].name;
        this.visible = this.brands[index].visible;
        this.image = this.brands[index].image;
        this.imageOption = true;
        this.media = null;
      }
    },

    fetchBrands() {
      let url = domain + "item/brand";
      axios
        .get(url, {
          params: {
            order: "name",
            orderWay: "asc"
          }
        })
        .then(res => {
          if (res.data.length) {
            res.data.forEach(doc => {
              let visible = true;
              let active = true;
              if (doc.visible == 1) {
                visible = true;
              } else {
                visible = false;
              }
              if (doc.active == 1) {
                active = true;
              } else {
                active = false;
              }

              let brand = {
                visible: visible,
                active: active,
                id: doc.id,
                name: doc.name,
                image: doc.image
              };

              this.brands.push(brand);
            });
          }
        });
    },

    goBack() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push("/inventory");
    },

    uploadImage() {
      let file = this.$refs.image.files[0];
      this.image = null;
      return firestorage
        .ref("items/brands/" + file.name)
        .put(file)
        .then(res => {
          //downloading url
          let mediaURL = res.ref.getDownloadURL();
          return mediaURL;
        })
        .catch(err => {
          console.log(err);
          return null;
        });
    },

    uploadData() {
      if (this.edit != true) {
        {
          let url = domain + "item/brand";
          axios
            .post(
              url,
              {
                name: this.name,
                active: this.active,
                visible: this.visible,
                image: this.image
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "Access-Control-Allow-Origin": AccessControlAllowOrigin,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              if (res.data.visible == 1) {
                res.data.visible == true;
              } else {
                res.data.visible == false;
              }
              if (res.data.active == 1) {
                res.data.active == true;
              } else {
                res.data.active == false;
              }
              this.brands.unshift(res.data);

              //// to show

              this.message = "Data Upload Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              console.log(err);
            });
        }
      } else {
        {
          let url = domain + "item/brand/" + this.brandId;
          axios
            .post(
              url,
              {
                _method: "PUT",
                name: this.name,
                active: this.active,
                visible: this.visible,
                image: this.image
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": "application/json",
                  "Access-Control-Allow-Origin": "*",
                  "X-Requested-With": "XMLHttpRequest",
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              // this.allcat = null;
              // this.homecat = null;
              // this.subcatcode = null;
              // this.name = null;
              // this.image = null;
              // this.media = null;
              this.brands.forEach((doc, index) => {
                if (doc.id == res.data.id) {
                  if (doc.visible == 1) {
                    doc.visible == true;
                  } else {
                    doc.visible == false;
                  }
                  if (doc.active == 1) {
                    doc.active == true;
                  } else {
                    doc.active == false;
                  }
                  Vue.set(this.brands, index, res.data);
                }
              });
              //// to show

              this.message = "Data Upload Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              console.log(err);

              this.message =
                "Maaf kijiye kuch gadbad ho gya, kahi kuch jhol ho gya";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        }
      }
    },

    validate() {
      this.errors = [];
      if (this.name) {
        return true;
      } else {
        if (!this.name) {
          Vue.set(this.errors, 3, "Fill Name");
        }

        Vue.set(this.errors, 1, "Something is missing");
        return false;
      }
    }
  }
};
</script>

<style>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.modal-dialog {
  overflow-y: initial !important;
}
.modal-body {
  overflow-y: auto;
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}

.brand-image {
  cursor: pointer;
}

.bw-greyscale {
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}
</style>





